import { OnInit, Component, Output, EventEmitter, Input } from "@angular/core";
import { Pager } from "../../../common/constant/config";
import { MessageService } from "primeng/components/common/messageservice";
import { handleResult, handleError } from "../../../common/func/common-func";
import { TaskToAuthorizationService } from "../task-to-authorization.service";
import { RoleDateModel } from "../../process-definition-model";
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';

@Component({
  selector: 'role-select-list',
  templateUrl: './role-select-list.component.html'
})

export class RoleSelectListComponent implements OnInit {
  partyId: any;
  msg = [];
  @Input("in-value") inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  // 流程定义分页
  pager: Pager = new Pager();
  // 查询到的员工列表
  queryRoleDefinitions = [];
  // 查询条件实例
  queryRoleDefinition: RoleDateModel = new RoleDateModel;
  constructor(
    private messageService: MessageService,
    private taskToAuthorizationService: TaskToAuthorizationService,
    public httpService: CusOperationHttpService,
  ) { }

  ngOnInit() {
    this.pager.first = '0';
    if (this.inValue) {
      this.partyId = this.inValue;
    }
    this.queryRoleList();
    // this.queryRoleDefinitions=[
    //   {tellerName: '张三', tellerId: '0500', orgId: 'T00000', orgName: '台州银行'},
    //   {tellerName: '李四', tellerId: '0550', orgId: 'T00000', orgName: '台州银行'}
    // ]
  }

  // 分页
  paginate(event) {
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.queryRoleList();
  }

  // 按条件查询
  query(): void {
    this.pager.first = '0';
    this.pager.page = '1';
    this.queryRoleList();

  }

  // 重置查询条件
  reset(): void {
    this.queryRoleDefinition.tellerId = null;
    this.queryRoleDefinition.tellerName = null;
    this.query();
  }

  // 查询流程编码按钮
  queryRoleList(): void {
    let partyId;
    if (this.partyId) {
      partyId = this.partyId;
    }
    const bodyParams = {
      tellerId: this.queryRoleDefinition.tellerId,
      tellerName: this.queryRoleDefinition.tellerName,
      pageSize: parseInt(this.pager.rows, 10),
      pageNum: parseInt(this.pager.page, 10),
      partyId,
    };
    // this.taskToAuthorizationService.queryTellerByCombConditions(bodyParams)
    //   .subscribe(
    //   data => {
    //     handleResult(data, this.messageService, () => {
    //       this.queryRoleDefinitions = data.pageData[0].result;
    //       // 总条数
    //       this.pager.totalRecords = data.pageData[0].totalRows;
    //     }, true, { message:'查询成功', errorMsg: '查询失败' });
    //   },
    //   err => {
    //     handleError(err, this.messageService, () => {
    //     });
    //   }
    //   );


    this.httpService.queryTellersByPartyIdAndRoleId(bodyParams).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.queryRoleDefinitions = data.tellerList;
        this.pager.totalRecords = data.total;
      } else {
        this.msg = [];
        this.msg.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msg = [];
      this.msg.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  // 选中员工
  selectRole(event): void {
    this.outValue.emit(event.data);
  }
}