import { Injectable } from '@angular/core';
import { HttpService } from '../../common/service/http.service';
import { AUTHORIZATION_API } from '../process-definition.constant';
import { RTS_HTTP_URL } from 'app/@uisftech/common/service/http.constant';
// import { RTS_HTTP_URL } from '../../common/constant/config';

@Injectable()
export class TaskToAuthorizationService {
  constructor(
    private httpService: HttpService
  ) {

  }

  // 查询任务转授权
  queryAuthorizationList = (params) => this.httpService.post(RTS_HTTP_URL + AUTHORIZATION_API.selectList, params, null);

  // 增加任务转授权
  addAcceditRecord = (params) => this.httpService.post(RTS_HTTP_URL + AUTHORIZATION_API.addAcceditRecord, params, null);

  // 修改任务转授权
  updateAcceditRecord = (params) => this.httpService.post(RTS_HTTP_URL + AUTHORIZATION_API.updateAcceditRecord, params, null);

  // 删除任务转授权
  deleteAcceditRecord = (params) => this.httpService.post(RTS_HTTP_URL + AUTHORIZATION_API.deleteAcceditRecord, params, null);

  // 批量取消转授权
  cancelAuthorize = (params) => this.httpService.post(RTS_HTTP_URL + AUTHORIZATION_API.cancelAuthorize, params, null);

  // 查询允许转授权的流程
  selectAuthorizeWfDef = (params) => this.httpService.post(RTS_HTTP_URL + AUTHORIZATION_API.selectAuthorizeWfDef, params, null);
  // 查询人员接口
  queryTellerByCombConditions = (params) => this.httpService.post(RTS_HTTP_URL + AUTHORIZATION_API.queryTellerByCombConditions, params, null);
}
