import { Component, OnInit, OnChanges, Input, Output, EventEmitter } from '@angular/core';
import { EventServiceListModel, DictionaryTypeModel } from '../../process-definition-model';
import { EVENT_SERVICE_CALL_MODE, EVENT_SERVICE_CALL_PARAMS_FLAG, EVENT_SERVICE_CALL_TYPE } from '../../../common/constant/code.value';
import { paginatorUtils, Pager } from '../../../common/constant/config';
import { EventServiceService } from '../../event-service/event-service.service';
import { handleResult, handleError, findObject, showMessage } from '../../../common/func/common-func';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { TreeNode, ConfirmationService, MenuItem, DataTable } from 'primeng/primeng';

@Component({
  selector: 'app-event-service-call',
  templateUrl: './event-service-call.component.html',
  styleUrls: ['./event-service-call.component.scss']
})
export class EventServiceCallComponent implements OnInit {


  @Input() inValue: any;

  @Input() isShow: boolean;

  @Output() outValue: EventEmitter<any> = new EventEmitter();

  @Input() modifyProps: any;

  msg = [];
  // 分页
  pager: Pager = new Pager();
  // 调用类型
  eventServiceCallType = EVENT_SERVICE_CALL_TYPE;
  // 调用模式
  eventServiceCallMode = EVENT_SERVICE_CALL_MODE;
  // 参数获取方式
  eventServiceCallParamflag = EVENT_SERVICE_CALL_PARAMS_FLAG;
  // 事件服务列表
  eventServiceList: EventServiceListModel[] = [];
  // 列表中选中行实例
  selectedEventService: EventServiceListModel[];
  // 查询条件的的事件服务实例
  queryEventService: EventServiceListModel = new EventServiceListModel();
  // 查询条件中的调用类型
  queryCallType: DictionaryTypeModel = new DictionaryTypeModel();
  // 修改服务中选中的调用模式
  selectedEventServiceCallMode: DictionaryTypeModel = new DictionaryTypeModel();
  // 修改事件服务实例
  modifyEventServiceInstance: EventServiceListModel = new EventServiceListModel();
  // 复选框选中的数据
  selectedValues: any[];
  // 控制修改事件服务模态框显隐
  editorEventServiceDisplay = false;
  // 修改时间服务表单
  editorEventServiceForm: FormGroup;
  // 分页时存的临时变量
  paginateSelectedValues: any[];



  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private eventServiceService: EventServiceService,
  ) {
    this.createValidators();
  }

  ngOnInit() {

  }


  createValidators() {
    this.editorEventServiceForm = this.fb.group({
      'nameName': ['', Validators.required],
      'orderName': ['', Validators.required],
      'callModeName': ['', Validators.required],
      'callConditionName': ['', Validators.required]
    });
  }


  // 按照搜索条件查询
  queryFirst() {
    this.pager.first = '0';
    this.pager.page = '1';
    this.getEventServiceList();
  }



  // 重置
  reset(): void {
    this.queryEventService.name = null;
    this.queryCallType = new DictionaryTypeModel();
  }


  resetSelected(): void {
    this.selectedValues = [];
    this.paginateSelectedValues = [];
  }

  // 查询事件服务列表
  getEventServiceList(): void {
    if (this.modifyProps && Array.isArray(this.modifyProps)) {
      this.paginateSelectedValues = [...this.modifyProps]
    }
    if (this.selectedValues && Array.isArray(this.selectedValues)) {
      if (this.selectedValues.length > 1) {
        let hash = {};
        this.paginateSelectedValues = this.selectedValues.reduce((item, next) => {
          hash[next.id] ? '' : hash[next.id] = true && item.push(next);
          return item;
        }, [])
      } else if (this.selectedValues.length === 1) {
        this.paginateSelectedValues = this.paginateSelectedValues.concat(this.selectedValues);
      }
    }

    const bodyParams = {
      name: this.queryEventService.name,
      callType: this.queryCallType && this.queryCallType.type,
      pageRows: parseInt(this.pager.rows, 10),
      curPage: parseInt(this.pager.page, 10)
    };
    this.eventServiceService.searchlistEvtList(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.eventServiceList = data.pageData[0].result;
          this.pager.totalRecords = data.pageData[0].totalRows;
          let hash = {};
          this.selectedValues = [... this.selectedValues.reduce((item, next) => {
            hash[next.id] ? '' : hash[next.id] = true && item.push(next);
            return item;
          }, [])]
          if (this.paginateSelectedValues && Array.isArray(this.paginateSelectedValues) && this.paginateSelectedValues.length) {
            this.paginateSelectedValues.map(val => {
              this.eventServiceList.map(item => {
                if (val.id === item.id) {
                  item['callMode'] = val['callMode'];
                  item['callCondition'] = val['callCondition'];
                  item['order'] = val['order'];
                  this.selectedValues.push(item);
                }
              })

            })
          }
        });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
  }



  // 节点定义分页
  paginate(event) {
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.getEventServiceList();
  }


  // 修改事件服务
  modifyEventService(item): void {
    this.modifyEventServiceInstance = { ...item };
    // 默认调用模式为1同步调用
    this.selectedEventServiceCallMode = findObject('1', this.eventServiceCallMode, 'type');
    this.editorEventServiceDisplay = true;
  }

  // 删除事件服务按钮
  deleteEventService(item, table): void {
    this.confirmationService.confirm({
      message: '此节点定义可能被流程引用，删除可能会导致异常，继续删除吗？',
      header: '信息提示框',
      icon: 'fa fa-question-circle',
      accept: () => {
        const bodyParams = {
          id: item.id
        };
        this.eventServiceService.deleteEvtService(bodyParams)
          .subscribe(
          data => {
            handleResult(data, this.messageService, () => {
              this.queryFirst();
            }, true, { message: '删除成功！', errorMsg: '删除失败' });
          },
          err => {
            handleError(err, this.messageService, () => {
            });
          }
          );
      }
    });
  }


  // 给数据添加三个字段
  updateEventData(): void {
    const index = this.eventServiceList.findIndex((value, index, arr) => {
      return value.id === this.modifyEventServiceInstance.id;
    })
    this.eventServiceList[index].callMode = this.selectedEventServiceCallMode.type || '1';
    this.eventServiceList[index].callCondition = this.modifyEventServiceInstance.callCondition;
    this.eventServiceList[index].order = this.modifyEventServiceInstance.order;
    this.editorEventServiceDisplay = false;
    if (this.eventServiceList[index].callMode && this.eventServiceList[index].callCondition && this.eventServiceList[index].order) {
      showMessage(this.messageService, 'success', '服务调用次序、方式、条件添加成功');
    }

  }

  // 重置事件配置
  resetEventService(): void {
    this.selectedEventServiceCallMode = findObject('1', this.eventServiceCallMode, 'type');
    this.modifyEventServiceInstance.order = null;
    this.modifyEventServiceInstance.callCondition = null;
  }



}
