import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { ConfirmationService } from 'primeng/primeng';
import { ExceptionStrategyModel, NodeBusinessStatusModel } from '../../process-definition-model';
import { EVENT_TYPE_CODE, STATUS_TYPE } from '../../../common/constant/code.value';
import { EngTypeModel } from '../../../process-instance/process-instance-model';
import { findObject, handleResult, handleError } from '../../../common/func/common-func';
import { BusinessStatusService } from '../business-status.service';

@Component({
  selector: 'app-add-update-business-status',
  templateUrl: './add-update-business-status.component.html',
  styleUrls: ['./add-update-business-status.component.scss']
})
export class AddUpdateBusinessStatusComponent implements OnInit, OnChanges {

  msg = [];

  @Input() inValue: any;

  @Output() outValueEmitter: EventEmitter<any> = new EventEmitter();

  @Input() businessStatusInstance: NodeBusinessStatusModel;

  // 类型编码
  eventTypeCodes = EVENT_TYPE_CODE;
  // 选中的类型编码
  selectedEventTypeCode: EngTypeModel = new EngTypeModel();
  // 状态类型
  nodeStatusTypes = STATUS_TYPE;
  // 选中的状态类型
  selectedNodeStatusType: EngTypeModel = new EngTypeModel();
  // 表单
  businessStatusForm: FormGroup;
  // 新增或修改的业务状态实例
  addOrModifyNodeBusinessStatusInfo: NodeBusinessStatusModel = new NodeBusinessStatusModel();

  dialogParam = {
    display: false,
    showModel: '',
    headerTitle: ''
  };



  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private businessStatusService: BusinessStatusService,
  ) { }

  ngOnInit() {
    this.createValidators();
  }


  ngOnChanges() {
    this.inValue && this.inValue.isModify && this.resetModify();
  }





  createValidators() {
    this.businessStatusForm = this.fb.group({
      'eventTypeCodeName': ['', Validators.required],
      'nodeStatusTypeName': ['', Validators.required],
      'processCodeName': ['', Validators.required],
      'processNameName': ['', Validators.maxLength(64)],
      'nodeIdName': ['', Validators.required],
      'nodeNameName': ['', Validators.maxLength(100)],
      'nodeStateName': ['', [Validators.required, Validators.maxLength(16)]],
      'nodeStateNameName': ['', Validators.maxLength(32)]
    });

  }


  // 重置
  reset(): void {
    if (this.inValue) {
      this.inValue.isAdd && this.resetAdd();
      this.inValue.isModify && this.resetModify();
    }

  }


  // 重置新增
  resetAdd(): void {
    this.addOrModifyNodeBusinessStatusInfo = new NodeBusinessStatusModel();
    this.selectedEventTypeCode = { ...this.eventTypeCodes[0] };
    this.selectedNodeStatusType = { ...this.nodeStatusTypes[0] };
  }

  // 重置修改
  resetModify(): void {
    this.addOrModifyNodeBusinessStatusInfo = { ...this.businessStatusInstance };
    this.dropDownSetValue();
  }

  // 修改时的下拉框复制
  dropDownSetValue(): void {
    this.selectedEventTypeCode = this.businessStatusInstance.eventType ? findObject(this.businessStatusInstance.eventType, this.eventTypeCodes, 'type') : null;
    this.selectedNodeStatusType = this.businessStatusInstance.stateType ? findObject(this.businessStatusInstance.stateType, this.nodeStatusTypes, 'type') : null;
  }

  // 查找流程编码
  queryProcessCode(): void {
    this.dialogParam.showModel = '11';
    this.dialogParam.headerTitle = '流程定义列表';
    this.dialogParam.display = true;
  }

  // 选中流程编码后的回调函数
  callBackDefine(event): void {
    this.dialogParam.display = !event;
    this.addOrModifyNodeBusinessStatusInfo.processCode = event.data.defId;
    this.addOrModifyNodeBusinessStatusInfo.processName = event.data.defName;
  }

  // 查找流程定义
  queryNodeDefine(): void {
    this.dialogParam.showModel = '21';
    this.dialogParam.headerTitle = '节点定义列表';
    this.dialogParam.display = true;

  }

  // 选中流程定义的回调函数
  callBackNodeDefine(event): void {
    this.dialogParam.display = !event;
    this.addOrModifyNodeBusinessStatusInfo.nodeId = event.data.nodeId;
    this.addOrModifyNodeBusinessStatusInfo.nodeName = event.data.nodeName;
  }

  // 更新或者保存
  saveOrUpdate(): void {
    const bodyParams = {
      eventType: this.selectedEventTypeCode ? this.selectedEventTypeCode.type : '',
      stateType: this.selectedNodeStatusType ? this.selectedNodeStatusType.type : '',
      processCode: this.addOrModifyNodeBusinessStatusInfo.processCode,
      processName: this.addOrModifyNodeBusinessStatusInfo.processName,
      nodeId: this.addOrModifyNodeBusinessStatusInfo.nodeId,
      nodeName: this.addOrModifyNodeBusinessStatusInfo.nodeName,
      nodeState: this.addOrModifyNodeBusinessStatusInfo.nodeState,
      nodeStateName: this.addOrModifyNodeBusinessStatusInfo.nodeStateName,
    };
    if (this.inValue.isModify) {
      Object.assign(bodyParams, { id: this.addOrModifyNodeBusinessStatusInfo.id });
      this.businessStatusService.updateWfNodeState(bodyParams)
        .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.outValueEmitter.emit(event);
          }, true, { message: '更新成功！', errorMsg: '更新失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
        );
    }
    if (this.inValue.isAdd) {
      this.businessStatusService.addWfNodeState(bodyParams)
        .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.outValueEmitter.emit(event);
          }, true, { message: '保存成功！', errorMsg: '保存失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
        );
    }

  }




}
