import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { Component, OnInit, Output, EventEmitter } from "@angular/core";
import { handleResult, handleError } from '../../../../common/func/common-func';
import { GeneralParametersService } from '../../general-parameters.service';
import { MessageService } from 'primeng/components/common/messageservice';

@Component({
  selector: 'app-process-range-add',
  templateUrl: './process-range-add.component.html',
  styleUrls: ['./process-range-add.component.scss']
})

export class ProcessRangeAddComponent implements OnInit {
  addRanageForm: FormGroup;
  // 弹框
  dialogParam = {
    display: false,
    headerTitle: ''
  };
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  constructor(
    private fb: FormBuilder,
    private generalParametersService: GeneralParametersService,
    private messageService: MessageService
  ) {

  }
  ngOnInit() {
    this.addRanageForm = this.fb.group({
      paramCode: ['', [Validators.required]],
      paramName: ['', [Validators.required]]
    });
  }
  // 保存
  save() {
    let bodyParams = {
      paramType: 'M0',   // 流程范围
      paramCode: this.addRanageForm.get('paramCode').value,
      paramName: this.addRanageForm.get('paramName').value
    };
    this.generalParametersService.addWfParam(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.reset();
          this.outValue.emit(false);
        }, true, { message: '保存成功！', errorMsg: '保存失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
  }
  // 重置
  reset() {
    this.addRanageForm.reset();
  }
  // 选择流程
  selectProcess(defineList) {
    defineList.reset();
    defineList.queryProcessCode();
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '选择流程';
  }
  // 选择流程的回调
  callBackDefine(event): void {
    this.dialogParam.display = !event;
    this.addRanageForm.patchValue({
      paramCode: event.data.defId,
      paramName: event.data.defName
    });
  }
}