import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ProsTypeModel, BUSINESS_SIGN_APPROVE_PRO_TYPE, RuleModel, DictionaryTypeModel, EventServiceListModel, MessageModel, ProcessDefinitionModel, SUB_NODE_PRO_TYPE } from '../../process-definition-model';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { ConfirmationService } from 'primeng/primeng';
import { BusinessNodeManagementService } from '../business-node-management.service';
import { handleResult, handleError, findObject, isJsonString, showMessage } from '../../../common/func/common-func';
import { BUS_NODE_TYPES, ACCESSORY_PROP_TYPE, TASK_DISTRIBUTION_TYPE, TASK_TIME_OUT_HANDLE_TYPE, TASK_NOTIFY_TYPE, TIMEOUT_WARNING_TYPE, SUB_NODE_TYPES } from '../../../common/constant/code.value';


@Component({
  selector: 'app-update-props-extend-subnode',
  templateUrl: './update-props-extend-subnode.component.html',
  styleUrls: ['./update-props-extend-subnode.component.scss']
})
export class UpdatePropsExtendSubnodeComponent implements OnChanges {
  msg = [];
  // 接收的传入的节点
  @Input() isFlowModel: any;
  //接收的传入的属性
  @Input() inValue: any;
  // 模型类型
  @Input() modelType: any;
  // 父dialog  解决dialog嵌套
  @Input() dialogTemplate: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  // 表单
  updateSubNodeForm: FormGroup;
  // 子节点属性类型
  prosExtendSubNodeTypes = SUB_NODE_TYPES;
  // 修改的扩展属性类型
  selectedProsExtendType: ProsTypeModel = new ProsTypeModel();
  // 修改的标志
  isAssignmentFlag = true;
  // 修改的属性类型
  updatePropsType: string;

  dialogParam = {
    display: false,
    headerTitle: ''
  };
  // 显示规则树还是自动服务调用
  showRuleTreeOrAutoService = {
    isRuleTree: false,
    isSubNode: false,
  }
  // 选中流程建模
  selectedFlowModel: ProcessDefinitionModel = new ProcessDefinitionModel();
  // 节点进入、离开条件
  nodeConditonString: string;
  // 重置时的的临时变量
  defaultNodeConditonString: string;
  // 节点进入、离开条件映射值
  nodeConditonMapValue: RuleModel = new RuleModel();
  // 重置时的临时变量
  defaultNodeConditonMapValue: RuleModel = new RuleModel();
  // 任务处理时间
  taskHandleTime: number;
  // 选中的流程数组
  selectedFlowModelArray: ProcessDefinitionModel = new ProcessDefinitionModel();
  // 重置时的临时变量
  defaultFlowModelArray: ProcessDefinitionModel = new ProcessDefinitionModel();
  // 服务调用
  isRuleNum: number = 0;
  // 控制脚本测试的模态框显隐
  verifyScriptDisplay = false;








  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private businessNodeManagementService: BusinessNodeManagementService,
  ) {
    this.createValidators();
  }

  ngOnChanges() {
    this.setPropsAndValue();
  }




  createValidators() {
    this.updateSubNodeForm = this.fb.group({
      'updateProsExtendTypeName': new FormControl('', Validators.required),
      'subFlowlName': new FormControl('', Validators.required),
      'funURLName': new FormControl(''),
      'funListMapName': new FormControl('')
    });
  }


  // 选择事件调用
  selectedPro(fm): void {
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '事件服务调用';
    this.showRuleTreeOrAutoService = {
      isRuleTree: false,
      isSubNode: true
    }
    fm.reset();
    fm.resetSelected();
    fm.getProcessDefinitions();
  }

  // 选择规则树
  selectedProRule(): void {
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '规则树及列表';
    this.showRuleTreeOrAutoService = {
      isRuleTree: true,
      isSubNode: false
    };
    this.isRuleNum++;
  }


  // 选择流程建模
  selectedEventService(event): void {
    // if (!item.selectedValues || (item.selectedValues && item.selectedValues < 1)) {
    //   showMessage(this.messageService, 'error', '请选择流程建模！');
    //   return;
    // }
    // this.selectedFlowModelArray = Array.from(item.selectedValues);
    // const nameArray = [];
    // const noOrderArray = [];
    // this.selectedFlowModelArray.forEach(val => {
    //   nameArray.push(val.defId);
    // })
    // this.subFlowModelValue = nameArray.join('、');
    this.selectedFlowModelArray = { ...event };
    this.dialogParam.display = false;
  }

  // 从规则树传上来的值
  callBackRule(event): void {
    this.dialogParam.display = !event;
    if (this.selectedProsExtendType) {
      if (this.selectedProsExtendType.type === SUB_NODE_PRO_TYPE.NODE_ENTRY_CONDITION || this.selectedProsExtendType.type === SUB_NODE_PRO_TYPE.NODE_EXIT_CONDITION) {
        this.nodeConditonMapValue = { ...event };
      }
    }

  }

  // 验证脚本按钮
  verifyScript(verifyTestScript): void {
    verifyTestScript.resetListAndResult();
    this.verifyScriptDisplay = true;
  }

  // 测试脚本模态框的关闭按钮
  callBackVerifyScript(event): void {
    this.verifyScriptDisplay = !event;
  }


  // 重置修改的属性
  resetUpdateProsExtend(): void {
    // if (Array.isArray(this.defaultFlowModelArray) && this.defaultFlowModelArray.length) {
    //   this.selectedFlowModelArray = Array.from(this.defaultFlowModelArray);
    //   const nameArr = [];
    //   this.selectedFlowModelArray.forEach(val => {
    //     nameArr.push(val.defId);
    //   })
    //   this.subFlowModelValue = nameArr.join('、');
    // }
    this.selectedFlowModelArray = {...this.defaultFlowModelArray};
    this.nodeConditonMapValue = { ...this.defaultNodeConditonMapValue };
    this.nodeConditonString = this.defaultNodeConditonString;
  }


  //  更新属性值
  updateProsExtend(): void {
    this.isAssignmentFlag = false;
    let value = null;
    if (this.inValue.propName === SUB_NODE_PRO_TYPE.NODE_ENTRY_CONDITION || this.inValue.propName === SUB_NODE_PRO_TYPE.NODE_EXIT_CONDITION) {
      if (!this.nodeConditonString && !this.nodeConditonMapValue.label) {
        showMessage(this.messageService, 'error', '条件或规则映射都无属性值，禁止保存');
        return;
      }
      let valueParams = {};
      this.nodeConditonString ? valueParams = {
        type: '3',
        value: this.nodeConditonString
      } : this.nodeConditonMapValue.label ? valueParams = {
        type: '2',
        value: JSON.stringify(this.nodeConditonMapValue)
      } : valueParams;
      value = JSON.stringify(valueParams);
    } else if (this.selectedProsExtendType.type === SUB_NODE_PRO_TYPE.SUB_FLOW_SELECTION_CALL) {
      const obj = [{
        defId:this.selectedFlowModelArray.defId,
        defName:this.selectedFlowModelArray.defName
      }];
      value = JSON.stringify(obj);
    }
    const bodyParams = {
      ownId: this.isFlowModel.nodeId || '',
      ownType: this.modelType,
      propName: this.selectedProsExtendType.type || '',
      propDesc: this.selectedProsExtendType.descp || '',
      propValue: value,
    }
    this.businessNodeManagementService.updateNodePropExtends(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.outValue.emit(event);
        }, true, { message: ' 更新成功', errorMsg: '更新失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
          this.outValue.emit(event);
        });
      }
      );
    this.isAssignmentFlag = true;
  }



  // 给属性类型和属性赋值
  setPropsAndValue(): void {
    if (this.isAssignmentFlag) {
      if (this.inValue) {
        this.selectedProsExtendType = findObject(this.inValue.propName, this.prosExtendSubNodeTypes, 'type');
        this.updatePropsType = this.selectedProsExtendType.descp;
        if (this.inValue.propName === SUB_NODE_PRO_TYPE.SUB_FLOW_SELECTION_CALL) {
          this.updateSubNodeForm.addControl('subFlowlName', new FormControl('', Validators.required));
          const value = JSON.parse(this.inValue.propValue);
          this.defaultFlowModelArray = { ...value[0]};
          this.selectedFlowModelArray = { ...value[0]};
          // const nameArr = [];
          // value.forEach(val => {
          //   nameArr.push(val.defId);
          // })

        } else if (this.inValue.propName === SUB_NODE_PRO_TYPE.NODE_ENTRY_CONDITION || this.inValue.propName === SUB_NODE_PRO_TYPE.NODE_EXIT_CONDITION) {
          const value = JSON.parse(this.inValue.propValue);
          if (isJsonString(value.value)) {
            const jsonVal = JSON.parse(value.value);
            this.defaultNodeConditonMapValue = { ...jsonVal };;
            this.nodeConditonMapValue = { ...jsonVal };;
          } else {
            this.nodeConditonString = value.value;
            this.defaultNodeConditonString = value.value;
          }
        }
      }
    }
  }





}
