import { Component, OnInit, Input, EventEmitter, Output, OnChanges } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { TIME_TO_START_TYPE, WEEKS } from '../../../../common/constant/code.value';
import { DictionaryTypeModel } from '../../../process-definition-model';
import { findObject, formatDate } from '../../../../common/func/common-func';
import { TIME_FORMAT } from '../../../../common/constant/config';

@Component({
  selector: 'add-time-to-start',
  templateUrl: './add-time-to-start.component.html',
  styleUrls: ['./add-time-to-start.component.scss']
})
export class AddTimeToStartComponent implements OnChanges, OnInit {



  @Output() outValue: EventEmitter<any> = new EventEmitter();

  @Input() inValue: number = 0;

  msg = [];

  // 启动时间
  startTime: Date;
  // 表单
  timeToStartForm: FormGroup;
  // 定时类型
  timeToStartType = TIME_TO_START_TYPE;
  // 选中的定时类型
  selectedTimeStartType: DictionaryTypeModel = new DictionaryTypeModel();
  // 星期
  weeks = WEEKS;
  // 选中的星期
  selectedWeeks: DictionaryTypeModel[] = [];

  selectedWeekStr: any[];
  // 几号
  dateTime: number;

  // 修改重置时启动时间的临时变量
  defaultStartTime: Date;
  // 修改重置时选中的定时类型临时变量
  defaultSelectedTimeStartType: DictionaryTypeModel = new DictionaryTypeModel();
  // 修改重置时星期的临时变量
  defaultSelectedWeeks: DictionaryTypeModel[] = [];
  // 修改重置时几号的临时变量
  defaultDateTime: number;


  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
  ) {
    this.createValidators();
  }

  ngOnInit() {

  }

  ngOnChanges() {
    // this.resetAdd();
  }

  createValidators() {
    this.timeToStartForm = this.fb.group({
      'timeName': ['', Validators.required],
      'typeName': ['', Validators.required],
      'weekName': ['', Validators.required],
      'dateName': ['', Validators.required]
    });
  }



  // 类型选择
  changeType(event): void {
    if (event.type === 'week') {
      this.timeToStartForm.addControl('weekName', new FormControl('', Validators.required));
      this.timeToStartForm.removeControl('dateName');
    } else if (event.type === 'date') {
      this.timeToStartForm.addControl('dateName', new FormControl('', Validators.required));
      this.timeToStartForm.removeControl('weekName');
    }
  }


  // 新增重置
  resetAdd(): void {
    this.startTime = null;
    this.selectedTimeStartType = findObject('week', this.timeToStartType, 'type');
    this.timeToStartForm.addControl('weekName', new FormControl('', Validators.required));
    this.timeToStartForm.removeControl('dateName');
    this.selectedWeeks = [];
    this.selectedWeekStr = null;
    this.dateTime = null;
  }

  // 重置修改
  reset(): void {
    if (this.inValue) {
      this.startTime = this.defaultStartTime;
      this.selectedTimeStartType = { ...this.defaultSelectedTimeStartType };
      if (this.defaultSelectedTimeStartType.type === 'week') {
        this.timeToStartForm.addControl('weekName', new FormControl('', Validators.required));
        this.timeToStartForm.removeControl('dateName');
      } else {
        this.timeToStartForm.addControl('dateName', new FormControl('', Validators.required));
        this.timeToStartForm.removeControl('weekName');
      }
      this.selectedWeeks = [...this.defaultSelectedWeeks];

      this.dateTime = this.defaultDateTime;
    } else {
      this.resetAdd();
    }


  }



  //  保存属性值
  save(): void {
    let obj = {};
    let startTime = null;
    if (!this.startTime) {
      startTime = null;
    } else if (Object.prototype.toString.call(this.startTime) === "[object String]") {
      startTime = this.startTime;
    } else {
      startTime = formatDate(this.startTime, TIME_FORMAT);
    }
    if (this.selectedTimeStartType.type === 'week') {
      const uniqueArray = Array.from(new Set(this.selectedWeeks));
      if (uniqueArray && Array.isArray(uniqueArray) && uniqueArray.length) {
        const startWeek = uniqueArray.map(item => {
          return item.type;
        }).join(',');
        const startWeekStr = uniqueArray.map(item => {
          return item.descp;
        });
        obj = {
          startTime,
          startWeek,
          startWeekStr
        }
      }

    } else {
      obj = {
        startTime,
        startDate: this.dateTime
      }
    }
    this.outValue.emit(obj);
  }


}

