import { Component, OnInit } from '@angular/core';
import { TreeNode, ConfirmationService, MenuItem } from 'primeng/primeng';
import { expandLastNode, findObject, handleError, handleResult, formatDate, collectAllSystemIds, isJsonString, showMessage } from '../../common/func/common-func';
import { MessageService } from 'primeng/components/common/messageservice';
import { ProcessMaintenanceService } from '../process-maintenance/process-maintenance.service';
import {
  ProcessDirectoryModel, BusinessNodeModel,
  DictionaryTypeModel,
  BusinessNodeProsExtendModel,
  MessageModel,
  BUSINESS_PRO_EXTENT,
  ProjectModel,
  AUTO_NODE_PRO_TYPE,
  EventServiceListModel,
  BUSINESS_SIGN_APPROVE_PRO_TYPE,
  SUB_NODE_PRO_TYPE
} from '../process-definition-model';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Pager, paginatorUtils, CALENDAR_CN, FULL_HORIZONTAL_DATE_FORMAT } from '../../common/constant/config';
import { BusinessNodeManagementService } from './business-node-management.service';
import { MessageTemplateService } from '../message-template/message-template.service';
import { EventServiceService } from '../event-service/event-service.service';
import { TIMEOUT_WARNING_TYPE, TASK_NOTIFY_TYPE, TASK_NODE_TYPES } from '../../common/constant/code.value';
import { checkFlowCode } from 'app/pages/tzb/workflow/activiti-work-flow/common/constant/validator.utils';

@Component({
  selector: 'app-business-node-management',
  templateUrl: './business-node-management.component.html',
  styleUrls: ['./business-node-management.component.scss']
})
export class BusinessNodeManagementComponent implements OnInit {
  msgs = [];
  // 节点定义分页
  pager: Pager = new Pager();
  // 流程目录列表
  processDirectorys: TreeNode[];
  // 保存流程目录展开状态的变量
  expandNodes: TreeNode[];
  // 选中的集合
  selectedProcessDirectory: TreeNode;
  // 查询到节点定义集合
  queryBusinessNodes: BusinessNodeModel[] = [];
  // 选中的单行流程定义
  selectedBusinessNode: BusinessNodeModel;
  // 查询条件的节点定义实例
  businessNodeInstance: BusinessNodeModel = new BusinessNodeModel();
  // 右功能键
  items: MenuItem[];
  // 界面左侧流程目录的右键
  processItems: MenuItem[];
  // 新增修改节点定义的表单
  businessNodeForm: FormGroup;
  // 流程显示
  processDisplay = false;
  // 是修改流程
  isModifyProcess = false;
  // 是新增流程
  isAddProcess = false;
  // 增加或修改的流程实例
  modifyOrAddBusinessNode: BusinessNodeModel = new BusinessNodeModel();
  // 修改重置的变量
  defaultBusinessNode: BusinessNodeModel = new BusinessNodeModel();

  systemName: string;

  systemId: string;

  defaultSystemId: string;

  defaultSystemName: string;

  // 控制修改或新增流程目录页面显隐
  processDirectoryDisplay = false;
  // 是新增流程目录
  isAddProcessDirectory = false;
  // 是修改流程目录
  isModifyProcessDirectory = false;
  // 新增或修改流程目录表单
  processDirectoryForm: FormGroup;
  // 选中的单行流程目录
  selectedExportProcessDirectory: TreeNode[];
  // 标题
  headerTip: string = null;
  // 节点定义类型
  businessNodeTypes = TASK_NODE_TYPES;
  // 修改扩展属性时的模板类型
  messageTypes: DictionaryTypeModel[] = [];
  // 选中的扩展属性模板类型
  queryMessageType: DictionaryTypeModel = new DictionaryTypeModel();
  // 默认的邮件通知类型
  defaultMessageType: DictionaryTypeModel = new DictionaryTypeModel();
  // 下拉框中选中的节点定义类型
  selectedBusinessNodeType: DictionaryTypeModel;
  // 控制业务属性页面显隐
  businessNodeProsExtendDisplay = false;
  // 节点定义扩展属性列表
  businessNodeProsExtendList: BusinessNodeProsExtendModel[] = [];
  // 单选选中的扩展节点属性实例
  selectedBusinessNodeProsExtend: BusinessNodeProsExtendModel;
  // 修改节点定义扩展属性实例
  modifyBusinessNodeProsExtend: BusinessNodeProsExtendModel = new BusinessNodeProsExtendModel();
  // 修改业节点扩展属性重置时的中间变量
  defaultBusinessNodeProsExtend: BusinessNodeProsExtendModel = new BusinessNodeProsExtendModel();
  // 控制修改节点定义扩展属性页面显隐
  modifyBusinessNodeProsExtendPlay = false;
  // 修改节点定义扩展属性表单
  businessNodeExtendForm: FormGroup;
  // 新增节点定义扩展属性表单
  addBusinessNodeExtendForm: FormGroup;
  // 修改扩展属性页面选中的节点类型
  selectedBusinessNodeExtendType: DictionaryTypeModel;
  // 消息模板列表
  messageTemplateList: MessageModel[] = [];
  // 分页实例
  paginatorInstance1: MessageModel = new MessageModel();

  // 日期格式
  CALENDAR_CN = CALENDAR_CN;

  projectsDisplay = false;
  // 单选选择的项目目录
  selectedProjectMenu: ProjectModel = new ProjectModel();
  // 扩展属性时的类型
  prosExtendTypes: DictionaryTypeModel[] = [];
  // 选中的扩展属性类型
  selectedProsExtendType: DictionaryTypeModel = new DictionaryTypeModel();
  // 控制新增节点定义扩展属性页面显隐
  addBusinessNodeProsExtendPlay = false;
  // 节点定义扩展属性类型
  // businessNodeExtendTypes: DictionaryTypeModel[] = [];
  // 所属模型类型
  isNodeModelType: string = '0';  //  '0' -节点属性 '1'-流程属性
  // 节点分类所有的节点标识
  allSystemIds: string[] = [];
  // 节点定义所有的节点标识
  nodeDefineAllSystemIds: string[] = [];
  // 节点定义属性列表所有的属性类型
  nodeDefinePropNames: string[] = [];


  // 规则属性模态框
  ruleProDisplay = false;
  // 业务节点、审批节点、会签节点模态框显隐
  businessApproveSignProsPlay = false;
  // 新增属性按钮 新增模态框数据是否清空
  isRest = false;
  // 控制新增扩展属性模态框显隐
  addProsExtendPlay = false;
  dialogParam = {
    display: false,
    showModel: '',
    headerTitle: ''
  };

  // 显示新增属性模态框
  showProsModal = true;
  // 预警消息类型
  timeOutWarningType = TIMEOUT_WARNING_TYPE;
  //  任务通知机制
  taskNotifyTypes = TASK_NOTIFY_TYPE;
  // 控制复制节点模态框显引
  copyProcessDisplay = false;
  // 复制的节点表单
  copyBusinessNodeForm: FormGroup;
  // 复制节点的实例
  copyBusinessNode: BusinessNodeModel = new BusinessNodeModel();
  // 查看扩展属性控制
  showNum: number = 0;






  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private processMaintenanceService: ProcessMaintenanceService,
    private businessNodeManagementService: BusinessNodeManagementService,
    private messageTemplateService: MessageTemplateService,
    private eventServiceService: EventServiceService,

  ) {





  }


  ngOnInit() {
    this.queryProcessDirectoryTree(true);
    this.createValidators();
    this.paginatorInstance1.rowNum = paginatorUtils.rowNum;
    this.paginatorInstance1.rowNum = paginatorUtils.startRow;
    this.processItems = [
      {
        label: '新增', icon: 'fa-search', command: (event) => {
          this.addProcessDirectory();
        }
      },
      {
        label: '修改', icon: 'fa-search', command: (event) => {
          this.modifyProcessDirectory();
        }
      },
      {
        label: '删除', icon: 'fa-search', command: (event) => {
          this.deleteProcessDirectory();
        }
      },
      { separator: true },
      {
        label: '展开', icon: 'fa-search', command: (event) => {
          this.expandRecursive(this.selectedProcessDirectory, true);
        }
      },
      {
        label: '折叠', icon: 'fa-search', command: (event) => {
          this.expandRecursive(this.selectedProcessDirectory, false);
        }
      }
    ];

  }

  createValidators() {
    this.businessNodeForm = this.fb.group({
      'storeNameName': ['', [Validators.required, checkFlowCode]],
      'nameName': ['', Validators.required],
      'displayName': ['', Validators.required]
    });
    this.processDirectoryForm = this.fb.group({
      'sysNodeName': ['', Validators.required],
      'sysNodeIdName': ['', Validators.required]
    });
    // 修改扩展属性表单
    this.businessNodeExtendForm = this.fb.group({
      'businessNodeIdName': ['', Validators.required],
      'propNameName': ['', Validators.required],
      'emailName': ['', Validators.required],
      'smsName': ['', Validators.required],
      'extendTypeName': ['', Validators.required],
      'nofName': ['', Validators.required],
      'timeStartName': ['', Validators.required],
      'userRulelName': ['', Validators.required]
    });

    // 新增扩展属性表单
    this.addBusinessNodeExtendForm = this.fb.group({
      'addProsExtendTypeName': ['', Validators.required],
      'ServiceCallName': ['', Validators.required],
    });

    this.copyBusinessNodeForm = this.fb.group({
      'storeNameName': ['', [Validators.required, checkFlowCode]],
      'nameName': ['', Validators.required],
    });
  }




  // 查找节点分类目录树
  queryProcessDirectoryTree(flag: boolean, expandNodes?): void {
    const bodyParams = { id: 1 };
    this.processMaintenanceService.searchProcessDirectory(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.processDirectorys = data.pageData[0].result;
          if (this.processDirectorys.length) {
            const systemIdArray = [];
            this.allSystemIds = Array.from(collectAllSystemIds(this.processDirectorys[0], 'systemId', systemIdArray));
          }
          if (flag) {
            if (this.processDirectorys && this.processDirectorys.length > 0) {
              this.expandRecursive(this.processDirectorys[0], true);
            }
          }
          if (expandNodes && expandNodes.length > 0) {
            expandLastNode(this.processDirectorys, expandNodes);
          }
        });
      },
      err => {
        handleError(err, this.messageService, () => {

        });
      });
  }


  queryBusinessNode(): void {
    this.pager.first = '0';
    this.pager.page = '1';
    this.getBusinessNode();
  }


  // 节点定义分页
  paginate(event) {
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.getBusinessNode();
  }

  // 重置查询条件
  resetQuery(): void {
    this.businessNodeInstance.nodeId = null;
    this.businessNodeInstance.nodeName = null;
  }


  // 查找节点定义列表
  getBusinessNode(): void {
    if (!this.selectedProcessDirectory) {
      showMessage(this.messageService, 'error', '请先选择左侧节点分类！');
      return;
    }
    const bodyParams = {
      nodeId: this.businessNodeInstance.nodeId,
      nodeName: this.businessNodeInstance.nodeName,
      parentId: this.selectedProcessDirectory['keyId'],
      pageRows: parseInt(this.pager.rows, 10),
      curPage: parseInt(this.pager.page, 10)
    };
    this.nodeDefineAllSystemIds = [];
    this.businessNodeManagementService.queryBussinessNode(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.queryBusinessNodes = data.pageData[0].result;
          this.pager.totalRecords = data.pageData[0].totalRows;
          if (this.queryBusinessNodes.length > 0) {
            this.queryBusinessNodes.map(item => {
              this.nodeDefineAllSystemIds.push(item.nodeId);
            })
          }
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );


  }



  // 点击左边流程目录树节点
  onNodeSelect(event, table): void {
    const clickNodeData = event.node;
    this.headerTip = clickNodeData['systemName'];
    this.selectedBusinessNode = null;
    this.getBusinessNode();
  }


  // 新增节点定义按钮
  addProcess(): void {
    if (!this.selectedProcessDirectory) {
      showMessage(this.messageService, 'error', '请选择节点分类！');
      return;
    }
    this.reseAddBusinessNode();
    this.processDisplay = true;
    this.isAddProcess = true;
    this.isModifyProcess = false;

  }

  // 新增节点定义的重置
  reseAddBusinessNode(): void {
    this.modifyOrAddBusinessNode.nodeId = null;
    this.modifyOrAddBusinessNode.nodeName = null;
    this.selectedBusinessNodeType = null;
  }

  // 新增节点定义的保存
  saveBusinssNode(): void {
    if (this.nodeDefineAllSystemIds.indexOf(this.modifyOrAddBusinessNode.nodeId) > -1) {
      showMessage(this.messageService, 'error', `节点定义【${this.modifyOrAddBusinessNode.nodeId}】已存在，禁止添加！`);
      return;
    }
    const bodyParams = {
      parentId: this.selectedProcessDirectory['keyId'],
      nodeId: this.modifyOrAddBusinessNode.nodeId,
      nodeName: this.modifyOrAddBusinessNode.nodeName,
      type: this.selectedBusinessNodeType ? this.selectedBusinessNodeType.type : ''
    };
    this.businessNodeManagementService.addBussinessNode(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.getBusinessNode();
          this.selectedBusinessNode = null;
          this.processDisplay = false;
          this.isAddProcess = false;
          this.isModifyProcess = false;
        }, true, { message: ' 保存成功', errorMsg: '保存失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
  }

  // 节点定义表格右键修改按钮
  modifyBusinessNode(item): void {
    this.modifyOrAddBusinessNode = { ...item };
    this.defaultBusinessNode = { ...item };
    this.selectedBusinessNodeType = findObject(this.modifyOrAddBusinessNode.type, this.businessNodeTypes, 'type');
    this.processDisplay = true;
    this.isModifyProcess = true;
    this.isAddProcess = false;
  }

  // 节点定义修改的重置
  resetModifyBusinessNode(): void {
    this.modifyOrAddBusinessNode = { ...this.defaultBusinessNode };
    this.selectedBusinessNodeType = findObject(this.defaultBusinessNode.type, this.businessNodeTypes, 'type');
  }

  // 更新修改的节点定义
  updateBusinssNode(): void {
    const bodyParams = {
      id: this.modifyOrAddBusinessNode.id,
      nodeId: this.modifyOrAddBusinessNode.nodeId,
      nodeName: this.modifyOrAddBusinessNode.nodeName,
      type: this.selectedBusinessNodeType ? this.selectedBusinessNodeType.type : ''
    };
    this.businessNodeManagementService.updateBussinessNode(bodyParams)
      .subscribe(
      data => {
        this.processDisplay = false;
        this.isAddProcess = false;
        this.isModifyProcess = false;

        handleResult(data, this.messageService, () => {
          this.getBusinessNode();
          this.selectedBusinessNode = null;
        }, true, { message: ' 更新成功', errorMsg: '更新失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
          this.processDisplay = false;
          this.isAddProcess = false;
          this.isModifyProcess = false;

        });
      }
      );
  }

  // 保存
  updateOrSaveBusinssNode(): void {
    if (this.isModifyProcess) {
      this.updateBusinssNode();
    } else if (this.isAddProcess) {
      this.saveBusinssNode();
    }

  }


  // 节点定义新增或修改重置
  resetProcessBusinssNode(): void {
    this.isModifyProcess && this.resetModifyBusinessNode();
    this.isAddProcess && this.reseAddBusinessNode();
  }


  // 删除节点定义
  deleteBusinessNode(item): void {
    this.confirmationService.confirm({
      message: '此节点定义可能被流程引用，删除可能会导致异常，继续删除吗？',
      header: '信息提示框',
      icon: 'fa fa-question-circle',
      key: 'mapConfirm',
      accept: () => {
        const bodyParams = {
          nodeId: item.nodeId,
        };
        this.businessNodeManagementService.deleteBussinessNode(bodyParams)
          .subscribe(
          data => {

            handleResult(data, this.messageService, () => {
              this.getBusinessNode();
              this.selectedBusinessNode = null;
            }, true, { message: '删除成功', errorMsg: '删除失败' });
          },
          err => {
            handleError(err, this.messageService, () => {

            });
          }
          );
      }
    });
  }

  // 复制节点定义按钮
  copyBusinessNodeFunc(item): void {
    this.resetCopyProcessBusinssNode();
    this.copyBusinessNode.type = item.type;
    this.copyBusinessNode.id = item.id;
    this.copyProcessDisplay = true;
  }

  // 重置复制
  resetCopyProcessBusinssNode(): void {
    this.copyBusinessNode.nodeId = null;
    this.copyBusinessNode.nodeName = null;
  }

  // 保存复制的业务节点
  saveCopyBusinssNode(): void {
    const bodyParams = {
      id: this.copyBusinessNode.id,
      nodeId: this.copyBusinessNode.nodeId,
      nodeName: this.copyBusinessNode.nodeName,
      type: this.copyBusinessNode.type
    };
    this.businessNodeManagementService.copyBussinessNode(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.getBusinessNode();
          this.selectedBusinessNode = null;
        }, true, { message: ' 复制成功', errorMsg: '复制失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
    this.copyProcessDisplay = false;
  }



  // 折叠或展开
  expandRecursive(node: TreeNode, isExpand: boolean) {
    node.expanded = isExpand;
    node.children && node.children.forEach(childNode => {
      this.expandRecursive(childNode, isExpand);
    });
  }

  // 节点分类修改按钮
  modifyProcessDirectory(): void {
    this.systemId = this.selectedProcessDirectory['systemId'];
    this.systemName = this.selectedProcessDirectory['systemName'];
    this.defaultSystemId = this.selectedProcessDirectory['systemId'];
    this.defaultSystemName = this.selectedProcessDirectory['systemName'];
    this.processDirectoryDisplay = true;
    this.isModifyProcessDirectory = true;
    this.isAddProcessDirectory = false;
  }

  // 修改节点分类的更新
  updateModifyProcessDirectory(): void {
    this.expandNodes = [...this.processDirectorys];
    const bodyParams = {
      systemId: this.systemId,
      systemName: this.systemName,
      id: this.selectedProcessDirectory['keyId']
    };
    this.processMaintenanceService.updateProcessDirectory(bodyParams)
      .subscribe(
      data => {

        handleResult(data, this.messageService, () => {
          this.queryProcessDirectoryTree(false, this.expandNodes);
          this.selectedProcessDirectory = null;
        }, true, { message: ' 更新成功', errorMsg: '更新失败' });
      },
      err => {
        handleError(err, this.messageService, () => {

        });
      }
      );
    this.processDirectoryDisplay = false;
    this.isModifyProcessDirectory = false;
    this.isAddProcessDirectory = false;
  }

  // 修改节点分类的重置
  resetModifyProcessDirectory(): void {
    this.systemId = this.defaultSystemId;
    this.systemName = this.defaultSystemName;
  }

  // 节点分类的新增按钮
  addProcessDirectory(): void {
    this.resetAddProcessDirectory();
    this.processDirectoryDisplay = true;
    this.isModifyProcessDirectory = false;
    this.isAddProcessDirectory = true;
  }

  // 新增节点分类的保存
  saveAddProcessDirectory(): void {
    if (this.allSystemIds.indexOf(this.systemId) > -1) {
      showMessage(this.messageService, 'error', `节点分类标识【${this.systemId}】已存在，禁止添加！`);
      return;
    }
    this.expandNodes = [...this.processDirectorys];
    const bodyParams = {
      systemId: this.systemId,
      systemName: this.systemName,
      parentId: this.selectedProcessDirectory['keyId']
    };
    this.processMaintenanceService.addProcessDirectory(bodyParams)
      .subscribe(
      data => {

        handleResult(data, this.messageService, () => {
          this.queryProcessDirectoryTree(false, this.expandNodes);
          this.selectedProcessDirectory = null;
        }, true, { message: ' 保存成功', errorMsg: '保存失败' });
      },
      err => {
        handleError(err, this.messageService, () => {

        });
      }
      );
    this.processDirectoryDisplay = false;
    this.isModifyProcessDirectory = false;
    this.isAddProcessDirectory = false;
  }

  // 新增节点分类的重置
  resetAddProcessDirectory(): void {

    this.systemName = null;
    this.systemId = null;
  }


  // 新增或修改节点分类的保存
  updateOrSaveProcessDirectory(): void {
    this.isModifyProcessDirectory && this.updateModifyProcessDirectory();
    this.isAddProcessDirectory && this.saveAddProcessDirectory();
  }

  // 新增或修改节点分类的重置
  resetProcessProcessDirectory(): void {
    this.isModifyProcessDirectory && this.resetModifyProcessDirectory();
    this.isAddProcessDirectory && this.resetAddProcessDirectory();
  }
  // 删除节点分类
  deleteProcessDirectory(): void {
    if (Array.isArray(this.selectedProcessDirectory.children) && this.selectedProcessDirectory.children.length) {
      showMessage(this.messageService, 'error', `【${this.selectedProcessDirectory['systemName']}】 下已有节点建模记录，禁止删除！`);
      return;
    }
    this.confirmationService.confirm({
      message: '您确定要删除吗？',
      header: '信息提示框',
      icon: 'fa fa-question-circle',
      key: 'mapConfirm',
      accept: () => {
        this.expandNodes = [...this.processDirectorys];
        const bodyParams = {
          id: this.selectedProcessDirectory['keyId']
        };
        this.processMaintenanceService.deleteProcessDirectory(bodyParams)
          .subscribe(
          data => {

            handleResult(data, this.messageService, () => {
              this.queryProcessDirectoryTree(false, this.expandNodes);
              this.selectedProcessDirectory = null;
              this.headerTip = null;
              this.queryBusinessNodes = [];
            }, true, { message: ' 删除成功', errorMsg: '删除失败' });
          },
          err => {
            handleError(err, this.messageService, () => {

            });
          }
          );
      }
    });
  }


  // 查看扩展属性按钮
  viewBusinessNodePropExtends(item): void {
    this.selectedBusinessNode = { ...item };
    this.showNum++;
    this.businessNodeProsExtendDisplay = true;
  }

}








