import { Injectable } from '@angular/core';
import { HttpService } from '../../common/service/http.service';
import { MESSAGE_API } from '../process-definition.constant';
import { RTS_HTTP_URL } from 'app/@uisftech/common/service/http.constant';
// import { RTS_HTTP_URL } from '../../common/constant/config';

@Injectable()
export class MessageTemplateService {

  constructor(
    private httpService: HttpService
  ) {
  }

  // 查询消息模板
  searchMsgTemList = (params) => this.httpService.post(RTS_HTTP_URL + MESSAGE_API.msgTemList, params, null);

  // 更新修改的消息模板
  updateMsgTem = (params) => this.httpService.post(RTS_HTTP_URL + MESSAGE_API.updateMsgTem, params, null);

  // 保存新增的消息模板
  addMsgTem = (params) => this.httpService.post(RTS_HTTP_URL + MESSAGE_API.addMsgTem, params, null);

  // 删除消息模板
  deleteMsgTem = (params) => this.httpService.post(RTS_HTTP_URL + MESSAGE_API.deleteMsgTem, params, null);

}
