import { Component, OnInit, DoCheck, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ConfirmationService } from 'primeng/primeng';
import { handleResult, handleError, formatDate, showMessage } from '../../common/func/common-func';import { AuthorizationModel } from '../process-definition-model';
import { paginatorUtils, CALENDAR_CN, FULL_HORIZONTAL_DATE_FORMAT } from '../../common/constant/config';
import { EngTypeModel } from '../../process-instance/process-instance-model';
import { MessageService } from 'primeng/components/common/messageservice';
import { TaskToAuthorizationService } from '../task-to-authorization/task-to-authorization.service';
import { VALID_FLAGS, VALID_FLAGS_DESCP } from '../../common/constant/code.value';
import { AddTaskAuthorizationManageComponent } from './add-task-authorization-manage/add-task-authorization-manage.component';
import { HttpService } from 'app/@uisftech/common/service/http.service';

@Component({
  selector: 'app-task-to-authorization-manage',
  templateUrl: './task-to-authorization-manage.component.html',
  styleUrls: ['./task-to-authorization-manage.component.scss']
})
export class TaskToAuthorizationManageComponent implements OnInit, DoCheck {
  msgs = [];
  // 分页实例
  paginatorInstance: AuthorizationModel = new AuthorizationModel();
  // 数据总量
  total: string;
  // 任务转授权列表
  authorizationList: AuthorizationModel[] = [];
  // 选中的任务授权
  selectedAuthorization: AuthorizationModel[] = [];
  // 查询条件的任务授权
  queryAuthorization: AuthorizationModel = new AuthorizationModel();
  // 生效标志
  valiFlags: EngTypeModel[] = [];
  // 查询条件中的选中的生效状态
  queryAuthorEffect: EngTypeModel = new EngTypeModel();
  // 新增或修改选中的生效标志
  modifyOrAddValiFlag: EngTypeModel = new EngTypeModel();
  // 授权方式
  authorizationType: EngTypeModel[] = [];
  // 新增或修改选中的授权类型
  modifyOrAddAuthorizationType: EngTypeModel;
  // 日期
  CALENDAR_CN = CALENDAR_CN;
  // 查询授权生效时间
  queryAuthorizationStartTime: Date;
  // 查询授权结束时间
  queryAuthorizationEndTime: Date;
  // 控制新增或修改任务转授权模态框显隐
  authorizationDisplay = false;
  // 修改任务转授权变量
  isModifyAuthorizationDisplay = false;
  // 新增任务转授权变量
  isAddAuthorizationDisplay = false;
  // 转授权表单
  authorizationForm: FormGroup;
  // 新增或修改转授权实例
  modifyOrAddAuthorization: AuthorizationModel = new AuthorizationModel();
  // 修改转授权重置时的变量
  defaultAuthorization: AuthorizationModel = new AuthorizationModel();
  // 新增或修改的授权生效时间
  modifyOrAddAuthorizationStartTime: Date;
  // 新增或修改的授权结束时间
  modifyOrAddAuthorizationEndTime: Date;
  // 登录用户
  loginSys: string;
  // 当前时间
  currentTime: Date;

  // 选中的流程
  selectedProcess = {
    defNames: '',
    defIds: ''
  };

  // 弹框参数
  dialogParam = {
    dialogHeight: 0,
    headerTitle: '',
    display: false,
    showModel: 0
  };
  // 可选择的最小日期
  minDate: Date = new Date('1970/1/1');
  // 可选择的最大日期
  maxDate: Date = new Date('30000/12/31');
  // 当前是选择授权人 or 被授权人
  selectRoleFlag: string;
  @ViewChild('addAuthorization') addTaskAuthorizationManageComponent: AddTaskAuthorizationManageComponent;
  // 授权内容
  valiFlagOPtions = VALID_FLAGS_DESCP;
  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private taskToAuthorizationService: TaskToAuthorizationService,
    public httpService: HttpService
  ) {
    this.valiFlags = VALID_FLAGS;

    this.authorizationType = [
      { type: '0', descp: '追加' },
      { type: '1', descp: '替换' }
    ];

    this.createValidators();
  }

  ngOnInit() {
    this.loginSys = sessionStorage.getItem('sys');
    this.paginatorInstance.rowNum = paginatorUtils.rowNum;
    this.paginatorInstance.startRow = paginatorUtils.startRow;
  }

  ngDoCheck(): void {   //触发变更检测机制就是调用DoCheck
    this.currentTime = new Date();
  }


  createValidators() {
    this.authorizationForm = this.fb.group({
      'sendIdName': ['', Validators.required],
      'reciveIdName': ['', Validators.required],
      'valiFlagName': ['', Validators.required],
      'startTimeName': [''],
      'endTimeName': [''],
      'remarkName': ['']
    });
  }



  queryFirst(table) {
    table.first = 0;
    this.query(table);
  }


  // 按照搜索条件查询
  query(table): void {
    table.first = table.first || 0;
    table.rows = table.rows || parseInt(paginatorUtils.rowNum, 10);
    // 每页显示的条数
    this.paginatorInstance.rowNum = table.rows + '';
    // 当前页
    this.paginatorInstance.startRow = (table.first / table.rows) + 1 + '';
    this.getAuthorizationList(this.queryAuthorEffect.type, this.queryAuthorization.reciveId, this.queryAuthorizationStartTime, this.queryAuthorizationEndTime, this.queryAuthorization.flowCode, this.queryAuthorization.sendId);
  }

  // 重置查询
  reset(dt): void {
    this.queryAuthorization.reciveId = null;
    this.queryAuthorization.sendId = null;
    this.queryAuthorization.flowCode = null;
    this.queryAuthorization.flowName = null;
    this.queryAuthorEffect = new EngTypeModel();
    this.queryAuthorizationStartTime = null;
    this.queryAuthorizationEndTime = null;
    this.queryFirst(dt);
  }


  // 获取任务授权列表
  getAuthorizationList(state?: string, reciveId?: string, queryAuthorizationStartTime?: any, queryAuthorizationEndTime?: any, flowCode?: any, sendId?: any): void {
    const startTime = queryAuthorizationStartTime && formatDate(queryAuthorizationStartTime, FULL_HORIZONTAL_DATE_FORMAT);
    const endTime = queryAuthorizationEndTime && formatDate(queryAuthorizationEndTime, FULL_HORIZONTAL_DATE_FORMAT);
    if (startTime && endTime) {
      if (new Date(startTime).getTime() > new Date(endTime).getTime()) {
        showMessage(this.messageService, 'error', '结束时间应晚于开始时间');
        return;
      }
    }
    const bodyParams = {
      state,
      reciveId,
      startTime,
      endTime,
      flowCode,
      sendId,
      pageRows: parseInt(this.paginatorInstance.rowNum, 10),
      curPage: parseInt(this.paginatorInstance.startRow, 10)
    };
    this.taskToAuthorizationService.queryAuthorizationList(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.authorizationList = data.pageData[0].result;
            this.total = data.pageData[0].totalRows;
            if (this.authorizationList.length > 0) {
              this.authorizationList.map(item => {
                item.isShowCancel = item.startTime < this.currentTime.getTime() ? true : false;
                // 兼容旧数据格式
                item.flowName = item.flowName? item.flowName: '全部流程';
              })
            }
          }, true, { errorMsg: '查询失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      );
  }

  // 新增转授权预约信息按钮
  addAuthorization(): void {
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '新增任务转授权预约信息';
    this.dialogParam.showModel = 1;
    this.addTaskAuthorizationManageComponent && this.addTaskAuthorizationManageComponent.resetAuthorization();
  }

  // 取消授权
  cancleAuthorization(item, table): void {
    if (item.state === '2') {
      showMessage(this.messageService, 'error', '授权已经取消！');
      return;
    }
    const bodyParams = {
      id: item.id+''
    };
    this.taskToAuthorizationService.cancelAuthorize(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.query(table);
          }, true, { message: '取消成功！', errorMsg: '取消失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      );
  }

  // 删除的请求
  deleteRequest(params, table) {
    this.taskToAuthorizationService.deleteAcceditRecord(params)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.query(table);
          }, true, { message: '删除成功！', errorMsg: '删除失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      );
  }

  // 逐个删除
  deleteAuthorization(item, table): void {
    this.confirmationService.confirm({
      message: '确定删除吗？',
      header: '信息提示框',
      icon: 'fa fa-question-circle',
      accept: () => {
        const bodyParams = {
          id: item.id+''
        };
        this.deleteRequest(bodyParams, table);
      }
    });
  }

  // 批量删除
  batchDeleteAuthorization (table) {
    if (this.selectedAuthorization.length) {
      let flag = false;
      const ids = [];
      this.selectedAuthorization.map(item=>{
        if (item.state !== '0') {
          return flag = true;
        }
        ids.push(item.id);
      });
      if (flag) {
        return showMessage(this.messageService, 'error', '选中的数据中包含已生效或不生效的授权任务，不允许执行批量删除操作！');
      }
      this.confirmationService.confirm({
        message: '确定删除吗？',
        header: '信息提示框',
        icon: 'fa fa-question-circle',
        accept: () => {
          const bodyParams = {
            id: ids.join(',')
          };
          this.deleteRequest(bodyParams, table);
        }
      });
    } else {
      showMessage(this.messageService, 'error', '请选择要删除的授权任务！');
      return;
    }
  }

  // 批量取消授权
  batchCancelAuthorization(table) {
    if (this.selectedAuthorization.length) {
      let flag = false;
      const ids = [];
      this.selectedAuthorization.map(item=>{
        if (item.state !== '1') {
          return flag = true;
        }
        ids.push(item.id);
      });
      if (flag) {
        return showMessage(this.messageService, 'error', '选中的任务中包含已生效或不生效的授权任务，不允许执行批量取消授权操作！');
      }
      this.confirmationService.confirm({
        message: '确定对选中的任务进行取消授权操作吗？',
        header: '信息提示框',
        icon: 'fa fa-question-circle',
        accept: () => {
          const bodyParams = {
            id: ids.join(',')
          };
          this.taskToAuthorizationService.cancelAuthorize(bodyParams)
            .subscribe(
              data => {
                handleResult(data, this.messageService, () => {
                  this.query(table);
                }, true, { message: '取消授权成功！', errorMsg: '取消授权失败' });
              },
              err => {
                handleError(err, this.messageService, () => {
                });
              }
            );
        }
      });
    } else {
      return showMessage(this.messageService, 'error', '请选择要执行授权取消操作的授权任务！');
    }
  }

  // 选则流程弹框
  selectProcess() {
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '选择流程范围';
    this.dialogParam.showModel = 2;
  }
  orgid: any;
  // 选择被授权人 or 授权人
  selectSendId(flag) {
    let sys = this.httpService.handleParams({}).commonHeader;
    if (sys.extParam['orgId']) {
      this.orgid = sys.extParam['orgId'];
    }
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '员工列表';
    this.dialogParam.showModel = 3;
    this.selectRoleFlag = flag;
  }
  // 选择流程的回调
  selectProcessCallback(value) {
    if(Array.isArray(value)) {
      const names = [];
      const ids = [];
      value.forEach(item=>{
        names.push(item.defName);
        ids.push(item.defId);
      });
      this.queryAuthorization.flowName = names.join(',');
      this.queryAuthorization.flowCode = ids.join(',');
    }
    this.onDialogHidden();
  }
  // 选择人员的回调
  selectRoleCallback(value) {
    this.onDialogHidden();
    if (value.tellerId) {
      this.selectRoleFlag === 'reciveId'? this.queryAuthorization.reciveId = value.tellerId : this.queryAuthorization.sendId = value.tellerId;
    }
  }

  // 关闭弹框
  onDialogHidden() {
    this.dialogParam.display = false;
    this.dialogParam.headerTitle = '';
    this.dialogParam.showModel = 0;
  }

  // 新增成功的回调
  addSuccessCallback(event, table) {
    if (event) {
      this.onDialogHidden();
      this.queryFirst(table);
    }
  }
}
