import { Component, OnInit, Input, EventEmitter, Output, OnChanges } from '@angular/core';
import { ProsTypeModel, AUTO_NODE_PRO_TYPE, EventServiceListModel, RuleModel } from '../../process-definition-model';
import { AUTO_NODE_TYPES } from '../../../common/constant/code.value';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { BusinessNodeManagementService } from '../business-node-management.service';
import { handleResult, handleError, findObject, isJsonString, showMessage } from '../../../common/func/common-func';
import { AUTHORIZATION_API } from '../../process-definition.constant';

@Component({
  selector: 'app-update-props-extend-auto',
  templateUrl: './update-props-extend-auto.component.html',
  styleUrls: ['./update-props-extend-auto.component.scss']
})
export class UpdatePropsExtendAutoComponent implements OnChanges {

  // 接收的传入的节点
  @Input() isFlowModel: any;
  //接收的传入的属性
  @Input() inValue: any;
  // 模型类型
  @Input() modelType: any;
  // 父dialog  解决dialog嵌套
  @Input() dialogTemplate: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();

  @Input() isRest: boolean;

  msg = [];
  // 自动节点的属性类型
  prosExtendAutoTypes = AUTO_NODE_TYPES;
  // 选中的扩展属性类型
  selectedProsExtendType: ProsTypeModel = new ProsTypeModel();
  // 表单
  autoNodeForm: FormGroup;

  dialogParam = {
    display: false,
    headerTitle: ''
  };

  // 修改的标志
  isAssignmentFlag = true;
  // 修改的属性类型
  updatePropsType: string;
  // 选中的事件服务
  selectedEventService: EventServiceListModel = new EventServiceListModel();
  // 重置时的临时变量
  defaultSelectedEventService: EventServiceListModel = new EventServiceListModel();
  // 显示规则树还是自动服务调用
  showRuleTreeOrAutoService = {
    isRuleTree: false,
    isAutoService: false,
  }
  // 节点进入、离开条件
  nodeConditonString: string;
  // 重置时的的临时变量
  defaultNodeConditonString: string;
  // 节点进入、离开条件映射值
  nodeConditonMapValue: RuleModel = new RuleModel();
  // 重置时的临时变量
  defaultNodeConditonMapValue: RuleModel = new RuleModel();
  // 任务处理时间
  taskHandleTime: number;
  // 子节点属性类型
  prosExtendAutoNodeTypes = AUTO_NODE_TYPES;
  // 调用服务规则
  isRuleNum: number = 0;
  // 控制脚本测试的模态框显隐
  verifyScriptDisplay = false;
  // 控制事件服务列表模态框显隐
  eventDisplay = false;
  // 触发调用服务值
  endEmitService: string;
  // 选中事件服务映射数组
  selectedEventServiceMap: any[];
  // 重置时的临时变量
  defaultEventService: any[];


  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private businessNodeManagementService: BusinessNodeManagementService,
  ) {
    this.createValidators();
  }

  ngOnChanges() {
    this.setPropsAndValue();
  }


  createValidators() {
    this.autoNodeForm = this.fb.group({
      'updateProsExtendTypeName': ['', Validators.required],
      'ServiceCallName': ['', Validators.required],
      'endEmitServiceName': ['', Validators.required],
      'nodeExitConName': [''],
      'nodeExitConMapName': ['']
    });
  }

  // 选择规则树
  selectedPro(): void {
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '规则树及列表';
    this.showRuleTreeOrAutoService = {
      isRuleTree: true,
      isAutoService: false,
    }
  }

  // 选择自动服务调用
  selecteServiceMapping(): void {
    this.dialogParam.headerTitle = '事件服务';
    this.dialogParam.display = true;
    this.showRuleTreeOrAutoService = {
      isAutoService: true,
      isRuleTree: false
    };
  }



  // 从调用服务传上来的值
  callBack(event): void {
    this.dialogParam.display = !event;
    if (this.selectedProsExtendType) {
      if (this.selectedProsExtendType.type === AUTO_NODE_PRO_TYPE.AUTO_NODE_SERVERICE_CALL) {
        this.selectedEventService = { ...event };

      }
    }

  }


  // 从规则树传上来的值
  callBackRule(event): void {
    this.dialogParam.display = !event;
    if (this.selectedProsExtendType) {
      if (this.selectedProsExtendType.type === AUTO_NODE_PRO_TYPE.NODE_ENTRY_CONDITION || this.selectedProsExtendType.type === AUTO_NODE_PRO_TYPE.NODE_EXIT_CONDITION) {
        this.nodeConditonMapValue = { ...event };
      }
    }

  }

  // 事件服务调用选择
  selectedPropsMap(): void {
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '事件服务';
    this.showRuleTreeOrAutoService = {
      isRuleTree: false,
      isAutoService: true
    }
  }

  // 节点进入和离开事件服务调用选择
  selectedEvent(eventService): void {
    this.eventDisplay = true;
    eventService.reset();
    eventService.resetSelected();
    eventService.getEventServiceList();
  }

  // 选择规则树
  selectedProRule(): void {
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '规则树及列表';
    this.showRuleTreeOrAutoService = {
      isRuleTree: true,
      isAutoService: false
    };
    this.isRuleNum++;
  }
  // 验证脚本按钮
  verifyScript(verifyTestScript): void {
    verifyTestScript.resetListAndResult();
    this.verifyScriptDisplay = true;
  }

  // 测试脚本模态框的关闭按钮
  callBackVerifyScript(event): void {
    this.verifyScriptDisplay = !event;
  }


  // 重置修改的属性
  resetUpdateProsExtend(): void {
    this.selectedEventService = this.defaultSelectedEventService;
    this.nodeConditonMapValue = this.defaultNodeConditonMapValue;
    this.nodeConditonString = this.defaultNodeConditonString;
    if (Array.isArray(this.defaultEventService) && this.defaultEventService.length > 0) {
      this.selectedEventServiceMap = Array.from(this.defaultEventService);
      const nameArr = [];
      this.selectedEventServiceMap.forEach(val => {
        nameArr.push(val.name);
      })
      this.endEmitService = nameArr.join('、');
    }
  }


  // 选择事件服务
  selectedEventServiceFun(item): void {
    if (!item.selectedValues || (item.selectedValues && item.selectedValues < 1)) {
      showMessage(this.messageService, 'error', '请选择调用服务！');
      return;
    }
    let hash = {};
    this.selectedEventServiceMap = item.selectedValues.reduce((item, next) => {
      hash[next.id] ? '' : hash[next.id] = true && item.push(next);
      return item;
    }, [])
    const noSetService = this.selectedEventServiceMap.filter(item => {
      return !item.order;
    });
    if (Array.isArray(noSetService) && noSetService.length) {
      showMessage(this.messageService, 'error', '有服务调用次序、方式、条件未配置！');
      return;
    }
    const nameArray = [];
    this.selectedEventServiceMap.forEach(val => {
      nameArray.push(val.name);
    })
    this.endEmitService = nameArray.join('、');
    this.eventDisplay = false;

  }


  //  更新属性值
  updateProsExtend(): void {
    this.isAssignmentFlag = false;
    let value = null;
    if (this.inValue.propName === AUTO_NODE_PRO_TYPE.NODE_ENTRY_CONDITION || this.inValue.propName === AUTO_NODE_PRO_TYPE.NODE_EXIT_CONDITION) {
      if (!this.nodeConditonString && !this.nodeConditonMapValue.label) {
        showMessage(this.messageService, 'error', '条件或规则映射都无属性值，禁止保存');
        return;
      }
      let valueParams = {};
      this.nodeConditonString ? valueParams = {
        type: '3',
        value: this.nodeConditonString
      } : this.nodeConditonMapValue.label ? valueParams = {
        type: '2',
        value: JSON.stringify(this.nodeConditonMapValue)
      } : valueParams;
      value = JSON.stringify(valueParams);
    } else if (this.selectedProsExtendType.type === AUTO_NODE_PRO_TYPE.AUTO_NODE_SERVERICE_CALL) {
      value = JSON.stringify(this.selectedEventService);
    }
    const bodyParams = {
      ownId: this.isFlowModel.nodeId || '',
      ownType: this.modelType,
      propName: this.selectedProsExtendType.type || '',
      propDesc: this.selectedProsExtendType.descp || '',
      propValue: value,
    }
    this.businessNodeManagementService.updateNodePropExtends(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.outValue.emit(event);
        }, true, { message: ' 更新成功', errorMsg: '更新失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
          this.outValue.emit(event);
        });
      }
      );
    this.isAssignmentFlag = true;
  }



  // 给属性类型和属性赋值
  setPropsAndValue(): void {
    if (this.isAssignmentFlag) {
      if (this.inValue) {
        this.selectedProsExtendType = findObject(this.inValue.propName, this.prosExtendAutoNodeTypes, 'type');
        this.updatePropsType = this.selectedProsExtendType.descp;
        if (this.inValue.propName === AUTO_NODE_PRO_TYPE.AUTO_NODE_SERVERICE_CALL) {
          this.autoNodeForm.removeControl('endEmitServiceName');
          this.autoNodeForm.addControl('ServiceCallName', new FormControl('', Validators.required));
          const value = JSON.parse(this.inValue.propValue);
          this.selectedEventService = { ...value };
          this.defaultSelectedEventService = { ...value };
        } else if (this.inValue.propName === AUTO_NODE_PRO_TYPE.NODE_ENTRY_CONDITION || this.inValue.propName === AUTO_NODE_PRO_TYPE.NODE_EXIT_CONDITION) {
          this.autoNodeForm.removeControl('endEmitServiceName');
          this.autoNodeForm.removeControl('ServiceCallName');
          const value = JSON.parse(this.inValue.propValue);
          if (isJsonString(value.value)) {
            const jsonVal = JSON.parse(value.value);
            this.defaultNodeConditonMapValue = { ...jsonVal };
            this.nodeConditonMapValue = { ...jsonVal };
          } else {
            this.nodeConditonString = value.value;
            this.defaultNodeConditonString = value.value;
          }
        } else if (this.inValue.propName === AUTO_NODE_PRO_TYPE.START_TASK_EVENT || this.inValue.propName === AUTO_NODE_PRO_TYPE.END_TASK_EVENT) {
          this.autoNodeForm.removeControl('ServiceCallName');
          this.autoNodeForm.addControl('endEmitServiceName', new FormControl('', Validators.required));
          const value = JSON.parse(this.inValue.propValue);
          this.defaultEventService = Array.from(value);
          const nameArr = [];
          value.forEach(val => {
            nameArr.push(val.name);
          })
          this.endEmitService = nameArr.join('、');
        }
      }
    }
  }



}
