import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { handleResult, handleError, findObject, showMessage } from '../../common/func/common-func';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { ConfirmationService } from 'primeng/primeng';
import { DictionaryTypeModel, NodeBusinessStatusModel } from '../process-definition-model';
import { paginatorUtils, Pager } from '../../common/constant/config';
import { BusinessStatusService } from './business-status.service';
import { EVENT_TYPE_CODE, STATUS_TYPE } from '../../common/constant/code.value';

@Component({
  selector: 'app-business-status',
  templateUrl: './business-status.component.html',
  styleUrls: ['./business-status.component.scss']
})
export class BusinessStatusComponent implements OnInit {



  @Input() inValue: any;
  // 模型类型
  @Input() modelType: any;
  // 是否重置
  @Input() isRest: number = 0;

  @Output() outValue: EventEmitter<any> = new EventEmitter();

  msg = [];
  // 流程节点业务状态信息列表
  nodeBusinessStatusInfoList: NodeBusinessStatusModel[] = [];
  // 查询条件的业务状态实例
  queryNodeBusinessStatusInfo: NodeBusinessStatusModel = new NodeBusinessStatusModel();
  // 分页实例
  paginatorInstance: NodeBusinessStatusModel = new NodeBusinessStatusModel();
  // 选中的通用参数实例
  selectedNodeBusinessStatusInfo: NodeBusinessStatusModel = new NodeBusinessStatusModel();
  // 类型编码
  eventTypeCodes = EVENT_TYPE_CODE;
  // 选中的参数类型
  selectedEventTypeCode: DictionaryTypeModel = new DictionaryTypeModel();
  // 数据总量
  total: string;

  dialogParam = {
    display: false,
    isAdd: false,
    isModify: false,
    headerTitle: ''
  };
  // 状态类型
  nodeStatusTypes = STATUS_TYPE;










  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private businessStatusService: BusinessStatusService,
  ) {
  }

  ngOnInit() {
    this.paginatorInstance.rowNum = paginatorUtils.rowNum;
    this.paginatorInstance.startRow = paginatorUtils.startRow;
  }





  // 查询流程节点业务状态信息列表
  getBusinessStatusInfoList(processCode?: string, processName?: string, eventType?: string): void {
    const bodyParams = {
      processCode,
      processName,
      eventType,
      pageRows: parseInt(this.paginatorInstance.rowNum, 10),
      curPage: parseInt(this.paginatorInstance.startRow, 10)
    };
    this.businessStatusService.listWfNodeState(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.nodeBusinessStatusInfoList = data.pageData[0].result;
          this.total = data.pageData[0].totalRows;
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {

        });
      }
      );
  }



  // 重置查询
  reset(): void {
    this.selectedEventTypeCode = new DictionaryTypeModel();
    this.queryNodeBusinessStatusInfo.processCode = null;
    this.queryNodeBusinessStatusInfo.processName = null;
  }

  // 按照搜索条件查询
  queryFirst(table) {
    table.first = 0;
    this.query(table);
  }



  // 查询
  query(table): void {
    table.first = table.first || 0;
    table.rows = table.rows || parseInt(paginatorUtils.rowNum, 10);
    // 每页显示的条数
    this.paginatorInstance.rowNum = table.rows + '';
    // 当前页
    this.paginatorInstance.startRow = (table.first / table.rows) + 1 + '';
    this.getBusinessStatusInfoList(this.queryNodeBusinessStatusInfo.processCode, this.queryNodeBusinessStatusInfo.processName, this.selectedEventTypeCode.type);
  }





  // 删除通用参数
  deleteNodeBusinessStatusInfo(item, table) {
    this.confirmationService.confirm({
      message: '确认删除吗？',
      header: '信息提示框',
      icon: 'fa fa-question-circle',
      accept: () => {
        const bodyParams = {
          id: item.id || ''
        };
        this.businessStatusService.deletleWfNodeState(bodyParams)
          .subscribe(
          data => {
            handleResult(data, this.messageService, () => {
              this.query(table);
            }, true, { message: '删除成功！', errorMsg: '删除失败' });
          },
          err => {
            handleError(err, this.messageService, () => {
            });
          }
          );
      }
    });
  }




  // 新增通用参数按钮
  addNodeBusinessStatusInfo(businessStatus): void {
    this.dialogParam = {
      display: true,
      isAdd: true,
      isModify: false,
      headerTitle: '新增流程节点业务状态'
    };
    businessStatus.resetAdd();
  }

  // 修改通用参数按钮
  modifyNodeBusinessStatusInfo(item, businessStatus): void {
    this.selectedNodeBusinessStatusInfo = { ...item };
    this.dialogParam = {
      display: true,
      isAdd: false,
      isModify: true,
      headerTitle: '修改流程节点业务状态'
    };
    businessStatus.resetModify();
  }


  // 新增属性模态框传值 ***
  addOrUpdatePropsCallBack(event): void {
    this.dialogParam.display = !event;
    this.getBusinessStatusInfoList();
  }



}
