import { Injectable } from '@angular/core';
import { HttpService } from '../../common/service/http.service';
import { PROCESS_MAINTENANCE_API } from '../process-definition.constant';
import { ACTIVITI_CONTEXT_PATH, ACTIVITI_DESIGN_CONTEXT_PATH } from '../../common/constant/config';
import { RTS_HTTP_URL, ACTIVITI_HTTP_URL } from 'app/@uisftech/common/service/http.constant';

@Injectable()
export class ProcessMaintenanceService {

  constructor(
    private httpService: HttpService
  ) {
  }

  searchProcessDirectory = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.searchProcessDirectoryTree, params, null);

  // 查询流程定义
  queryProcessDefinitionsByStore = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.queryProcessDefinitions, params, null);

  // 流程定义新增保存
  addProcessDefinition = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.addProcessDefinition, params, null);

  // 流程定义修改更新
  editProcessDefinition = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.editProcessDefinition, params, null);

  // 流程定义删除
  deleteProcessDefinition = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.deleteProcessDefinition, params, null);

  // 复制流程定义
  copyWorkFlow = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.copyWorkFlow, params, null);

  // 查询流程版本
  selectVersionList = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.selectVersionList, params, null);

  // 部署
  publishProcess = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.publishProcess, params, null);

  // 更新修改的系统节点
  updateProcessDirectory = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.updateProcessDirectory, params, null);

  // 保存新增的系统节点
  addProcessDirectory = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.addProcessDirectory, params, null);

  // 删除系统节点
  deleteProcessDirectory = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.deleteProcessDirectory, params, null);

  // 跳转到流程设计器
  toActivitiPage = (params) => window.open(ACTIVITI_HTTP_URL + ACTIVITI_CONTEXT_PATH + ACTIVITI_DESIGN_CONTEXT_PATH + `${params}`);

  // 流程数据导出
  exportWorkFlow = (params) => window.open(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.exportWorkFlow + `/${params}`, null);

  // 流程数据导入
  importWorkFlow = (params) => this.httpService.post2(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.importWorkFlow, params, null);

  // 版本恢复
  backHistVersion = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.backHistVersion, params, null);

  // 获取流程所有扩展属性
  wfDefCollect = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.wfDefCollect, params, null);

  // 部署流程前检验是否可以即时生效
  checkWorkFlow = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_MAINTENANCE_API.checkWorkFlow, params, null);
}
