import { Component, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { GeneralParametersService } from '../../general-parameters.service';
import { handleResult, handleError, findObject } from '../../../../common/func/common-func';
import { EVENT_TYPE } from '../../../../common/constant/code.value';
import { GeneralParamModel, DictionaryTypeModel } from '../../../process-definition-model';

@Component({
  selector: 'app-add-global-event',
  templateUrl: './add-global-event.component.html',
  styleUrls: ['./add-global-event.component.scss']
})
export class AddGlobalEventComponent implements OnChanges {

  msg = [];

  @Input() isModify: boolean;
  @Input() updateValue: Object ={};

  @Output() outValueEmitter: EventEmitter<any> = new EventEmitter();
  // 表单
  globalEventForm: FormGroup;
  //新增或者修改的事项大类实例
  addOrUpdateGlobalParams: GeneralParamModel = new GeneralParamModel();
  // 修改时重置的临时变量
  defaultGlobalParams: GeneralParamModel = new GeneralParamModel();
  eventType = EVENT_TYPE;
  // 选中的事件类型
  selectedParameCode: DictionaryTypeModel = new DictionaryTypeModel();
  // 重置时事件类型的临时变量
  defaultSelectedParameCode: DictionaryTypeModel = new DictionaryTypeModel();
  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private generalParametersService: GeneralParametersService
  ) { }


  ngOnChanges() {
    this.createValidators();
    this.addOrUpdateGlobalParams = new GeneralParamModel();
    this.defaultGlobalParams = new GeneralParamModel();
    if (this.isModify) {
      this.selectedParameCode = findObject(this.updateValue['paramCode'], this.eventType, 'type');
      this.addOrUpdateGlobalParams.id = this.updateValue['id'];
      this.addOrUpdateGlobalParams.paramObjName = this.updateValue['paramObjName'];
      this.addOrUpdateGlobalParams.paramObjCode = this.updateValue['paramObjCode'];
      this.defaultGlobalParams.paramObjName = this.updateValue['paramObjName'];
      this.defaultGlobalParams.paramObjCode = this.updateValue['paramObjCode'];
      this.defaultGlobalParams.id = this.updateValue['id'];
      this.defaultSelectedParameCode = findObject(this.updateValue['paramCode'], this.eventType, 'type');
      this.globalEventForm.patchValue({
        paramCode: this.selectedParameCode,
        paramObjName: this.updateValue['paramObjName'],
        paramObjCode: this.updateValue['paramObjCode']
      });
    }
  }

  createValidators() {
    this.globalEventForm = this.fb.group({
      paramCode: ['', Validators.required],
      paramObjName: ['', Validators.required],
      paramObjCode: ['', Validators.required]
    });

  }

  // 保存
  preserveClick() {
    // 处理是否提交
    for (const key in this.globalEventForm.controls) {
      if (this.globalEventForm.controls.hasOwnProperty(key)) {
        this.globalEventForm.controls[key].markAsDirty();
      }
    }

    // 表单校验不通过  不提交
    if (!this.globalEventForm.valid) {
      return;
    }
    // 提交服务
    const param = {
      paramType: 'GE',
      paramCode: this.selectedParameCode.type,
      paramName: this.selectedParameCode.descp,
      paramObjCode: this.addOrUpdateGlobalParams.paramObjCode,
      paramObjName: this.addOrUpdateGlobalParams.paramObjName
    };
    const url = this.isModify? 'updateWfParam': 'addWfParam';
    this.generalParametersService[url](this.isModify? Object.assign(param, {
      id: this.addOrUpdateGlobalParams.id
    }) : param)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.outValueEmitter.emit(true)
          }, true, { message: '保存成功！', errorMsg: '保存失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
            this.outValueEmitter.emit(false)
          });
        }
      );
  }


  // 重置
  reset() {
    if (this.isModify) {
      this.globalEventForm.get('paramCode').setValue(this.defaultSelectedParameCode);
      this.globalEventForm.get('paramObjName').setValue(this.defaultGlobalParams.paramObjName);
      this.globalEventForm.get('paramObjCode').setValue(this.defaultGlobalParams.paramObjCode);
      this.addOrUpdateGlobalParams = this.defaultGlobalParams;
      this.selectedParameCode = this.defaultSelectedParameCode;
    } else {
      this.globalEventForm.get('paramCode').setValue(null);
      this.globalEventForm.get('paramObjName').setValue(null);
      this.globalEventForm.get('paramObjCode').setValue(null);
      this.addOrUpdateGlobalParams = this.defaultGlobalParams = new GeneralParamModel();
      this.selectedParameCode = this.defaultSelectedParameCode = new DictionaryTypeModel();
    }
  }
}