import { OnInit, Component, Output, EventEmitter, Input, OnChanges } from "@angular/core";
import { Pager } from "../../../common/constant/config";
import { ProcessDefinitionModel } from "../../process-definition-model";
import { MessageService } from "primeng/components/common/messageservice";
import { handleResult, handleError } from "../../../common/func/common-func";
import { TaskToAuthorizationService } from "../task-to-authorization.service";

@Component({
  selector: 'process-select-list',
  templateUrl: './process-select-list.component.html'
})

export class ProcessSelectListComponent implements OnInit {
  @Input()
  selectedProcessId: string;
  msg = [];
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  // 流程定义分页
  pager: Pager = new Pager();
  // 查询到的流程定义集合
  queryProcessDefinitions: ProcessDefinitionModel[] = [];
  // 查询条件的流程定义实例
  queryProcessDefinition: ProcessDefinitionModel = new ProcessDefinitionModel();
  // 选中的流程
  selectedProcess: ProcessDefinitionModel[] = [];
  constructor(
    private messageService: MessageService,
    private taskToAuthorizationService: TaskToAuthorizationService
  ) { }

  ngOnInit() {
    this.queryProcessCode();
  }

  // 流程定义分页
  paginate(event) {
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.queryProcessCode();
  }

  // 按条件查询
  query(): void {
    this.pager.first = '0';
    this.pager.page = '1';
    this.queryProcessCode();

  }

  // 重置查询条件
  reset(): void {
    this.queryProcessDefinition.defId = null;
    this.queryProcessDefinition.defName = null;
    this.query();
  }

  // 查询流程编码按钮
  queryProcessCode(): void {
    const bodyParams = {
      defId: this.queryProcessDefinition.defId,
      defName: this.queryProcessDefinition.defName,
      version: 0,
      pageRows: parseInt(this.pager.rows, 10),
      curPage: parseInt(this.pager.page, 10)
    };
    this.taskToAuthorizationService.selectAuthorizeWfDef(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.queryProcessDefinitions = data.pageData[0].result;
          this.pager.totalRecords = data.pageData[0].totalRows;
          if (this.selectedProcessId){
            this.selectedProcess = this.queryProcessDefinitions.filter(item => this.selectedProcessId.indexOf(item.defId)>-1);
          }
        }, true, { message:'查询成功' ,errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );

  }

  // 选中流程编码
  selectProcessCode(): void {
    this.outValue.emit(this.selectedProcess);
  }
}