import { Component, OnInit } from '@angular/core';
import { MessageService } from 'primeng/components/common/messageservice';
import { GeneralParamModel } from '../../process-definition-model';
import { GENERAL_PARAMS_TYPES } from '../../../common/constant/code.value';
import { GeneralParametersService } from '../general-parameters.service';
import { handleResult, handleError } from '../../../common/func/common-func';
import { Pager } from 'app/pages/tzb/workflow/activiti-work-flow/common/constant/config';
import { ConfirmationService } from 'primeng/primeng';

@Component({
  selector: 'app-channel-map',
  templateUrl: './channel-map.component.html',
  styleUrls: ['./channel-map.component.scss']
})

export class ChannelMapComponent implements OnInit {

  generalParameterList: GeneralParamModel[] = [];
  // 查询条件的通用参数实例
  queryGeneralParam: GeneralParamModel = new GeneralParamModel();
  // 分页
  pager = new Pager();
  // 参数类型
  generalParamTypes = GENERAL_PARAMS_TYPES;

  dialogParam = {
    headerTitle: '',
    display: false,
    showModel: '',
    updateValue: {},
    dialogHeight: 550,
    dialogWidth: 700,
    isShow: false
  };

  dislogHeights = {
    add: 650,
    update: 650,
  };

  dialogWidths = {
    add: 900,
    update: 900,
  };



  constructor(
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private generalParametersService: GeneralParametersService,
  ) {
  }
  
  ngOnInit() {
  }

  // 重置查询
  reset(): void {
    this.queryGeneralParam.paramCode = null;
    this.queryGeneralParam.paramObjCode = null;
    //this.query();
  }

  // 按照搜索条件查询
  query() {
    this.pager.first = '0';
    this.pager.page = '1';
    this.getGeneralParamList('H0', this.queryGeneralParam.paramCode, this.queryGeneralParam.paramObjCode);
  }


  // 获取事项大类列表
  getGeneralParamList(paramType = 'H0', paramCode?: string, paramObjCode?: string): void {
    const bodyParams = {
      paramType,
      paramCode,
      paramObjCode,
      pageRows: parseInt(this.pager.rows, 10),
      curPage: parseInt(this.pager.page, 10)
    };
    this.generalParametersService.listWfParam(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.generalParameterList = data.pageData[0].result;
            this.pager.totalRecords = data.pageData[0].totalRows;
          }, true, { message: '查询成功', errorMsg: '查询失败' });
        },
        err => {
          handleError(err, this.messageService, () => {

          });
        }
      );
  }

  // 分页
  paginate(event) {
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.getGeneralParamList('H0', this.queryGeneralParam.paramCode, this.queryGeneralParam.paramObjCode);
  }


  // 添加
  add() {
    this.dialogParam.dialogHeight = this.dislogHeights.add;
    this.dialogParam.dialogWidth = this.dialogWidths.add;
    this.dialogParam.isShow = !this.dialogParam.isShow;
    this.dialogParam.headerTitle = '新增渠道映射';
    this.dialogParam.display = true;
    this.dialogParam.showModel = '1';
  }

  // 修改
  update(item) {
    this.dialogParam.updateValue = { ...item };
    this.dialogParam.dialogHeight = this.dislogHeights.update;
    this.dialogParam.dialogWidth = this.dialogWidths.update;
    this.dialogParam.headerTitle = '修改服务信息';
    this.dialogParam.display = true;
    this.dialogParam.showModel = '2';

  }

  // 新增属性模态框传值 ***
  callBack(event): void {
    this.dialogParam.display = !event;
    this.query();
    this.dialogParam.updateValue = null;
  }

  // 删除通用参数
  delete(item) {
    this.confirmationService.confirm({
      message: '确认删除吗？',
      header: '信息提示框',
      icon: 'fa fa-question-circle',
      accept: () => {
        const bodyParams = {
          id: item.id || ''
        };
        this.generalParametersService.deletleWfParam(bodyParams)
          .subscribe(
            data => {
              handleResult(data, this.messageService, () => {
                this.getGeneralParamList();
              }, true, { message: '删除成功！', errorMsg: '删除失败' });
            },
            err => {
              handleError(err, this.messageService, () => {
              });
            }
          );
      }
    });
  }
}
