import { OnInit, Component, Output, EventEmitter } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { AuthorizationModel } from "../../process-definition-model";
import { TaskToAuthorizationService } from "../task-to-authorization.service";
import { showMessage, formatDate, handleResult, handleError } from "../../../common/func/common-func";
import { MessageService } from "primeng/components/common/messageservice";
import { FULL_HORIZONTAL_DATE_FORMAT, CALENDAR_CN } from "../../../common/constant/config";
import { VALID_FLAGS_DESCP } from "../../../common/constant/code.value";
import { Message } from "primeng/primeng";
import { HttpService } from "app/@uisftech/common/service/http.service";

@Component({
  selector: 'add-task-authorization',
  templateUrl: './add-task-authorization.component.html'
})

export class AddTaskAuthorizationComponent implements OnInit {
  msgs: Message[] = [];
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  // 转授权表单
  authorizationForm: FormGroup;
  // 新增或修改转授权实例
  modifyOrAddAuthorization: AuthorizationModel = new AuthorizationModel();
  // 修改转授权重置时的变量
  defaultAuthorization: AuthorizationModel = new AuthorizationModel();
  // 新增或修改的授权生效时间
  modifyOrAddAuthorizationStartTime: Date;
  // 新增或修改的授权结束时间
  modifyOrAddAuthorizationEndTime: Date;
  // 登录用户
  loginSys: string;
  // 登录用户名
  userName: string;
  // 日期
  CALENDAR_CN = CALENDAR_CN;
  // 弹框参数
  dialogParam = {
    dialogHeight: 0,
    headerTitle: '',
    display: false,
    showModel: 0
  };
  // 新增时授权生效开始时间不能早于当前时间
  today: Date = new Date();
  // 可选择的最小日期
  minDate: Date = new Date('1970/1/1');
  // 可选择的最大日期
  maxDate: Date = new Date('30000/12/31');
  // 授权内容
  valiFlags = VALID_FLAGS_DESCP;
  // 修改初始化时间
  defaultDate = new Date();
  constructor(
    private fb: FormBuilder,
    private taskToAuthorizationService: TaskToAuthorizationService,
    private messageService: MessageService,
    public httpService: HttpService
  ){
    this.createValidators();
  }
  ngOnInit() {
    // 从报文头中取userId
    this.loginSys = this.httpService.handleParams({}).commonHeader;
    // 从 session中获取userName
    this.userName = JSON.parse(sessionStorage.getItem('chName'))['userName'];
    // 设置日期插件的初始化时间
    const date = new Date();
    const dateStr = date.getFullYear() + '/'+ (date.getMonth()+1) + '/'+ date.getDate() +' '+ date.getHours() + ':' + 30+ ':'+ date.getSeconds();
    this.defaultDate = new Date(dateStr);
    const dateStr1 = date.getFullYear() + '/'+ (date.getMonth()+1) + '/'+ date.getDate() +' '+ date.getHours() + ':'+ (date.getMinutes()+5)+ ':'+ date.getSeconds();    
    this.modifyOrAddAuthorizationStartTime = new Date(dateStr1);
    // 授权内容默认选择授权流程处理 --- '0'
    this.modifyOrAddAuthorization.validFlag = { type: '0', descp: '授权流程处理' };
  }
  createValidators() {
    this.authorizationForm = this.fb.group({
      'reciveIdName': ['', Validators.required],
      // 'valiFlagName': ['', Validators.required],
      'flowCode': [''],
      'startTimeName': ['', Validators.required],
      'endTimeName': ['', Validators.required],
      'remarkName': [''],
      'validFlag': [{ type: '0', descp: '授权流程处理' }, Validators.required]
    });
  }
  // 新增重置
  resetAuthorization(): void {
    this.modifyOrAddAuthorization.reciveId = null;
    this.modifyOrAddAuthorization.reciveName = null;
    this.modifyOrAddAuthorization.flowName = null;
    this.modifyOrAddAuthorization.flowCode = null;
    this.modifyOrAddAuthorization.validFlag = { type: '0', descp: '授权流程处理' };
    this.modifyOrAddAuthorizationStartTime = null;
    this.modifyOrAddAuthorizationEndTime = null;
    this.modifyOrAddAuthorization.remark = null;
  }

  // 新增保存
  saveAuthorization(): void {
    // 授权人和被授权人不能是同一个人
    if (this.loginSys['userId'] === this.modifyOrAddAuthorization.reciveId) {
      return showMessage(this.messageService, 'error', '授权人和被授权人不能是同一个人');
    }
    const authorizationStartTime = new Date(this.modifyOrAddAuthorizationStartTime).getTime();
    const authorizationEndTime = new Date(this.modifyOrAddAuthorizationEndTime).getTime();
    const nowTime = Date.now();
    if (!!authorizationStartTime && !!authorizationEndTime) {
      if (authorizationStartTime > authorizationEndTime) {
        showMessage(this.messageService, 'error', '授权结束时间应晚于授权生效时间');
        return;
      }
    }
    let startTime = null;
    if (authorizationStartTime < nowTime) {
      startTime = formatDate(nowTime, FULL_HORIZONTAL_DATE_FORMAT);
    } else {
      startTime = this.modifyOrAddAuthorizationStartTime && formatDate(this.modifyOrAddAuthorizationStartTime, FULL_HORIZONTAL_DATE_FORMAT);
    }
    const endTime = this.modifyOrAddAuthorizationEndTime && formatDate(this.modifyOrAddAuthorizationEndTime, FULL_HORIZONTAL_DATE_FORMAT);
    const addBodyParams = {
      sendId: this.loginSys['userId'], // 取登录人id
      sendName: this.userName, // 取登录人名称
      operator: this.loginSys['userId'],  // 取登录人id
      operatorName: this.userName, // 取登录人名称
      remark: this.modifyOrAddAuthorization.remark,
      reciveId: this.modifyOrAddAuthorization.reciveId,
      reciveName: this.modifyOrAddAuthorization.reciveName,
      flowCode: this.modifyOrAddAuthorization.flowCode || '',
      flowName: this.modifyOrAddAuthorization.flowName || '',
      validFlag:this.modifyOrAddAuthorization.validFlag.type,
      startTime,
      endTime
    };
    this.taskToAuthorizationService.addAcceditRecord(addBodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.outValue.emit({flag: true});
          }, true, { message: '保存成功！', errorMsg: '保存失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      );
  }

  // 选则流程弹框
  selectProcess() {
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '选择流程范围';
    this.dialogParam.showModel = 1;
  }
  orgid: any;
  // 选择被授权人
  selectSendId() {
    let sys = this.httpService.handleParams({}).commonHeader;
    if (sys.extParam['orgId']) {
      this.orgid = sys.extParam['orgId'];
    }
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '员工列表';
    this.dialogParam.showModel = 2;
  }
  // 选择流程的回调
  selectProcessCallback(value) {
    if (Array.isArray(value)) {
      const names = [];
      const ids = [];
      value.forEach(item => {
        names.push(item.defName);
        ids.push(item.defId);
      });
      this.modifyOrAddAuthorization.flowName = names.join(',');
      this.modifyOrAddAuthorization.flowCode = ids.join(',');
    }
    this.onDialogHidden();
  }
  // 选择人员的回调
  selectRoleCallback(value) {
    this.onDialogHidden();
    if (value.tellerId) {
      this.modifyOrAddAuthorization.reciveId = value.tellerId;
      this.modifyOrAddAuthorization.reciveName = value.tellerName;
    }
  }

  // 关闭弹框
  onDialogHidden() {
    this.dialogParam.display = false;
    this.dialogParam.headerTitle = '';
    this.dialogParam.showModel = 0;
  }
}