import { Component, OnInit } from '@angular/core';
import { EXCEPTION_TYPE, EXCEPTION_PATTERN, EXCEPTION_HANDLE_MODELS, EXCEPTION_HANDLE_USR_TYPES, EXCEPTION_NOTIFY_TYPES, EXCEPTION_AUTO_HANDLE, SMALL_EXCEPTION_PATTERN, SMALL_EXCEPTION_TYPE } from '../../common/constant/code.value';
import { EngTypeModel } from '../../process-instance/process-instance-model';
import { ExceptionStrategyModel } from '../process-definition-model';
import { ConfirmationService } from 'primeng/primeng';
import { MessageService } from 'primeng/components/common/messageservice';
import { ExceptionStrategyService } from './exception-strategy.service';
import { paginatorUtils } from '../../common/constant/config';
import { handleError, handleResult } from '../../common/func/common-func';

@Component({
  selector: 'app-exception-strategy',
  templateUrl: './exception-strategy.component.html',
  styleUrls: ['./exception-strategy.component.scss']
})
export class ExceptionStrategyComponent implements OnInit {

  msg = [];
  // 异常类型
  exceptionTypes = EXCEPTION_TYPE;;
  // 查询条件中选中的异常类型
  queryExceptionType: EngTypeModel = new EngTypeModel();
  // 异常模式
  exceptionPatterns = EXCEPTION_PATTERN;
  // 查询条件中选中的异常模式
  queryExceptionPattern: EngTypeModel = new EngTypeModel();
  // 异常策略列表
  exceptionStrategyList: ExceptionStrategyModel[] = [];
  // 列表中选中的异常策略
  selectedExceptionStrategy: ExceptionStrategyModel = new ExceptionStrategyModel();
  // 分页实例
  paginatorInstance: ExceptionStrategyModel = new ExceptionStrategyModel();
  // 异常策略数据总量
  total: string;
  // 异常处理模式
  exceptionHandleModels = EXCEPTION_HANDLE_MODELS;
  // 异常处理人员类型
  exceptionHandleUser = EXCEPTION_HANDLE_USR_TYPES;
  // 异常通知方式
  exceptionNotify = EXCEPTION_NOTIFY_TYPES;
  // 异常自动处理模式
  execptionAutoHandle = EXCEPTION_AUTO_HANDLE;

  dialogParam = {
    display: false,
    headerTitle: '',
    isAdd: false,
    isModify: false
  };

  // 屏蔽后的查询条件中的异常模式
  queryExceptionPatterns = SMALL_EXCEPTION_PATTERN;
  // 屏蔽后的查询条件中的异常类型
  queryExceptionTypes = SMALL_EXCEPTION_TYPE;


  constructor(
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private exceptionStrategyService: ExceptionStrategyService,
  ) { }

  ngOnInit() {
    this.paginatorInstance.rowNum = paginatorUtils.rowNum;
    this.paginatorInstance.startRow = paginatorUtils.startRow;
  }


  // 获取异常信息列表
  getExceptionStrategyList(expMode?: string, expType?: string): void {
    const bodyParams = {
      expMode,
      expType,
      pageRows: parseInt(this.paginatorInstance.rowNum, 10),
      curPage: parseInt(this.paginatorInstance.startRow, 10)
    };
    this.exceptionStrategyService.searchSelectList(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.exceptionStrategyList = data.pageData[0].result;
          this.total = data.pageData[0].totalRows;
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {

        });
      }
      );
  }


  // 按照搜索条件查询
  queryFirst(table) {
    table.first = 0;
    this.query(table);
  }



  query(table): void {
    table.first = table.first || 0;
    table.rows = table.rows || parseInt(paginatorUtils.rowNum, 10);
    // 每页显示的条数
    this.paginatorInstance.rowNum = table.rows + '';
    // 当前页
    this.paginatorInstance.startRow = (table.first / table.rows) + 1 + '';
    this.getExceptionStrategyList(this.queryExceptionPattern.type, this.queryExceptionType.type);
  }


  // 重置查询
  reset(): void {
    this.queryExceptionPattern = new EngTypeModel();
    this.queryExceptionType = new EngTypeModel();
  }

  // 新增异常策略按钮
  addExceptionStrategy(): void {
    this.selectedExceptionStrategy = new ExceptionStrategyModel();
    this.dialogParam.isAdd = true;
    this.dialogParam.isModify = false;
    this.dialogParam.headerTitle = '新增异常策略';
    this.dialogParam.display = true;
  }

  modifyExceptionStrategy(item): void {
    this.selectedExceptionStrategy = { ...item };
    this.dialogParam.isAdd = false;
    this.dialogParam.isModify = true;
    this.dialogParam.headerTitle = '修改异常策略';
    this.dialogParam.display = true;
  }


  // 新增/修改模态框传值 ***
  addOrUpdateCallBack(event): void {
    this.dialogParam.display = !event;
    this.getExceptionStrategyList();
  }


  // 删除
  deleteExceptionStrategy(item, table): void {
    this.confirmationService.confirm({
      message: '确认删除吗？',
      header: '信息提示框',
      icon: 'fa fa-question-circle',
      accept: () => {
        const bodyParams = {
          id: item.id
        };
        this.exceptionStrategyService.deleteExceptionDo(bodyParams)
          .subscribe(
          data => {
            handleResult(data, this.messageService, () => {
              this.query(table);
            }, true, { message: '删除成功！', errorMsg: '删除失败' });
          },
          err => {
            handleError(err, this.messageService, () => {
            });
          }
          );
      }
    });

  }







}
