import { OnInit, Component } from "@angular/core";
import { Pager } from 'app/@uisftech/common/constant/config';
import { GeneralParametersService } from '../general-parameters.service';
import { handleResult, handleError } from '../../../common/func/common-func';
import { MessageService } from 'primeng/components/common/messageservice';
import { ConfirmationService, Message } from 'primeng/primeng';

@Component({
  selector: 'app-process-range',
  templateUrl: './process-range.component.html',
  styleUrls: ['./process-range.compoent.scss']
})

export class ProcessRangeComponent implements OnInit {
  msgs: Message[] = [];
  // 分页
  pager: Pager = new Pager();
  // 选中的流程
  selectedProcess = {
    paramName: ''
  };
  // 弹框
  dialogParam = {
    display: false,
    headerTitle: '',
    index: 0
  };
  processList = [];
  constructor(
    private generalParametersService: GeneralParametersService,
    private messageService: MessageService,
    private confirmationService: ConfirmationService
  ) {

  }
  ngOnInit(){
    this.selectedProcess['paramCode'] = '';
    this.selectedProcess['paramName'] = '';
  }
  initQuery() {
    this.pager.first = '0';
    this.pager.page = '1';
    this.query('M0', this.selectedProcess['paramCode']);
  }
  // 查询
  query(paramType = 'M0', paramCode?: string): void {
    const bodyParams = {
      paramType,
      paramCode,
      pageRows: parseInt(this.pager.rows, 10),
      curPage: parseInt(this.pager.page, 10)
    };
    this.generalParametersService.listWfParam(bodyParams).subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.processList = data.pageData[0].result;
          this.pager.totalRecords = data.pageData[0].totalRows;
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {

        });
      }
      );
  }
  reset() {
    this.selectedProcess['paramCode'] = '';
    this.selectedProcess['paramName'] = '';
    this.initQuery();
  }
  // 分页
  paginate(event) {
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.query('M0', this.selectedProcess['paramCode']);
  }
  // 选择流程
  selectProcess(defineList) {
    defineList.reset();
    defineList.queryProcessCode();
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '选择流程';
    this.dialogParam.index = 1;
  }
  // 选择流程的回调
  callBackDefine(event): void {
    this.dialogParam.display = !event;
    this.dialogParam.index = 0;
    this.selectedProcess['paramCode'] = event.data.defId;
    this.selectedProcess['paramName'] = event.data.defName;
  }
  // 新增
  add(rangeAdd) {
    rangeAdd.reset();
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '新增流程范围';
    this.dialogParam.index = 2;
  }
  // 新增成功
  addSucess(event) {
    this.dialogParam.display = false;
    this.dialogParam.index = 0;
    this.initQuery();
  }
  // 删除
  delete (item) {
    this.confirmationService.confirm({
      message: '确定要删除此流程吗？',
      header: '信息提示框',
      icon: 'fa fa-question-circle',
      key: 'rangeConfirm',
      accept: () => {
        this.generalParametersService.deletleWfParam({id: item.id || ''}).subscribe(
          data => {
            handleResult(data, this.messageService, () => {
              this.initQuery();
            }, true, { message: '删除成功！', errorMsg: '删除失败' });
          },                                          
          err => {
            handleError(err, this.messageService, () => {
    
            });
          });
      }
  });
  }
}