import { Injectable } from '@angular/core';
import { HttpService } from '../../common/service/http.service';
import { BUSINESS_NODE_MANAGEMENT, RULE_ENGINE_API, VERIFY_SCRIPT_API } from '../process-definition.constant';
// import { RTS_HTTP_URL, ACTIVITI_HTTP_URL, RULE_ENGINE_HTTP_URL } from '../../common/constant/config';
import { RTS_HTTP_URL, ACTIVITI_HTTP_URL, RULE_ENGINE_HTTP_URL } from 'app/@uisftech/common/service/http.constant'

@Injectable()
export class BusinessNodeManagementService {

  constructor(
    private httpService: HttpService
  ) {


  }
  // 查询业务节点
  queryBussinessNode = (params) => this.httpService.post(RTS_HTTP_URL + BUSINESS_NODE_MANAGEMENT.queryBussinessNodeList, params, null);

  // 更新修改的业务节点
  updateBussinessNode = (params) => this.httpService.post(RTS_HTTP_URL + BUSINESS_NODE_MANAGEMENT.updateBussinessNode, params, null);

  // 保存新增的业务节点
  addBussinessNode = (params) => this.httpService.post(RTS_HTTP_URL + BUSINESS_NODE_MANAGEMENT.addBussinessNode, params, null);

  // 删除业务节点
  deleteBussinessNode = (params) => this.httpService.post(RTS_HTTP_URL + BUSINESS_NODE_MANAGEMENT.deleteBussinessNode, params, null);

  // 复制业务节点
  copyBussinessNode = (params) => this.httpService.post(RTS_HTTP_URL + BUSINESS_NODE_MANAGEMENT.copyBussinessNode, params, null);

  // 查询业务扩展属性列表
  queryBussinessNodePropExtends = (params) => this.httpService.post(RTS_HTTP_URL + BUSINESS_NODE_MANAGEMENT.viewBussinessNodePropExtends, params, null);

  // 跟新修改的扩展属性
  updateNodePropExtends = (params) => this.httpService.post(RTS_HTTP_URL + BUSINESS_NODE_MANAGEMENT.updateNodePropExtends, params, null);

  // 保存新增的扩展属性
  addNodePropExtends = (params) => this.httpService.post(RTS_HTTP_URL + BUSINESS_NODE_MANAGEMENT.addNodePropExtends, params, null);

  // 删除扩展属性
  deleteNodePropExtends = (params) => this.httpService.post(RTS_HTTP_URL + BUSINESS_NODE_MANAGEMENT.deleteNodePropExtends, params, null);

  // 获取选人规则
  getPersonRule = (params) => this.httpService.get(ACTIVITI_HTTP_URL + BUSINESS_NODE_MANAGEMENT.getPersonRule, params, null);

  // 加载规则引擎树
  queryLoadLibProjectTree = (params) => this.httpService.post(RULE_ENGINE_HTTP_URL + RULE_ENGINE_API.loadLibProject, params, null);

  // 查询规则列表根据左侧树的ID
  queryRuleFileList = (params) => this.httpService.post(RULE_ENGINE_HTTP_URL + RULE_ENGINE_API.queryRuleFileList, params, null);

  // js校验
  javaScriptTest = (params) => this.httpService.post(RTS_HTTP_URL + VERIFY_SCRIPT_API.javaScriptTest, params, null);


}
