import { Component, OnInit, Input, } from '@angular/core';
import { BusinessNodeProsExtendModel, FLOW_MODEL_PRO_TYPE, } from '../../process-definition-model';
import { EVENT_SERVICE_CALL_MODE, } from '../../../common/constant/code.value';
import { handleResult, handleError, findObject, isJsonString } from '../../../common/func/common-func';
import { MessageService } from 'primeng/components/common/messageservice';
import { ConfirmationService } from 'primeng/primeng';
import { BusinessNodeManagementService } from '../../business-node-management/business-node-management.service';

@Component({
  selector: 'app-extend-props-list',
  templateUrl: './extend-props-list.component.html',
  styleUrls: ['./extend-props-list.component.scss']
})
export class ExtendPropsListComponent implements OnInit {

  msg = [];
  // 从流程建模页面传回来的流程定义
  @Input() inValue: any;
  // 从点击流程建模界面的查看扩展属性按钮传的值
  @Input() isShow: any;
  // 流程节点扩展属性列表
  businessNodeProsExtendList: BusinessNodeProsExtendModel[] = [];
  // 单选选中的扩展节点属性实例
  selectedBusinessNodeProsExtend: BusinessNodeProsExtendModel;
  // 新增属性按钮 新增模态框数据是否清空
  isRest: number = 0;
  // 修改属性按钮, 修改模态框数据是否清空
  isRestModify: number = 0;
  // 显示新增属性模态框
  showProsModal = true;

  dialogParam = {
    display: false,
    showModel: '',
    headerTitle: ''
  };
  // 所属模型类型
  isNodeModelType: string = '1';  //  '0' -节点属性 '1'-流程属性
  // 调用模式
  eventServiceCallMode = EVENT_SERVICE_CALL_MODE;

  constructor(
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private businessNodeManagementService: BusinessNodeManagementService,
  ) {

  }

  ngOnInit() {
  }


  // 查看扩展属性按钮
  getBusinessNodePropExtends(defId?: string): void {
    if (defId || this.inValue) {
      const bodyParams = {
        ownId: defId || this.inValue.defId
      };
      this.businessNodeManagementService.queryBussinessNodePropExtends(bodyParams)
        .subscribe(
          data => {
            handleResult(data, this.messageService, () => {
              this.businessNodeProsExtendList = data.pageData[0].result;
              this.businessNodeProsExtendList.forEach(element => {
                if (element.propName === FLOW_MODEL_PRO_TYPE.FLOW_START_EVENT || element.propName === FLOW_MODEL_PRO_TYPE.FLOW_END_EVENT || element.propName === FLOW_MODEL_PRO_TYPE.FLOW_END_ERROR_EVENT || element.propName === FLOW_MODEL_PRO_TYPE.HANG_UP_EVENT || element.propName === FLOW_MODEL_PRO_TYPE.RECOVER_EVENT || element.propName === FLOW_MODEL_PRO_TYPE.CANCEL_EVENT) {
                  if (element.propValue) {
                    const value = JSON.parse(element.propValue);
                    const nameArray = [];
                    value.forEach((item, index) => {
                      if (item.order && item.callMode) {
                        const mode = findObject(item.callMode, this.eventServiceCallMode, 'type');
                        const str = `(${index + 1})、${item.name}:调用次序为${item.order};调用模式为${mode.descp}`;
                        nameArray.push(str);
                      } else {
                        const str = `(${index + 1})、${item.name}:调用次序(无);调用模式(无)`;
                        nameArray.push(str);
                      }
                    });
                    element.extendType = nameArray.join('、');
                    // const nameArray = [];
                    // value.forEach(item => {
                    //   nameArray.push(item.name);
                    // });
                  }
                } else if (element.propName === FLOW_MODEL_PRO_TYPE.SKIP_FIRST_NODE || element.propName === FLOW_MODEL_PRO_TYPE.CHECK_PERSON || element.propName === FLOW_MODEL_PRO_TYPE.CONFIR_INFO || element.propName === FLOW_MODEL_PRO_TYPE.PRESON_PRO || event.type === FLOW_MODEL_PRO_TYPE.CALL_BACK_JUMP) {
                  if (element.propValue) {
                    element.extendType = element.propValue === 'true' ? '是' : '否';
                  }
                } else if (element.propName === FLOW_MODEL_PRO_TYPE.TIME_TO_START) {
                  if (element.propValue && element.propValue !== 'undefined') {
                    // element.extendType = `${element.propValue}天`;
                    const value = JSON.parse(element.propValue);
                    let str = '';
                    let str1 = '';
                    Array.isArray(value) && value.forEach((item, index) => {
                      if (item['startWeek']) {
                        str += `(${index + 1})、${item.startWeekStr}的${item.startTime}启动；`
                      } else if (item['startDate']) {
                        str1 += `(${index + 1})、每个月的${item.startDate}号${item.startTime}启动；`
                      }
                    });
                    element.extendType = str + str1;
                  }
                } else if (element.propName === FLOW_MODEL_PRO_TYPE.MAX_TIME) {
                  // if (element.propValue) {
                  //   element.extendType = `${element.propValue}小时`;
                  // }
                  if (element.propValue) {
                    const value = JSON.parse(element.propValue);
                    if (isJsonString(value.value)) {
                      const jsonVal = JSON.parse(value.value);
                      element.extendType = jsonVal.label
                    } else {
                      element.extendType = `${value.value}小时`;
                    }
                  }
                } else if (element.propName === FLOW_MODEL_PRO_TYPE.OUT_TIME_EXECUTION 
                  || element.propName === FLOW_MODEL_PRO_TYPE.CHECKINOUT
                  || element.propName === FLOW_MODEL_PRO_TYPE.FLOW_AUTO_REPEAT_PROCESS  
                  ) {
                  if (element.propValue) {
                    const value = JSON.parse(element.propValue)
                    element.extendType = value.descp;
                  }
                } else if (element.propName === FLOW_MODEL_PRO_TYPE.START_CONDITION || element.propName === FLOW_MODEL_PRO_TYPE.END_CONDITION) {
                  if (element.propValue) {
                    const value = JSON.parse(element.propValue);
                    if (isJsonString(value.value)) {
                      const jsonVal = JSON.parse(value.value);
                      element.extendType = jsonVal.label
                    } else {
                      element.extendType = value.value;
                    }
                  }
                } else if (element.propName === FLOW_MODEL_PRO_TYPE.CHANNEL_FLOW_CTRL_LIST) {
                  if (element.propValue) {
                    element.extendType = element.propValue;
                  }
                } else if (element.propName === FLOW_MODEL_PRO_TYPE.IS_FLOW_AUTHORIZE) {
                  if (element.propValue) {
                    const value = JSON.parse(element.propValue);
                    element.extendType = value['isflag']? '允许转授权': '不允许转授权';
                  }
                } else if (element.propName === FLOW_MODEL_PRO_TYPE.NOTIFY_FOR_PROCESS_CREATER) {
                  if (element.propValue) {
                    const value = JSON.parse(element.propValue);
                    element.extendType = value['eventId'];
                  }
                }
              });
            }, true, { errorMsg: '查询失败' });
          },
          err => {
            handleError(err, this.messageService, () => {
            });
          }
        );
    }

  }




  // 新增扩展属性 ***
  addProsExtend(): void {
    this.isRest = 0;
    this.dialogParam.display = true;
    this.showProsModal = true;
    if (this.inValue) {
      this.dialogParam.headerTitle = `新增${this.inValue['defName']}的扩展属性`;
      this.dialogParam.showModel = '10';
    }
  }


  // 修改扩展属性 ***
  modifyProsExtend(item): void {
    this.isRestModify = 0;
    this.selectedBusinessNodeProsExtend = { ...item };
    this.dialogParam.showModel = '11';
    this.dialogParam.headerTitle = `修改${item.propDesc}扩展属性`;
    this.dialogParam.display = true;
    this.showProsModal = true;
  }

  // 删除扩展属性
  deleteProsExtend(item): void {
    this.confirmationService.confirm({
      message: '您确定要删除吗？',
      header: '信息提示框',
      icon: 'fa fa-question-circle',
      key: 'mapConfirm2',
      accept: () => {
        const bodyParams = {
          ownId: item.ownId,
          propName: item.propName
        };
        this.businessNodeManagementService.deleteNodePropExtends(bodyParams)
          .subscribe(
            data => {
              handleResult(data, this.messageService, () => {
                this.getBusinessNodePropExtends();
                this.selectedBusinessNodeProsExtend = null;
              }, true, { message: '删除成功', errorMsg: '删除失败' });
            },
            err => {
              handleError(err, this.messageService, () => {
              });
            }
          );
      }
    });
  }



  // 修改属性模态框传值 ***
  updatePropscallBack(event): void {
    this.showProsModal = !event;
    this.getBusinessNodePropExtends();
    this.selectedBusinessNodeProsExtend = null;
  }



  // 新增属性模态框传值 ***
  addPropsCallBack(event): void {
    this.dialogParam.display = !event;
    this.getBusinessNodePropExtends();
    this.selectedBusinessNodeProsExtend = null;
  }


}
