import { Component, OnInit, OnChanges, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { DictionaryTypeModel, GeneralParamModel, GENERAL_PARAMS_TYPE, MessageModel, BUSINESS_SIGN_APPROVE_PRO_TYPE, ProcessDefinitionModel, GeneralParamTimeOutModel } from '../../process-definition-model';
import { GENERAL_PARAMS_TYPES, GLOBAL_NOTIFY_TYPES, MESSAGE_TEMPLATE_TYPE } from '../../../common/constant/code.value';
import { GeneralParametersService } from '../general-parameters.service';
import { handleResult, handleError, findObject, showMessage } from '../../../common/func/common-func';
import { Pager } from 'app/pages/tzb/workflow/activiti-work-flow/common/constant/config';
import { ConfirmationService } from 'primeng/primeng';

@Component({
  selector: 'app-timeout-notification',
  templateUrl: './timeout-notification.component.html',
  styleUrls: ['./timeout-notification.component.scss']
})

export class TimeoutNotificationComponent implements OnInit {

  // 通用参数列表
  generalParameterList: GeneralParamModel[] = [];
  // 超时通知列表类型
  timeOutNotifyGeneralParam: any[] = [];
  //新增或者修改的事项大类实例
  addOrUpdateGeneralParams: GeneralParamModel = new GeneralParamModel();
  // 分页
  pager: Pager = new Pager();
  // 参数类型
  generalParamTypes = GENERAL_PARAMS_TYPES;

  dialogParam = {
    display: false,
    isAdd: false,
    isModify: false,
    headerTitle: ''
  };

  bigClassForm: FormGroup;

  //  选中的内容模板模板
  contentTemplate: MessageModel = new MessageModel();
  // 超时前几小时通知
  taskHandleTime: number;
  // 修改重置时的临时变量
  defaultTaskHandleTime: number;
  // 超时通知策略通知类型
  timeOutNotify = GLOBAL_NOTIFY_TYPES;
  // 选中的通知类型
  selectedTimeOutNotify: DictionaryTypeModel = new DictionaryTypeModel();
  // 修改重置时的临时变量
  defaultSelectedTimeOutNotify: DictionaryTypeModel = new DictionaryTypeModel();
  // 通知机制
  notifyMechanisms = MESSAGE_TEMPLATE_TYPE;
  // 选中的通知机制
  selectedNotifyMechanism: DictionaryTypeModel = new DictionaryTypeModel();
  // 修改重置时的临时变量
  defaultSelectedNotifyMechanism: DictionaryTypeModel = new DictionaryTypeModel();
  // 控制预警消息模板模态框显隐
  messageDisplay = false;
  // 预警消息模板标题
  messageHeaderTitle: string;
  // 修改重置时的临时变量
  defaultTemplateId: string;


  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private generalParametersService: GeneralParametersService,
  ) {
    this.createValidators();
  }



  ngOnInit() {
  }


  createValidators() {
    this.bigClassForm = this.fb.group({
      'taskHandleName': ['', Validators.required],
      'notifyTypeName': ['', Validators.required],
      'notifyMechanismName': ['', Validators.required]
      // 'notifyTemplateName': ['', Validators.required]
    });

  }



  // 获取事项大类列表
  getGeneralParamList(paramType = 'G0', paramCode?: string, paramName?: string): void {
    this.timeOutNotifyGeneralParam = [];
    let obj = {};
    obj['id'] = [];
    const bodyParams = {
      paramType,
      paramCode,
      paramName,
      pageRows: parseInt(this.pager.rows, 10),
      curPage: parseInt(this.pager.page, 10)
    };
    this.generalParametersService.listWfParam(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.generalParameterList = data.pageData[0].result;
            this.generalParameterList && this.generalParameterList.length && this.generalParameterList.map(item => {
              obj[item.paramCode] = item.paramObjCode;
              obj['id'].push(item.id);
            })
            if (this.generalParameterList.length) {
              this.pager.totalRecords = '1';
              this.timeOutNotifyGeneralParam.push(obj);
            } else {
              this.pager.totalRecords = '0';
            }

          }, true, { errorMsg: '查询失败' });
        },
        err => {
          handleError(err, this.messageService, () => {

          });
        }
      );


  }

  // 分页
  paginate(event) {
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.getGeneralParamList();
  }


  // 改变通知机制
  changeNotifyMechanisms(event): void {
    this.contentTemplate.templateId = null;

  }


  //  新增事项大类按钮
  addBigClass(): void {
    this.resetAdd();
    this.dialogParam.headerTitle = '新增超时通知策略';
    this.dialogParam.isModify = false;
    this.dialogParam.isAdd = true;
    this.dialogParam.display = true;
  }


  // 保存
  saveOrUpate(): void {
    let bodyParams = {
      timeoutType: this.selectedTimeOutNotify.type,
      timeoutFunc: this.selectedNotifyMechanism.type,
      timeoutTemplate: this.contentTemplate.templateId,
      timeoutWarnPreHour: `${this.taskHandleTime}`
    };
    this.generalParametersService.addUpdateWfParamTimeout(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.getGeneralParamList();
            this.dialogParam.display = false;
            this.dialogParam.headerTitle = '';
            this.dialogParam.isAdd = false;
            this.dialogParam.isModify = false;
          }, true, { message: '保存成功！', errorMsg: '保存失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      );
  }


  // 修改按钮
  modify(item): void {
    console.log(item);
    this.taskHandleTime = item.timeoutWarnPreHour;
    this.defaultTaskHandleTime = item.timeoutWarnPreHour;
    const timeOutNotify = findObject(item.timeoutType, this.timeOutNotify, 'type');
    this.selectedTimeOutNotify = { ...timeOutNotify };
    this.defaultSelectedTimeOutNotify = { ...timeOutNotify };
    const notifyMechanism = findObject(item.timeoutFunc, this.notifyMechanisms, 'type');
    this.selectedNotifyMechanism = { ...notifyMechanism };
    this.defaultSelectedNotifyMechanism = { ...notifyMechanism };
    this.contentTemplate.templateId = item.timeoutTemplate;
    this.defaultTemplateId = item.timeoutTemplate;
    this.dialogParam.headerTitle = '修改超时通知策略';
    this.dialogParam.isModify = true;
    this.dialogParam.isAdd = false;
    this.dialogParam.display = true;
  }


  reset() {
    this.dialogParam.isAdd && this.resetAdd();
    this.dialogParam.isModify && this.resetModify();
  }


  // 重置新增
  resetAdd(): void {
    this.taskHandleTime = null;
    this.selectedTimeOutNotify = findObject('timeoutBefore', this.timeOutNotify, 'type');
    this.selectedNotifyMechanism = findObject('sms', this.notifyMechanisms, 'type');
    this.contentTemplate.templateId = null;
  }

  // 重置修改
  resetModify(): void {
    this.taskHandleTime = this.defaultTaskHandleTime;
    this.selectedTimeOutNotify = { ... this.defaultSelectedTimeOutNotify };
    this.selectedNotifyMechanism = { ...this.defaultSelectedNotifyMechanism };
    this.contentTemplate.templateId = this.defaultTemplateId;
  }



  // 选择预警模板
  selectedMessage(): void {
    const notifyObj = findObject(this.selectedNotifyMechanism.type, this.notifyMechanisms, 'type');
    this.messageHeaderTitle = `${notifyObj.descp}列表`;
    this.messageDisplay = true;
  }


  // 从通知模板传上来的值
  callBackMessage(event): void {
    this.messageDisplay = !event;
    this.contentTemplate = { ...event };
  }


  // 删除通用参数
  delete(item) {

    this.confirmationService.confirm({
      message: '确认删除吗？',
      header: '信息提示框',
      icon: 'fa fa-question-circle',
      accept: () => {

        const bodyParams = {
          id: item.id.join(',')
        };
        this.generalParametersService.deletleWfParam(bodyParams)
          .subscribe(
            data => {
              handleResult(data, this.messageService, () => {
                this.getGeneralParamList();
              }, true, { message: '删除成功！', errorMsg: '删除失败' });
            },
            err => {
              handleError(err, this.messageService, () => {
              });
            }
          );
      }
    });
  }






}
