import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { ConfirmationService, } from 'primeng/primeng';
import { EventServiceService } from './event-service.service';
import { handleResult, handleError, findObject } from '../../common/func/common-func';
import { DictionaryTypeModel, EventServiceListModel } from '../process-definition-model';
import { paginatorUtils } from '../../common/constant/config';
import { EVENT_SERVICE_CALL_TYPE, EVENT_SERVICE_CALL_MODE, EVENT_SERVICE_CALL_PARAMS_FLAG, NO_ALL_EVENT_SERVICE_CALL_TYPE } from '../../common/constant/code.value';

@Component({
  selector: 'app-event-service',
  templateUrl: './event-service.component.html',
  styleUrls: ['./event-service.component.scss']
})
export class EventServiceComponent implements OnInit {

  @Input() inValue: boolean = true;

  @Output() outValue: EventEmitter<any> = new EventEmitter();

  msg = [];
  // 分页实例
  paginatorInstance: EventServiceListModel = new EventServiceListModel();
  // 调用类型
  eventServiceCallType = EVENT_SERVICE_CALL_TYPE;
  // 除过all调用类型
  noAllEventServiceCallType = NO_ALL_EVENT_SERVICE_CALL_TYPE;
  // 调用模式
  eventServiceCallMode = EVENT_SERVICE_CALL_MODE;
  // 参数获取方式
  eventServiceCallParamflag = EVENT_SERVICE_CALL_PARAMS_FLAG;
  // 事件服务列表
  eventServiceList: EventServiceListModel[] = [];
  // 列表中选中行实例
  selectedEventService: EventServiceListModel = new EventServiceListModel();
  // 查询条件的的事件服务实例
  queryEventService: EventServiceListModel = new EventServiceListModel();
  // 查询条件中的调用类型
  queryCallType: DictionaryTypeModel = new DictionaryTypeModel();
  // 查询条件中的调用类型
  queryCallMode: DictionaryTypeModel = new DictionaryTypeModel();
  // 控制修改或者新增事件服务页面显隐
  eventServiceDisplay = false;
  // 修改事件服务变量
  isModifyEventServiceDisplay = false;
  // 新增事件服务变量
  isAddEventServiceDisplay = false;
  // 新增或修改的表单
  eventServiceForm: FormGroup;
  // 总数据量
  total: string;
  // 修改或者新增的事件服务实例
  modifyOrAddEventService: EventServiceListModel = new EventServiceListModel();
  // 修改时的默认变量
  defaultEventService: EventServiceListModel = new EventServiceListModel();
  // 修改或修改的选中调用类型
  modifyOrAddEventServiceCallType: DictionaryTypeModel = new DictionaryTypeModel();
  // 修改或新增的选中参数获取方式
  modifyOrAddEventServiceCallParamflag: DictionaryTypeModel;

  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private eventServiceService: EventServiceService
  ) {

    this.createValidators();
  }

  ngOnInit() {
    this.paginatorInstance.rowNum = paginatorUtils.rowNum;
    this.paginatorInstance.startRow = paginatorUtils.startRow;

  }

  queryFirst(table) {
    table.first = 0;
    this.query(table);
  }


  // 按照搜索条件查询
  query(table): void {
    table.first = table.first || 0;
    table.rows = table.rows || parseInt(paginatorUtils.rowNum, 10);
    // 每页显示的条数
    this.paginatorInstance.rowNum = table.rows + '';
    // 当前页
    this.paginatorInstance.startRow = (table.first / table.rows) + 1 + '';
    this.getEventServiceList(this.queryEventService.callService, this.queryCallType.type, this.queryEventService.callMethod);
  }


  // 重置
  reset(): void {
    this.queryEventService = new EventServiceListModel();
    this.queryCallType = new DictionaryTypeModel();
    this.queryCallMode = new DictionaryTypeModel();
  }

  // 表单校验
  createValidators() {
    this.eventServiceForm = this.fb.group({
      'nameName': new FormControl('', Validators.required),
      'callTypeName': new FormControl('', Validators.required),
      // 'callModeName': new FormControl('', Validators.required),
      'callServiceName': new FormControl('', Validators.required),
      'callMethodName': new FormControl('', Validators.required),
      'callParamflagName': new FormControl('', Validators.required),
      'targetIdName': new FormControl(''),
      'channelName': new FormControl(''),
      'callInputName': new FormControl(''),
      'callOutputName': new FormControl(''),
      'terminalCodeName': new FormControl('')
    });
  }

  // 查询事件服务列表
  getEventServiceList(callService?: string, callType?: string, callMethod?: string): void {
    const bodyParams = {
      callService,
      callType,
      callMethod,
      pageRows: parseInt(this.paginatorInstance.rowNum, 10),
      curPage: parseInt(this.paginatorInstance.startRow, 10)
    };
    this.eventServiceService.searchlistEvtList(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.eventServiceList = data.pageData[0].result;
          this.total = data.pageData[0].totalRows;
        });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
  }

  // 修改事件服务按钮
  modifyEventService(item): void {
    this.modifyOrAddEventService = Object.assign({}, item);
    this.defaultEventService = Object.assign({}, item);
    this.modifyOrAddEventServiceCallType = findObject(this.modifyOrAddEventService.callType, this.eventServiceCallType, 'type');
    // this.modifyOrAddEventServiceCallMode = findObject(this.modifyOrAddEventService.callMode, this.eventServiceCallMode, 'type');
    this.modifyOrAddEventServiceCallParamflag = findObject(this.modifyOrAddEventService.callParamflag, this.eventServiceCallParamflag, 'type');
    if (this.modifyOrAddEventServiceCallType.type === '1') {
      this.eventServiceForm.addControl('callMethodName', new FormControl('', Validators.required));
      this.eventServiceForm.addControl('callServiceName', new FormControl('', Validators.required));
      this.eventServiceForm.addControl('callParamflagName', new FormControl('', Validators.required));
    } else if (this.modifyOrAddEventServiceCallType.type === '0' || this.modifyOrAddEventServiceCallType.type === '2') {
      this.eventServiceForm.addControl('callServiceName', new FormControl('', Validators.required));
      this.eventServiceForm.addControl('callParamflagName', new FormControl('', Validators.required));
      this.eventServiceForm.removeControl('callMethodName');
    } else if (this.modifyOrAddEventServiceCallType.type === '3') {
      this.eventServiceForm.addControl('callMethodName', new FormControl('', Validators.required));
      this.eventServiceForm.removeControl('callServiceName');
      this.eventServiceForm.removeControl('callParamflagName');
    }
    this.isAddEventServiceDisplay = false;
    this.isModifyEventServiceDisplay = true;
    this.eventServiceDisplay = true;

  }

  // 新增事件服务按钮
  addEventService(): void {
    this.resetAddEventService();
    if (this.modifyOrAddEventServiceCallType.type === '1') {
      this.eventServiceForm.addControl('callMethodName', new FormControl('', Validators.required));
      this.eventServiceForm.addControl('callServiceName', new FormControl('', Validators.required));
      this.eventServiceForm.addControl('callParamflagName', new FormControl('', Validators.required));
    } else if (this.modifyOrAddEventServiceCallType.type === '0' || this.modifyOrAddEventServiceCallType.type === '2') {
      this.eventServiceForm.addControl('callServiceName', new FormControl('', Validators.required));
      this.eventServiceForm.addControl('callParamflagName', new FormControl('', Validators.required));
      this.eventServiceForm.removeControl('callMethodName');
    } else if (this.modifyOrAddEventServiceCallType.type === '3') {
      this.eventServiceForm.addControl('callMethodName', new FormControl('', Validators.required));
      this.eventServiceForm.removeControl('callServiceName');
      this.eventServiceForm.removeControl('callParamflagName');
    }
    this.isModifyEventServiceDisplay = false;
    this.isAddEventServiceDisplay = true;
    this.eventServiceDisplay = true;
  }



  // 新增/修改事件服务调用类型选择
  changeCallType(event): void {
    if (this.modifyOrAddEventServiceCallType.type === '1') {
      this.eventServiceForm.addControl('callMethodName', new FormControl('', Validators.required));
      this.eventServiceForm.addControl('callServiceName', new FormControl('', Validators.required));
      this.eventServiceForm.addControl('callParamflagName', new FormControl('', Validators.required));
    } else if (this.modifyOrAddEventServiceCallType.type === '0' || this.modifyOrAddEventServiceCallType.type === '2' || this.modifyOrAddEventServiceCallType.type === '4') {
      this.eventServiceForm.addControl('callServiceName', new FormControl('', Validators.required));
      this.eventServiceForm.addControl('callParamflagName', new FormControl('', Validators.required));
      this.eventServiceForm.removeControl('callMethodName');
    } else if (this.modifyOrAddEventServiceCallType.type === '3') {
      this.eventServiceForm.addControl('callMethodName', new FormControl('', Validators.required));
      this.eventServiceForm.removeControl('callServiceName');
      this.eventServiceForm.removeControl('callParamflagName');
    }
    this.resetContent();
  }


  // 更新保存
  updateOrSaveEventService(table): void {
    let bodyParams = {};
    if (this.isModifyEventServiceDisplay) {
      const modigfyBodyParams = {
        id: this.modifyOrAddEventService.id,
        name: this.modifyOrAddEventService.name,
        callType: this.modifyOrAddEventServiceCallType ? this.modifyOrAddEventServiceCallType.type : '',
        // 'callMode': this.modifyOrAddEventServiceCallMode ? this.modifyOrAddEventServiceCallMode.type : '',
        callService: this.modifyOrAddEventService.callService,
        callMethod: this.modifyOrAddEventService.callMethod,
        // callParamflag: this.modifyOrAddEventServiceCallParamflag ? this.modifyOrAddEventServiceCallParamflag.type : '',
        callInput: this.modifyOrAddEventService.callInput,
        callOutput: this.modifyOrAddEventService.callOutput,
        callResult: this.modifyOrAddEventService.callResult,
        targetId: this.modifyOrAddEventService.targetId,
        channel: this.modifyOrAddEventService.channel,
        terminalCode: this.modifyOrAddEventService.terminalCode
      };
      if (this.modifyOrAddEventServiceCallType.type !== '3') {
        bodyParams = Object.assign(modigfyBodyParams, { callParamflag: this.modifyOrAddEventServiceCallParamflag ? this.modifyOrAddEventServiceCallParamflag.type : '' })
      } else {
        bodyParams = { ...modigfyBodyParams }
      }
      this.eventServiceService.updateEvtService(bodyParams)
        .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.query(table);
            this.isAddEventServiceDisplay = false;
            this.isModifyEventServiceDisplay = false;
            this.eventServiceDisplay = false;
          }, true, { message: '更新成功！', errorMsg: '更新失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
        );
    } else if (this.isAddEventServiceDisplay) {
      const addBodyParams = {
        name: this.modifyOrAddEventService.name,
        callType: this.modifyOrAddEventServiceCallType ? this.modifyOrAddEventServiceCallType.type : '',
        // 'callMode': this.modifyOrAddEventServiceCallMode ? this.modifyOrAddEventServiceCallMode.type : '',
        callService: this.modifyOrAddEventService.callService,
        callMethod: this.modifyOrAddEventService.callMethod,
        // callParamflag: this.modifyOrAddEventServiceCallParamflag ? this.modifyOrAddEventServiceCallParamflag.type : '',
        callInput: this.modifyOrAddEventService.callInput,
        callOutput: this.modifyOrAddEventService.callOutput,
        callResult: this.modifyOrAddEventService.callResult,
        targetId: this.modifyOrAddEventService.targetId,
        channel: this.modifyOrAddEventService.channel,
        terminalCode: this.modifyOrAddEventService.terminalCode
      };
      if (this.modifyOrAddEventServiceCallType.type !== '3') {
        bodyParams = Object.assign(addBodyParams, { callParamflag: this.modifyOrAddEventServiceCallParamflag ? this.modifyOrAddEventServiceCallParamflag.type : '' })
      } else {
        bodyParams = { ...addBodyParams }
      }
      this.eventServiceService.addEvtService(bodyParams)
        .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.query(table);
            this.isAddEventServiceDisplay = false;
            this.isModifyEventServiceDisplay = false;
            this.eventServiceDisplay = false;
          }, true, { message: '保存成功！', errorMsg: '保存失败' });
        },
        err => {
          handleError(err, this.messageService, () => {

          });
        }
        );
    }

  }

  // 重置修改
  resetModifyEventService(): void {
    this.modifyOrAddEventService = Object.assign({}, this.defaultEventService);
    this.modifyOrAddEventServiceCallType = findObject(this.defaultEventService.callType, this.eventServiceCallType, 'type');
    // this.modifyOrAddEventServiceCallMode = findObject(this.defaultEventService.callMode, this.eventServiceCallMode, 'type');
    this.modifyOrAddEventServiceCallParamflag = findObject(this.defaultEventService.callParamflag, this.eventServiceCallParamflag, 'type');
  }

  // 重置新增
  resetAddEventService(): void {
    this.modifyOrAddEventService = new EventServiceListModel();
    this.modifyOrAddEventServiceCallType = null;
    // this.modifyOrAddEventServiceCallMode = null;
    this.modifyOrAddEventServiceCallParamflag = findObject('1', this.eventServiceCallParamflag, 'type');
    this.modifyOrAddEventServiceCallType = findObject('1', this.eventServiceCallType, 'type');
  }
  resetContent(): void {
    this.modifyOrAddEventServiceCallParamflag = findObject('1', this.eventServiceCallParamflag, 'type');
    this.modifyOrAddEventService.callService = null;
    this.modifyOrAddEventService.callMethod = null;
    this.modifyOrAddEventService.callInput = null;
    this.modifyOrAddEventService.callOutput = null;
    this.modifyOrAddEventService.targetId = null;
    this.modifyOrAddEventService.channel = null;
    this.modifyOrAddEventService.terminalCode = null;

  }

  // 重置修改和新增
  resetEventService(): void {
    this.isModifyEventServiceDisplay && this.resetModifyEventService();
    this.isAddEventServiceDisplay && this.resetAddEventService();
  }



  // 删除事件服务按钮
  deleteEventService(item, table): void {
    this.confirmationService.confirm({
      message: '确认删除吗？',
      header: '信息提示框',
      icon: 'fa fa-question-circle',
      accept: () => {
        const bodyParams = {
          id: item.id
        };
        this.eventServiceService.deleteEvtService(bodyParams)
          .subscribe(
          data => {
            handleResult(data, this.messageService, () => {
              this.query(table)
            }, true, { message: '删除成功！', errorMsg: '删除失败' });
          },
          err => {
            handleError(err, this.messageService, () => {
            });
          }
          );
      }
    });
  }

  //
  selceteEventService(event): void {
    this.outValue.emit(event);
  }



}
