import { Component, OnChanges, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { DictionaryTypeModel, ProsTypeModel, BUSINESS_SIGN_APPROVE_PRO_TYPE, RuleModel, MessageModel, RadioConfig } from '../../process-definition-model';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { BusinessNodeManagementService } from '../business-node-management.service';
import { handleResult, handleError, findObject, showMessage } from '../../../common/func/common-func';
import { BUS_NODE_TYPES, ACCESSORY_PROP_TYPE, TASK_DISTRIBUTION_TYPE, TASK_TIME_OUT_HANDLE_TYPE, TIMEOUT_WARNING_TYPE, TASK_NOTIFY_TYPE, SIGN_TASK_DISTRIBUTION_TYPE, SIGN_NODE_TYPES, PROCESS_MODE, RECIPIENT_RETURN, TIME_UNIT, CRTL_TYPE } from '../../../common/constant/code.value';

@Component({
  selector: 'app-add-props-extend-business',
  templateUrl: './add-props-extend-business.component.html',
  styleUrls: ['./add-props-extend-business.component.scss']
})
export class AddPropsExtendBusinessComponent implements OnChanges, OnDestroy {
  @Input() inValue: any;
  // 模型类型
  @Input() modelType: any;
  // 是否重置
  @Input() isRest: number = 0;
  // 父dialog  解决dialog嵌套
  @Input() dialogTemplate: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();

  msgs = [];

  // 业务节点、审批节点的属性类型
  prosExtendBusinessTypes = BUS_NODE_TYPES;
  // 会签节点的属性类型
  signProsExtendBusinessTypes = SIGN_NODE_TYPES;
  // 选中的扩展属性类型
  selectedProsExtendType = new ProsTypeModel();
  // 表单
  businessApproveSignForm: FormGroup;

  dialogParam = {
    display: false,
    headerTitle: ''
  };

  // 选人规则属性
  userSelected = new RuleModel();

  // 功能清单-功能URL地址
  funUrlAddress: string;
  // 功能清单映射值
  funListMapValue = new RuleModel();
  // 任务处理时间
  taskHandleTime: number;
  // 任务处理时间单位
  timeUnitConfig = new RadioConfig();
  TIME_UNIT = TIME_UNIT;
  dataType: string;
  // 附件属性数组
  accessoryPropArray = ACCESSORY_PROP_TYPE;
  // 选中的附件属性
  selectedAccessoryProp = new DictionaryTypeModel();
  // 会签节点任务分配机制数组
  taskDistributionType = SIGN_TASK_DISTRIBUTION_TYPE;
  // 非会签任务分配机制数组
  noSignTaskDistributionType = TASK_DISTRIBUTION_TYPE;
  // 会签节点选中的任务分配机制
  selectedTaskDistributionp = new DictionaryTypeModel();
  // 业务节点和审批节点选中的任务分配机制
  selectedTaskDistributionp2 = new DictionaryTypeModel();
  // 任务超时处理机制
  taskOutTimeHandle = TASK_TIME_OUT_HANDLE_TYPE;
  // 选中的任务超时处理机制
  selectedTaskOutTimeHandle = new DictionaryTypeModel();
  // 任务回收人员映射
  taskRecoveryPerson = new RuleModel();
  // 霸屏时间间隔
  screenSpace: Number;
  // 时间间隔单位
  spaceUnitConfig = new RadioConfig();
  spaceUnit: string;
  // 处理机制规则映射
  processRule = new RuleModel();
  // 换人策略
  changPersonMapValue = new RuleModel();
  // 显示规则树还是事件服务
  showRuleTreeOEventService = {
    isEventService: false,
    isRuleTree: false
  };
  // 选中事件服务映射数组
  selectedEventServiceMap: any[];
  // 触发调用服务值
  endEmitService: string;
  // 超时预警机制
  timeOutWarningType = TIMEOUT_WARNING_TYPE;
  // 选中的超时预警
  selectedTimeOutWarning = new DictionaryTypeModel();
  // 超时预警时间
  timeOutWarningTime: number;
  //  选中的内容模板模板
  contentTemplate = new MessageModel();
  // 控制预警模板模态框显隐
  isMessage: boolean;
  //  任务通知机制
  taskNotifyTypes = TASK_NOTIFY_TYPE;
  // 选中的任务通知机制
  selectedTaskNotify = new DictionaryTypeModel();
  // 任务通知值
  notifyTemplate = new MessageModel();
  // 控制任务通知模态框
  isNotify: boolean;
  // 传给事件调用看是否清空调用次序、、
  isShow: boolean;
  eventId: string; // 事件ID
  // 调用服务规则
  isRuleNum: number = 0;
  // 任务分配机制中如果是百分比分配是输入框
  percentages: string;
  // 控制脚本测试的模态框显隐
  verifyScriptDisplay = false;
  // 最大检出数
  checkOutNum: number;
  // 任务处理时间规则
  taskHandleTimeRule = new RuleModel();
  // 节点渠道列表
  channelNodeList: string;
  // 选中的处理方式
  selectedProcessMode = new DictionaryTypeModel();
  // 处理方式
  processMode = PROCESS_MODE;
  // 领用退回机制
  RecipientsReturn = new RadioConfig();
  checkInFlag: string;
  // 最大可领用数
  maxCheckOutNum: Number;
  RECIPIENT_RETURN = RECIPIENT_RETURN;
  // 节点查询渠道控制列表
  channelNodeQueList: string;
  // 节点任务追回机制
  crtlTypeConfig = new RadioConfig();
  crtlType: string;
  CRTL_TYPE = CRTL_TYPE;
  // 节点任务追回渠道
  ctrlChannelList: string;
  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private businessNodeManagementService: BusinessNodeManagementService,
  ) {
    this.createValidators();
    this.RecipientsReturn.name = 'checkInFlag';
    this.RecipientsReturn.options = this.RECIPIENT_RETURN;
    this.timeUnitConfig.name = 'dataType';
    this.timeUnitConfig.options = this.TIME_UNIT;
    this.spaceUnitConfig.name = 'spaceUnit';
    this.spaceUnitConfig.options = this.TIME_UNIT;
    this.crtlTypeConfig.name = 'crtlType';
    this.crtlTypeConfig.options = this.CRTL_TYPE;
  }

  ngOnChanges() {
    this.isRest && this.resetAddProsExtend();
  }

  createValidators() {
    this.businessApproveSignForm = this.fb.group({
      'addProsExtendTypeName': ['', Validators.required],
      'addUserRulelName': ['', Validators.required],
      'funURLName': [''],
      'funListMapName': [''],
      'taskHandleName': [''],
      'taskHandleRulelName': [''],
      'accessoryPropName': ['', Validators.required],
      'taskDistributionName': ['', Validators.required],
      'taskDistributePercentName': ['', Validators.required],
      'taskOutTimeName': ['', Validators.required],
      'changPersonMapName': ['', Validators.required],
      'endEmitServiceName': ['', Validators.required],
      'imeOutWarningTimeName': [''],
      'timeOutWarnName': [''],
      'warnContenlName': [''],
      'notifyName': [''],
      'notifyTemplateName': [''],
      'checkOutName': ['', Validators.required],
      'channelNodeName': ['', Validators.required],
      'processMode': ['', Validators.required],
      'checkInFlag': ['', Validators.required],
      'maxCheckOutNum': ['', Validators.required],
      'dataType': ['0'],
      'taskRecoveryPerson': ['', Validators.required],
      'spaceUnit': ['0', Validators.required],
      'screenSpace': ['', Validators.required],
      'processRule': ['', Validators.required],
      'channelNodeQueList': ['', Validators.required],
      'crtlType': ['1', Validators.required],
      'ctrlChannelList': ['', Validators.required],
      'eventId': ['', Validators.required]
    });
  }

  // 移除新增扩展属性类型的校验规则(扩展类型除外)
  removeAlAddValiditor() {
    this.businessApproveSignForm.removeControl('addUserRulelName');
    this.businessApproveSignForm.removeControl('taskHandleName');
    this.businessApproveSignForm.removeControl('taskHandleRulelName');
    this.businessApproveSignForm.removeControl('accessoryPropName');
    this.businessApproveSignForm.removeControl('taskDistributionName');
    this.businessApproveSignForm.removeControl('taskDistributePercentName');
    this.businessApproveSignForm.removeControl('taskOutTimeName');
    this.businessApproveSignForm.removeControl('changPersonMapName');
    this.businessApproveSignForm.removeControl('endEmitServiceName');
    this.businessApproveSignForm.removeControl('checkOutName');
    this.businessApproveSignForm.removeControl('channelNodeName');
    this.businessApproveSignForm.removeControl('processMode');
    this.businessApproveSignForm.removeControl('checkInFlag');
    this.businessApproveSignForm.removeControl('maxCheckOutNum');
    this.businessApproveSignForm.removeControl('dataType');
    this.businessApproveSignForm.removeControl('taskRecoveryPerson');
    this.businessApproveSignForm.removeControl('spaceUnit');
    this.businessApproveSignForm.removeControl('screenSpace');
    this.businessApproveSignForm.removeControl('processRule');
    this.businessApproveSignForm.removeControl('channelNodeQueList');
    this.businessApproveSignForm.removeControl('crtlType');
    this.businessApproveSignForm.removeControl('ctrlChannelList');
    this.businessApproveSignForm.removeControl('eventId');
  }

  // 属性类型选择
  changeAddProsExtendType(event): void {
    this.removeAlAddValiditor();
    this.resetContent();
    if (event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.USERRULE || event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.FEATURE_TASK_NAME_DISPLAY) {
      this.businessApproveSignForm.addControl('addUserRulelName', new FormControl('', Validators.required));
    } else if (event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.FUNCTIONLIST || event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.NODE_ENTRY_CONDITION || event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.NODE_EXIT_CONDITION) {

    } else if (event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TIME_START || event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.SELECT_OPTION) {
      this.businessApproveSignForm.addControl('taskHandleName', new FormControl('', Validators.required));
    } else if (event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_OPTION_TIME) {
      this.businessApproveSignForm.addControl('taskHandleName', new FormControl(''));
      this.businessApproveSignForm.addControl('taskHandleRulelName', new FormControl(''));
      this.businessApproveSignForm.addControl('dataType', new FormControl('0'));
    } else if (event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.ACCESSORY_PROP) {
      this.businessApproveSignForm.addControl('accessoryPropName', new FormControl('', Validators.required));
    } else if (event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_ALLOCATION_TYPE) {
      this.businessApproveSignForm.addControl('taskDistributionName', new FormControl('', Validators.required));

      if (this.selectedTaskDistributionp.type === '2' || this.selectedTaskDistributionp2.type === '2') {
        this.businessApproveSignForm.addControl('taskDistributePercentName', new FormControl('', Validators.required));
      } else {
        this.businessApproveSignForm.removeControl('taskDistributePercentName');
      }
    } else if (event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TIME_OUT_OPRATE) {
      this.businessApproveSignForm.addControl('taskOutTimeName', new FormControl('', Validators.required));
    } else if (event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.MAX_CHECK_OUT_NUM) {
      this.businessApproveSignForm.addControl('checkOutName', new FormControl('', Validators.required));
    } else if (event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.START_TASK_EVENT || event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.END_TASK_EVENT || event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.CANCEL_TASK_EVENT || event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.RECALL_TASK_EVENT || event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.BACK_TASK_EVENT) {
      this.businessApproveSignForm.addControl('endEmitServiceName', new FormControl('', Validators.required));
    } else if (event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_OPTION_TIME_RULE) {
      this.businessApproveSignForm.addControl('taskHandleRulelName', new FormControl('', Validators.required));
    } else if (event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.CHANNEL_NODE_CTRL_LIST) {
      this.businessApproveSignForm.addControl('channelNodeName', new FormControl('', Validators.required));
    } else if (event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.NODE_AUTO_REPEATP_ROCESS) {
      this.businessApproveSignForm.addControl('processMode', new FormControl('', Validators.required));
    } else if (event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.CHECK_IN_OUT_VALUE) {
      this.businessApproveSignForm.addControl('checkInFlag', new FormControl('3', Validators.required));
      this.businessApproveSignForm.addControl('maxCheckOutNum', new FormControl('', Validators.required));
    } else if (event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.CHANNEL_NODE_QUE_LIST) {
      this.businessApproveSignForm.addControl('channelNodeQueList', new FormControl('', Validators.required));
    } else if (event.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_RECALL_CRTL) {
      this.businessApproveSignForm.addControl('crtlType', new FormControl('1', Validators.required));
      this.businessApproveSignForm.addControl('ctrlChannelList', new FormControl('', Validators.required));
    }
  }


  // 任务分配机制选择
  changeTaksDistributionType(event): void {
    if (event.type === '2') {
      this.businessApproveSignForm.addControl('taskDistributePercentName', new FormControl('', Validators.required));
    } else {
      this.businessApproveSignForm.removeControl('taskDistributePercentName');
    }
  }


  // 超时处理机制中
  changeTimeOutHandleType(event): void {
    if (this.selectedTaskOutTimeHandle.type === '6') {
      this.businessApproveSignForm.addControl('changPersonMapName', new FormControl('', Validators.required));
      this.businessApproveSignForm.removeControl('endEmitServiceName');
      this.businessApproveSignForm.removeControl('taskRecoveryPerson');
      this.businessApproveSignForm.removeControl('spaceUnit');
      this.businessApproveSignForm.removeControl('screenSpace');
      this.businessApproveSignForm.removeControl('processRule');
    } else if (this.selectedTaskOutTimeHandle.type === '7') {
      this.businessApproveSignForm.addControl('endEmitServiceName', new FormControl('', Validators.required));
      this.businessApproveSignForm.removeControl('taskRecoveryPerson');
      this.businessApproveSignForm.removeControl('changPersonMapName');
      this.businessApproveSignForm.removeControl('spaceUnit');
      this.businessApproveSignForm.removeControl('screenSpace');
      this.businessApproveSignForm.removeControl('processRule');
    } else if (this.selectedTaskOutTimeHandle.type === '8') {
      this.businessApproveSignForm.addControl('taskRecoveryPerson', new FormControl('', Validators.required));
      this.businessApproveSignForm.removeControl('changPersonMapName');
      this.businessApproveSignForm.removeControl('endEmitServiceName');
      this.businessApproveSignForm.removeControl('spaceUnit');
      this.businessApproveSignForm.removeControl('screenSpace');
      this.businessApproveSignForm.removeControl('processRule');
    } else if (this.selectedTaskOutTimeHandle.type === '9') {
      this.businessApproveSignForm.addControl('spaceUnit', new FormControl('0', Validators.required));
      this.businessApproveSignForm.addControl('screenSpace', new FormControl('', Validators.required));
      this.businessApproveSignForm.removeControl('changPersonMapName');
      this.businessApproveSignForm.removeControl('endEmitServiceName');
      this.businessApproveSignForm.removeControl('taskRecoveryPerson');
      this.businessApproveSignForm.removeControl('processRule');
    } else if (this.selectedTaskOutTimeHandle.type === '10') {
      this.businessApproveSignForm.addControl('processRule', new FormControl('0', Validators.required));
      this.businessApproveSignForm.removeControl('changPersonMapName');
      this.businessApproveSignForm.removeControl('endEmitServiceName');
      this.businessApproveSignForm.removeControl('taskRecoveryPerson');
      this.businessApproveSignForm.removeControl('spaceUnit');
      this.businessApproveSignForm.removeControl('screenSpace');
    } else {
      this.businessApproveSignForm.removeControl('endEmitServiceName');
      this.businessApproveSignForm.removeControl('taskRecoveryPerson');
      this.businessApproveSignForm.removeControl('changPersonMapName');
      this.businessApproveSignForm.removeControl('spaceUnit');
      this.businessApproveSignForm.removeControl('screenSpace');
      this.businessApproveSignForm.removeControl('processRule');
    }
  }


  // 任务通知机制下拉框的改变
  changeNotifyFunc(event): void {
    this.notifyTemplate.name = null;
    if (event.type === 'eventTrigger') {
      this.businessApproveSignForm.addControl('eventId', new FormControl('', Validators.required));
    } else {
      this.businessApproveSignForm.removeControl('eventId');
    }
  }


  // 选择属性映射
  selectedPro(): void {
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '规则树及列表';
    this.showRuleTreeOEventService.isRuleTree = true;
    this.isRuleNum++;
    if (this.selectedProsExtendType.type === 'timeOutOprate') {
      if (this.selectedTaskOutTimeHandle.type === '8') {
        this.selectedProsExtendType.value = '1'
      } else {
        this.selectedProsExtendType.value = '14'
      }
    }
  }


  // 选择预警模板
  selectedMessage(): void {
    this.dialogParam.headerTitle = '预警消息模板列表';
    if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TIME_OUT_WARN) {
      this.isMessage = true;
      this.isNotify = false;
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.NOTIFY_TYPE) {
      this.isMessage = false;
      this.isNotify = true;
    }

  }

  // 验证脚本按钮
  verifyScript(verifyTestScript): void {
    verifyTestScript.resetListAndResult();
    this.verifyScriptDisplay = true;
  }

  // 测试脚本模态框的关闭按钮
  callBackVerifyScript(event): void {
    this.verifyScriptDisplay = !event;
  }


  // 从规则树传上来的值
  callBack(event): void {
    this.showRuleTreeOEventService.isRuleTree = !event;
    if (this.selectedProsExtendType) {
      if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.USERRULE || this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.FEATURE_TASK_NAME_DISPLAY) {
        this.userSelected = { ...event };
      } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.FUNCTIONLIST || this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.NODE_ENTRY_CONDITION || this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.NODE_EXIT_CONDITION) {
        this.funListMapValue = { ...event };
      } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TIME_OUT_OPRATE) {
        // 超时换人策略
        if (this.selectedTaskOutTimeHandle.type === '6') {
          this.changPersonMapValue = { ...event };
        } else if (this.selectedTaskOutTimeHandle.type === '8') {
          this.taskRecoveryPerson = { ...event };
        } else if (this.selectedTaskOutTimeHandle.type === '10') {
          this.processRule = { ...event };
        }
      } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_OPTION_TIME_RULE || BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_OPTION_TIME) {
        this.taskHandleTimeRule = { ...event };
      }
    }
  }

  // 从预警消息模板传上来的值
  callBackMessage(event): void {
    this.isMessage = !event;
    this.isNotify = !event;
    if (this.selectedProsExtendType) {
      if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TIME_OUT_WARN) {
        this.contentTemplate = { ...event };
      } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.NOTIFY_TYPE) {
        if (this.selectedTaskNotify.type === 'sms' || this.selectedTaskNotify.type === 'email') {
          this.notifyTemplate = { ...event };
        }
      }
    }

  }

  // 各个选项重置
  resetContent(): void {
    this.userSelected.label = null;
    this.funUrlAddress = null;
    this.funListMapValue.label = null;
    this.taskHandleTime = null;
    // 附件属性缺省为 无需提供附件
    this.selectedAccessoryProp = findObject('0', this.accessoryPropArray, 'type');
    this.selectedProcessMode = findObject('0', this.processMode, 'type');
    // 任务分配选择缺省为 多人竞争分配
    if (this.inValue) {
      if (this.inValue.type === 'sign') {
        this.selectedTaskDistributionp = findObject('0', this.taskDistributionType, 'type');
      } else {
        this.selectedTaskDistributionp2 = findObject('0', this.noSignTaskDistributionType, 'type')
      }
    }
    this.percentages = null;
    // this.selectedTaskDistributionp = findObject('0', this.taskDistributionType, 'type');
    // 任务超时处理 缺省为人工干预处理
    this.selectedTaskOutTimeHandle = findObject('0', this.taskOutTimeHandle, 'type');
    this.endEmitService = null;
    this.timeOutWarningTime = null;
    this.contentTemplate.name = null;
    // 超时预警机制缺省为 短信模板预警
    this.selectedTimeOutWarning = findObject('0', this.timeOutWarningType, 'type');
    // 任务通知机制缺省为 无需通知
    this.selectedTaskNotify = findObject('no', this.taskNotifyTypes, 'type');
    this.notifyTemplate.name = null;
    this.changPersonMapValue.label = null;
    // 任务处理时间规则
    this.taskHandleTimeRule.label = null;
    this.checkOutNum = null;
    this.channelNodeList = null;
    this.maxCheckOutNum = null;
    this.checkInFlag = '3';
    this.dataType = '0';
    this.taskRecoveryPerson.label = null;
    this.spaceUnit = '0';
    this.screenSpace = null;
    this.processRule.label = null;
    this.channelNodeQueList = '';
    this.crtlType = '1';
    this.ctrlChannelList = '';
    this.eventId = '';
  }

  // 重置新增规则
  resetAddProsExtend(): void {
    this.selectedProsExtendType = new ProsTypeModel();
    this.resetContent();
  }

  //  保存属性值
  saveProsExtend(): void {
    if (!this.selectedProsExtendType.type) {
      showMessage(this.messageService, 'error', '请选择扩展属性类型！');
      return;
    }
    let value = null;
    if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.USERRULE || this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.FEATURE_TASK_NAME_DISPLAY) {
      value = JSON.stringify(this.userSelected);
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.NODE_ENTRY_CONDITION || this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.NODE_EXIT_CONDITION) {
      if (!this.funUrlAddress && !this.funListMapValue.label) {
        showMessage(this.messageService, 'error', '条件或规则映射都无属性值，禁止保存');
        return;
      }
      let valueParams = {};
      this.funUrlAddress ? valueParams = {
        type: '3',
        value: this.funUrlAddress
      } : this.funListMapValue.label ? valueParams = {
        type: '2',
        value: JSON.stringify(this.funListMapValue)
      } : valueParams;
      value = JSON.stringify(valueParams);
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.FUNCTIONLIST) {
      if (!this.funUrlAddress && !this.funListMapValue.label) {
        showMessage(this.messageService, 'error', '条件或规则映射都无属性值，禁止保存');
        return;
      }
      let valueParams = {};
      this.funUrlAddress ? valueParams = {
        type: '1',
        value: this.funUrlAddress
      } : this.funListMapValue.label ? valueParams = {
        type: '2',
        value: JSON.stringify(this.funListMapValue)
      } : valueParams;
      value = JSON.stringify(valueParams);
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TIME_START || this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.SELECT_OPTION) {
      value = `${this.taskHandleTime}`;
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_OPTION_TIME) {
      // 任务处理时间和任务处理时间规则二选一必输
      if ( this.taskHandleTime !== null || this.taskHandleTimeRule.label) {
        const valueObj = {
          timeOut: {
            dataType: this.dataType,
            value: this.taskHandleTime
          },
          urule: this.taskHandleTimeRule
        }
        value = JSON.stringify(valueObj);
      } else {
        showMessage(this.messageService, 'error', `任务处理时间和任务处理时间规则二选一必输!`);
        return;
      }
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.ACCESSORY_PROP) {
      value = JSON.stringify(this.selectedAccessoryProp);
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.NODE_AUTO_REPEATP_ROCESS) {
      value = JSON.stringify(this.selectedProcessMode);
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_OPTION_TIME_RULE) {
      value = JSON.stringify(this.taskHandleTimeRule);
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.MAX_CHECK_OUT_NUM) {
      value = value = `${this.checkOutNum}`;
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_ALLOCATION_TYPE) {
      const percentage = { percentage: this.percentages };
      const obj = { ...this.selectedTaskDistributionp, ...percentage };
      const obj2 = { ...this.selectedTaskDistributionp2, ...percentage };
      value = this.inValue.type === 'sign' ? JSON.stringify(obj) : JSON.stringify(obj2);
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TIME_OUT_OPRATE) {
      Array.isArray(this.selectedEventServiceMap) && this.selectedEventServiceMap.forEach(item => {
        if (!item['callMode']) {
          item = Object.assign(item, { callMode: '1' });
        }
      })
      if (this.selectedTaskOutTimeHandle.type === '6') {
        if (!this.changPersonMapValue) {
          showMessage(this.messageService, 'error', `请选择任务回收人员映射规则！`);
          return;
        }
        const valueParams = {
          type: this.selectedTaskOutTimeHandle,
          value: this.changPersonMapValue
        }
        value = JSON.stringify(valueParams);
      } else if (this.selectedTaskOutTimeHandle.type === '7') {
        if (!this.endEmitService) {
          showMessage(this.messageService, 'error', `请选择超时触发事件！`);
          return;
        }
        const valueParams = {
          type: this.selectedTaskOutTimeHandle,
          event: this.selectedEventServiceMap
        }
        value = JSON.stringify(valueParams);
      } else if (this.selectedTaskOutTimeHandle.type === '8') {
        if (!this.taskRecoveryPerson) {
          showMessage(this.messageService, 'error', `请选择任务回收人员规则！`);
          return;
        }
        const valueParams = {
          type: this.selectedTaskOutTimeHandle,
          urule: this.taskRecoveryPerson
        }
        value = JSON.stringify(valueParams);
      } else if (this.selectedTaskOutTimeHandle.type === '9') {
        const valueParams = {
          type: this.selectedTaskOutTimeHandle,
          timeOut: {
            dataType: this.spaceUnit,
            value: this.screenSpace
          }
        }
        value = JSON.stringify(valueParams);
      } else if (this.selectedTaskOutTimeHandle.type === '10') {
        const valueParams = {
          type: this.selectedTaskOutTimeHandle,
          urule: this.processRule
        }
        value = JSON.stringify(valueParams);
      } else {
        value = JSON.stringify(this.selectedTaskOutTimeHandle);
      }
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.START_TASK_EVENT || this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.END_TASK_EVENT || this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.CANCEL_TASK_EVENT || this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.RECALL_TASK_EVENT || this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.BACK_TASK_EVENT) {
      Array.isArray(this.selectedEventServiceMap) && this.selectedEventServiceMap.forEach(item => {
        if (!item['callMode']) {
          item = Object.assign(item, { callMode: '1' });
        }
      })
      value = JSON.stringify(this.selectedEventServiceMap);
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TIME_OUT_WARN) {
      const obj = {
        time: this.timeOutWarningTime,
        template: this.contentTemplate
      }
      value = JSON.stringify(obj);
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.NOTIFY_TYPE) {
      if (this.selectedTaskNotify.type === 'no') {
        value = JSON.stringify(this.selectedTaskNotify);
      } else if (this.selectedTaskNotify.type === 'sms' || this.selectedTaskNotify.type === 'email') {
        value = JSON.stringify(this.notifyTemplate);
      } else if (this.selectedTaskNotify.type === 'eventTrigger') {
        value = JSON.stringify(Object.assign(this.selectedTaskNotify, { eventId: this.eventId }));
      }
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.CHANNEL_NODE_CTRL_LIST) {
      value = this.channelNodeList;
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.CHECK_IN_OUT_VALUE) {
      const obj = {
        checkInFlag: this.checkInFlag,
        maxCheckOutNum: this.maxCheckOutNum
      };
      value = JSON.stringify(obj);
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.CHANNEL_NODE_QUE_LIST) {
      value = this.channelNodeQueList.replace(/，/g,",");
    } else if (this.selectedProsExtendType.type === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_RECALL_CRTL) {
      const obj = {
        crtlType: this.crtlType,
        ctrlChannelList: this.ctrlChannelList.replace(/，/g,",")
      }
      value = JSON.stringify(obj);
    }
    const bodyParams = {
      ownId: this.inValue.nodeId || '',
      ownType: this.modelType,
      propName: this.selectedProsExtendType.type || '',
      propDesc: this.selectedProsExtendType.descp || '',
      propValue: value
    };
    this.businessNodeManagementService.addNodePropExtends(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.outValue.emit(event);
          }, true, { message: ' 保存成功', errorMsg: '保存失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
            this.outValue.emit(event);
          });
        }
      );
    this.showRuleTreeOEventService.isEventService = false;
    this.showRuleTreeOEventService.isRuleTree = false;
  }

  // 事件服务调用选择
  selectedPropsMap(eventService): void {
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '事件服务';
    this.showRuleTreeOEventService = {
      isRuleTree: false,
      isEventService: true
    }
    eventService.reset();
    eventService.resetSelected();
    eventService.getEventServiceList();
  }


  // 选择事件服务
  selectedEventService(item): void {
    if (!item.selectedValues || (item.selectedValues && item.selectedValues < 1)) {
      showMessage(this.messageService, 'error', '请选择调用服务！');
      return;
    }
    let hash = {};
    this.selectedEventServiceMap = item.selectedValues.reduce((item, next) => {
      hash[next.id] ? '' : hash[next.id] = true && item.push(next);
      return item;
    }, [])
    const noSetService = this.selectedEventServiceMap.filter(item => {
      return !item.order;
    });
    if (Array.isArray(noSetService) && noSetService.length) {
      showMessage(this.messageService, 'error', '有服务调用次序、方式、条件未配置！');
      return;
    }
    const nameArray = [];
    this.selectedEventServiceMap.forEach(val => {
      nameArray.push(val.name);
      // if (!val.order) {
      //   noOrderArray.push(val.name);
      // }
    })
    // if (noOrderArray.length > 0) {
    //   this.messageService.add({ severity: 'error', summary: '信息提示框', detail: `[${noOrderArray}]无调用次序，请点击修改按钮进行添加次序！` });
    //   return;
    // }
    this.endEmitService = nameArray.join('、');
    this.dialogParam.display = false;
    // 这里为了不清空order等
    this.showRuleTreeOEventService.isEventService = true;
  }

  ngOnDestroy() {
    this.resetAddProsExtend();
  }
}

