import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { ProcessDefinitionComponent } from './process-definition.component';
import { ProcessMaintenanceComponent } from './process-maintenance/process-maintenance.component';
import { MessageTemplateComponent } from './message-template/message-template.component';
import { BusinessNodeManagementComponent } from './business-node-management/business-node-management.component';
import { EventServiceComponent } from './event-service/event-service.component';
import { TaskToAuthorizationComponent } from './task-to-authorization/task-to-authorization.component';
import { GeneralParametersComponent } from './general-parameters/general-parameters.component';
import { ExceptionStrategyComponent } from './exception-strategy/exception-strategy.component';
import { BusinessStatusComponent } from './business-status/business-status.component';
import { TaskToAuthorizationManageComponent } from './task-to-authorization-manage/task-to-authorization-manage.component';

export const routes: Routes = [
  {
    path: '',
    component: ProcessDefinitionComponent,
    children: [
      { path: '', redirectTo: 'process-maintenance', pathMatch: 'full' },
      { path: 'process-maintenance', component: ProcessMaintenanceComponent },
      { path: 'business-node-management', component: BusinessNodeManagementComponent },
      { path: 'business-status', component: BusinessStatusComponent },
      { path: 'message-template', component: MessageTemplateComponent },
      { path: 'event-service', component: EventServiceComponent },
      { path: 'task-to-authorization', component: TaskToAuthorizationComponent }, // 员工授权
      { path: 'task-to-authorization-manage', component: TaskToAuthorizationManageComponent },   // 管理授权
      { path: 'general-parameters', component: GeneralParametersComponent },
      { path: 'exception-strategy', component: ExceptionStrategyComponent },
    ]
  }
];

export const ProcessDefinitionRouting: ModuleWithProviders = RouterModule.forChild(routes);
