import { Component, OnInit, Input, Output, EventEmitter, OnChanges, ViewChild } from '@angular/core';
import { MessageModel } from '../../../process-definition-model';
import { MessageTemplateService } from '../../../message-template/message-template.service';
import { MessageService } from 'primeng/components/common/messageservice';
import { ConfirmationService, DataTable } from 'primeng/primeng';
import { handleResult, handleError, formatDate } from '../../../../common/func/common-func';
import { paginatorUtils, HORIZONTAL_LINE_SHORT_DATE_FORMAT, CALENDAR_CN } from '../../../../common/constant/config';
import { EngTypeModel } from '../../../../process-instance/process-instance-model';
import { MESSAGE_TEMPLATE_TYPE } from '../../../../common/constant/code.value';

@Component({
  selector: 'app-message-warning',
  templateUrl: './message-warning.component.html',
  styleUrls: ['./message-warning.component.scss']
})
export class MessageWarningComponent implements OnChanges {

  // 模板类型
  @Input() inValue: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();

  msg = [];
  // 消息模板列表
  messageTemplateList: MessageModel[] = [];
  // 分页实例
  paginatorInstance: MessageModel = new MessageModel();
  // 查询条件中的消息实例
  queryMessageTemplate: MessageModel = new MessageModel();
  // 表格中选中的单行消息实例
  selectedMessageTemplate: MessageModel = new MessageModel();
  // 消息类型
  messageTypes = MESSAGE_TEMPLATE_TYPE;
  // 选中的消息类型
  queryMessageType: EngTypeModel = new EngTypeModel();
  // 消息模板数据总量
  total: string;

  CALENDAR_CN = CALENDAR_CN;
  // 查询创建时间
  queryCreateTime: Date;

  @ViewChild('dt') dt: DataTable;


  constructor(
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private messageTemplateService: MessageTemplateService,
  ) { }

  ngOnChanges() {
    this.queryTemplate(this.dt);
  }



  // 获取列表
  getMessageTemplateList(type?: string, templateId?: string, name?: string, customCreateTime?: any): void {
    const createTime = customCreateTime == null ? null : formatDate(customCreateTime, HORIZONTAL_LINE_SHORT_DATE_FORMAT);
    const bodyParams = {
      templateId,
      name,
      type,
      createTime,
      pageRows: parseInt(this.paginatorInstance.rowNum),
      curPage: parseInt(this.paginatorInstance.startRow)
    };
    this.messageTemplateService.searchMsgTemList(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.messageTemplateList = data.pageData[0].result;
          this.total = data.pageData[0].totalRows;
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {

        });
      }
      );
  }

  queryFirst(table) {
    table.first = 0;
    this.queryTemplate(table);
  }


  // 按照搜索条件查询
  queryTemplate(table): void {
    table.first = table.first || 0;
    table.rows = table.rows || parseInt(paginatorUtils.rowNum, 10);
    // 每页显示的条数
    this.paginatorInstance.rowNum = table.rows + '';
    // 当前页
    this.paginatorInstance.startRow = (table.first / table.rows) + 1 + '';
    this.getMessageTemplateList(this.inValue || this.queryMessageType.type, this.queryMessageTemplate.name, this.queryMessageTemplate.templateId, this.queryCreateTime);
  }

  // 重置查询
  reset(): void {
    this.queryMessageTemplate.templateId = null;
    this.queryMessageTemplate.name = null;
    this.queryMessageType = new EngTypeModel();
    this.queryCreateTime = null;
  }

  selcetedData(event): void {
    this.outValue.emit(event);
  }

}
