import { Component, OnChanges, Input, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { PROCESS_MODEL_EXTEND_PROPS, TIMEOUT_HANDING_TYPES, TIME_TO_START_TYPE, WEEKS, CHECK_IN_OUT_VALUE, PROCESS_MODE } from '../../../common/constant/code.value';
import { ProsTypeModel, FLOW_MODEL_PRO_TYPE, DictionaryTypeModel, RuleModel, TimeToStartModel } from '../../process-definition-model';
import { BusinessNodeManagementService } from '../../business-node-management/business-node-management.service';
import { handleResult, handleError, findObject, isJsonString, showMessage } from '../../../common/func/common-func';
import { ConfirmationService } from 'primeng/primeng';

@Component({
  selector: 'app-update-process-props-extend',
  templateUrl: './update-process-props-extend.component.html',
  styleUrls: ['./update-process-props-extend.component.scss']
})
export class UpdateProcessPropsExtendComponent implements OnChanges {
  msg = [];
  // 接收的传入的流程
  @Input() isFlowModel: any;
  //接收的传入的属性
  @Input() inValue: any;
  // 模型类型
  @Input() modelType: any;
  // 父dialog  解决dialog嵌套
  @Input() dialogTemplate: any;

  @Input() isRest: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  // 修改的属性类型
  updatePropsType: string;
  // 流程建模扩展属性类型
  flowProsExtendTypes = PROCESS_MODEL_EXTEND_PROPS;
  // 选中的扩展属性类型
  selectedProsExtendType: ProsTypeModel = new ProsTypeModel();
  // 修改表单
  updateProcessPropsForm: FormGroup;
  // 结束触发调用服务值
  endEmitService: string;

  dialogParam = {
    display: false,
    headerTitle: ''
  };
  // 选中事件服务映射数组
  selectedEventServiceMap: any[];
  // 重置时的临时变量
  defaultFlowModelArray: any[];
  // 首节点是否跳过
  isSkipFirstNode: boolean;
  // 重置时的临时变量
  defaultIsSkipFirstNode: boolean;
  // 定时启动时长
  startTime: number;
  // 重置时的临时变量
  defaultStartTime: number;
  // 流程最长处理时间
  maxProcessHandleTime: number;
  // 重置时的临时变量
  defaultMaxProcessHandleTime: number;
  // 流程超时处理机制类型
  timeoutHandlingType = TIMEOUT_HANDING_TYPES;
  // 选中的流程超时处理机制
  selectedtTimeoutHandlingType = new DictionaryTypeModel();
  // 重置时的临时变量
  deafultSelectedtTimeoutHandlingType = new DictionaryTypeModel();
  // 显示规则树还是事件服务
  showRuleTreeOEventService = {
    isEventService: false,
    isRuleTree: false
  };
  // 启动条件/结束条件
  startConditonString: string;
  // 重置时的临时变量
  defaultStartConditonString: string;
  // 启动条件/结束条件映射值
  startConditonMapValue = new RuleModel();
  // 重置时的临时变量
  defaultStartConditonMapValue = new RuleModel();
  // 检入检出值
  checkInOuts = CHECK_IN_OUT_VALUE;
  // 选中的检入检出
  selectedCheckInOut = new DictionaryTypeModel();
  // 重置时的临时变量
  defaultCheckInOut = new DictionaryTypeModel();
  // 修改标志
  isAssignmentFlag = true;

  // resetFlag: number = 0;
  // 服务调用
  isRuleNum: number = 0;

  // 定时启动列表
  timeToStart: TimeToStartModel[] = [];
  // 
  isRemoveData: TimeToStartModel = new TimeToStartModel();
  //重置时的默认定时启动列表
  defaultTimeToStart: TimeToStartModel[] = [];
  // 定时时间模态框显隐
  timeDisplay = false;
  // 定时类型
  timeToStartType = TIME_TO_START_TYPE;
  // 修改定时启动
  isModifyNum: number = 0;
  // 星期
  weeks = WEEKS;
  // 控制脚本测试的模态框显隐
  verifyScriptDisplay = false;
  //选中的最长处理时间规则
  selectedMaxTimeRule = new RuleModel();
  // 重置时最长处理时间的临时变量
  defaultMaxTimeRule = new RuleModel();
  // 流程渠道控制列表
  channelFlowList: string;
  // 流程渠道控制列表重置时的临时变量
  defaultChannelFlowList: string;
  // 选中的处理方式值
  selectedprocessMode = new DictionaryTypeModel();
  // 选中方式下拉框值
  processMode = PROCESS_MODE;
  // 重置时的临时变量
  defaultProcessMode = new DictionaryTypeModel();
  // 是否转授权
  isFlowAuthorize = {
    isflag: false,
    defaultIsflag: false,
    ids: '',
    defaultIds: ''
  };
  // 动态提醒->事件ID
  eventId: string;
  // 动态提醒->事件ID重置时的临时变量
  defaultEventId: string;
  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private businessNodeManagementService: BusinessNodeManagementService,
  ) {
    this.createValidators();
  }

  ngOnChanges() {
    this.setPropsAndValue();
    // this.resetFlag = this.isRest;
  }

  createValidators() {
    this.updateProcessPropsForm = this.fb.group({
      'updateProsExtendTypeName': ['', Validators.required],
      'endEmitServiceName': ['', Validators.required],
      'skipFistNodeName': [''],
      // 'timeToStartName': ['', Validators.required],
      'maxTimeRulelName': [''],
      'maxTimeName': [''],
      'outTimeName': ['', Validators.required],
      'startConName': [''],
      'startConMapName': ['', Validators.required],
      'checkOutName': ['', Validators.required],
      'channelFlowName': ['', Validators.required],
      'processMode': ['', Validators.required],
      'isflagName': ['', Validators.required],
      'eventIds': [''],
      'eventId': ['']
    });
  }


  // 选择规则属性映射
  selectedProRule(): void {
    this.dialogParam.headerTitle = '规则树及列表';
    this.dialogParam.display = true;
    this.showRuleTreeOEventService = {
      isRuleTree: true,
      isEventService: false
    };
    this.isRuleNum++;
  }


  // 事件服务调用选择
  selectedPropsMap(eventService): void {
    this.dialogParam.display = true;
    this.dialogParam.headerTitle = '事件服务';
    this.showRuleTreeOEventService = {
      isEventService: true,
      isRuleTree: false
    };
    eventService.reset();
    eventService.resetSelected();
    eventService.getEventServiceList();

  }


  // 从规则树传上来的值
  callBack(event): void {
    this.dialogParam.display = !event;
    if (this.selectedProsExtendType) {
      if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.START_CONDITION || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.END_CONDITION) {
        this.startConditonMapValue = { ...event };
      } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.MAX_TIME) {
        this.selectedMaxTimeRule = { ...event };
      }
    }
  }

  // 验证脚本按钮
  verifyScript(verifyTestScript): void {
    verifyTestScript.resetListAndResult();
    this.verifyScriptDisplay = true;
  }



  // 选择事件服务
  selectedEventService(item): void {
    if (!item.selectedValues || (item.selectedValues && item.selectedValues < 1)) {
      showMessage(this.messageService, 'error', `请选择调用服务！`);
      return;
    }
    let hash = {};
    this.selectedEventServiceMap = item.selectedValues.reduce((item, next) => {
      hash[next.id] ? '' : hash[next.id] = true && item.push(next);
      return item;
    }, [])
    // this.selectedEventServiceMap = Array.from(item.selectedValues);
    const noSetService = this.selectedEventServiceMap.filter(item => {
      return !item.order;
    });
    if (Array.isArray(noSetService) && noSetService.length) {
      showMessage(this.messageService, 'error', '有服务调用次序、方式、条件未配置！');
      return;
    }
    const nameArray = [];
    this.selectedEventServiceMap.forEach(val => {
      nameArray.push(val.name);
    })
    this.endEmitService = nameArray.join('、');
    this.dialogParam.display = false;
  }



  // 重置修改的属性
  resetUpdateProsExtend(): void {
    if (Array.isArray(this.defaultFlowModelArray) && this.defaultFlowModelArray.length) {
      this.selectedEventServiceMap = Array.from(this.defaultFlowModelArray);
      const nameArr = [];
      this.selectedEventServiceMap.forEach(val => {
        nameArr.push(val.name);
      })
      this.endEmitService = nameArr.join('、');
    }
    this.isSkipFirstNode = this.defaultIsSkipFirstNode;
    this.startTime = this.defaultStartTime;
    this.maxProcessHandleTime = this.defaultMaxProcessHandleTime;
    this.selectedMaxTimeRule = { ...this.defaultMaxTimeRule };
    this.selectedtTimeoutHandlingType = this.deafultSelectedtTimeoutHandlingType;
    this.startConditonString = this.defaultStartConditonString;
    this.startConditonMapValue = { ...this.defaultStartConditonMapValue };
    //定时启动赋值
    this.timeToStart = [...this.defaultTimeToStart];
    this.selectedCheckInOut = { ...this.defaultCheckInOut };
    this.channelFlowList = this.defaultChannelFlowList;
    this.selectedprocessMode = { ...this.defaultProcessMode };
    this.isFlowAuthorize.isflag = this.isFlowAuthorize.defaultIsflag;
    this.isFlowAuthorize.ids = this.isFlowAuthorize.defaultIds;
    this.eventId = this.defaultEventId;
  }

  // 更新
  updateProsExtend(): void {
    if (!this.selectedProsExtendType.type) {
      showMessage(this.messageService, 'error', `请选择扩展属性类型！`);
      return;
    }
    let value = null;
    if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.FLOW_START_EVENT || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.FLOW_END_EVENT || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.FLOW_END_ERROR_EVENT || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.HANG_UP_EVENT || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.RECOVER_EVENT || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.CANCEL_EVENT) {
      Array.isArray(this.selectedEventServiceMap) && this.selectedEventServiceMap.forEach(item => {
        if (!item['callMode']) {
          item = Object.assign(item, { callMode: '1' });
        }
      })
      value = JSON.stringify(this.selectedEventServiceMap);
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.SKIP_FIRST_NODE || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.CHECK_PERSON || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.CONFIR_INFO || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.PRESON_PRO || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.CALL_BACK_JUMP) {
      value = `${this.isSkipFirstNode}`;
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.TIME_TO_START) {
      if (!this.timeToStart || !this.timeToStart.length) {
        showMessage(this.messageService, 'error', '未定义定时启动时间！');
        return;
      }
      value = JSON.stringify(this.timeToStart);
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.CHECKINOUT) {
      value = JSON.stringify(this.selectedCheckInOut);
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.FLOW_AUTO_REPEAT_PROCESS) {
      value = JSON.stringify(this.selectedprocessMode);
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.MAX_TIME) {
      if (!this.maxProcessHandleTime && !this.selectedMaxTimeRule.label) {
        showMessage(this.messageService, 'error', '最长处理时间和最长时间规则必选其一！');
        return;
      }
      let valueParams = {};
      this.maxProcessHandleTime ? valueParams = {
        type: '3',
        value: this.maxProcessHandleTime
      } : this.selectedMaxTimeRule.label ? valueParams = {
        type: '2',
        value: JSON.stringify(this.selectedMaxTimeRule)
      } : valueParams;
      value = JSON.stringify(valueParams);

    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.OUT_TIME_EXECUTION) {
      value = JSON.stringify(this.selectedtTimeoutHandlingType);
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.START_CONDITION || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.END_CONDITION) {
      let valueParams = {};
      this.startConditonString ? valueParams = {
        type: '3',
        value: this.startConditonString
      } : this.startConditonMapValue.label ? valueParams = {
        type: '2',
        value: JSON.stringify(this.startConditonMapValue)
      } : valueParams;
      value = JSON.stringify(valueParams);
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.CHANNEL_FLOW_CTRL_LIST) {
      value = this.channelFlowList;
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.IS_FLOW_AUTHORIZE) {
      const obj = {
        isflag: this.isFlowAuthorize.isflag,
        eventId: this.isFlowAuthorize.ids
      };
      value = JSON.stringify(obj);
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.NOTIFY_FOR_PROCESS_CREATER) 
{
      value = JSON.stringify({type: this.selectedProsExtendType.type, descp: this.selectedProsExtendType.descp, eventId: this.eventId});
    }
    const bodyParams = {
      ownId: this.isFlowModel.defId,
      ownType: this.modelType,
      propName: this.selectedProsExtendType.type,
      propDesc: this.selectedProsExtendType.descp,
      propValue: value,
    }
    this.businessNodeManagementService.updateNodePropExtends(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.outValue.emit(event);
          }, true, { message: ' 保存成功', errorMsg: '保存失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
            this.outValue.emit(event);
          });
        }
      );
  }


  // 给属性类型和属性赋值
  setPropsAndValue(): void {
    if (this.isAssignmentFlag) {
      if (this.inValue) {
        this.selectedProsExtendType = findObject(this.inValue.propName, this.flowProsExtendTypes, 'type');
        this.updatePropsType = this.selectedProsExtendType.descp;
        if (this.inValue.propName === FLOW_MODEL_PRO_TYPE.FLOW_START_EVENT || this.inValue.propName === FLOW_MODEL_PRO_TYPE.FLOW_END_EVENT || this.inValue.propName === FLOW_MODEL_PRO_TYPE.FLOW_END_ERROR_EVENT || this.inValue.propName === FLOW_MODEL_PRO_TYPE.HANG_UP_EVENT || this.inValue.propName === FLOW_MODEL_PRO_TYPE.RECOVER_EVENT || this.inValue.propName === FLOW_MODEL_PRO_TYPE.CANCEL_EVENT) {
          this.updateProcessPropsForm.addControl('endEmitServiceName', new FormControl('', Validators.required));
          this.updateProcessPropsForm.removeControl('outTimeName');
          this.updateProcessPropsForm.removeControl('startConName');
          this.updateProcessPropsForm.removeControl('startConMapName');
          this.updateProcessPropsForm.removeControl('checkOutName');
          this.updateProcessPropsForm.removeControl('processMode');
          this.updateProcessPropsForm.removeControl('channelFlowName');
          this.updateProcessPropsForm.removeControl('isflagName');
          this.updateProcessPropsForm.removeControl('eventIds');
          this.updateProcessPropsForm.removeControl('eventId');
          const value = JSON.parse(this.inValue.propValue);
          this.defaultFlowModelArray = Array.from(value);
          const nameArr = [];
          value.forEach(val => {
            nameArr.push(val.name);
          })
          this.endEmitService = nameArr.join('、');
        } else if (this.inValue.propName === FLOW_MODEL_PRO_TYPE.SKIP_FIRST_NODE || this.inValue.propName === FLOW_MODEL_PRO_TYPE.CHECK_PERSON || this.inValue.propName === FLOW_MODEL_PRO_TYPE.CONFIR_INFO || this.inValue.propName === FLOW_MODEL_PRO_TYPE.PRESON_PRO || this.inValue.propName === FLOW_MODEL_PRO_TYPE.CALL_BACK_JUMP) {
          this.updateProcessPropsForm.removeControl('timeToStartName');
          this.updateProcessPropsForm.removeControl('endEmitServiceName');
          this.updateProcessPropsForm.removeControl('outTimeName');
          this.updateProcessPropsForm.removeControl('startConName');
          this.updateProcessPropsForm.removeControl('startConMapName');
          this.updateProcessPropsForm.removeControl('checkOutName');
          this.updateProcessPropsForm.removeControl('channelFlowName');
          this.updateProcessPropsForm.removeControl('processMode');
          this.updateProcessPropsForm.removeControl('isflagName');
          this.updateProcessPropsForm.removeControl('eventIds');
          this.updateProcessPropsForm.removeControl('eventId');
          this.isSkipFirstNode = this.inValue.propValue === "false" ? false : true;
          this.defaultIsSkipFirstNode = this.inValue.propValue === "false" ? false : true;
        } else if (this.inValue.propName === FLOW_MODEL_PRO_TYPE.TIME_TO_START) {
          this.updateProcessPropsForm.removeControl('endEmitServiceName');
          this.updateProcessPropsForm.removeControl('outTimeName');
          this.updateProcessPropsForm.removeControl('startConName');
          this.updateProcessPropsForm.removeControl('startConMapName');
          this.updateProcessPropsForm.removeControl('checkOutName');
          this.updateProcessPropsForm.removeControl('channelFlowName');
          this.updateProcessPropsForm.removeControl('processMode');
          this.updateProcessPropsForm.removeControl('isflagName');
          this.updateProcessPropsForm.removeControl('eventIds');
          this.updateProcessPropsForm.removeControl('eventId');
          this.timeToStart = JSON.parse(this.inValue.propValue);
          this.defaultTimeToStart = JSON.parse(this.inValue.propValue);
        } else if (this.inValue.propName === FLOW_MODEL_PRO_TYPE.CHECKINOUT) {
          this.updateProcessPropsForm.removeControl('endEmitServiceName');
          this.updateProcessPropsForm.removeControl('outTimeName');
          this.updateProcessPropsForm.removeControl('startConName');
          this.updateProcessPropsForm.removeControl('startConMapName');
          this.updateProcessPropsForm.removeControl('processMode');
          this.updateProcessPropsForm.removeControl('channelFlowName');
          this.updateProcessPropsForm.removeControl('isflagName');
          this.updateProcessPropsForm.removeControl('eventIds');
          this.updateProcessPropsForm.removeControl('eventId');
          this.updateProcessPropsForm.addControl('checkOutName', new FormControl('', Validators.required));
          this.selectedCheckInOut = JSON.parse(this.inValue.propValue);
          this.defaultCheckInOut = JSON.parse(this.inValue.propValue);
        } else if (this.inValue.propName === FLOW_MODEL_PRO_TYPE.MAX_TIME) {
          this.updateProcessPropsForm.removeControl('endEmitServiceName');
          this.updateProcessPropsForm.removeControl('outTimeName');
          this.updateProcessPropsForm.removeControl('startConName');
          this.updateProcessPropsForm.removeControl('startConMapName');
          this.updateProcessPropsForm.removeControl('checkOutName');
          this.updateProcessPropsForm.removeControl('channelFlowName');
          this.updateProcessPropsForm.removeControl('processMode');
          this.updateProcessPropsForm.removeControl('isflagName');
          this.updateProcessPropsForm.removeControl('eventIds');
          this.updateProcessPropsForm.removeControl('eventId');
          const value = JSON.parse(this.inValue.propValue);
          if (isJsonString(value.value)) {
            const jsonVal = JSON.parse(value.value);
            this.selectedMaxTimeRule = { ...jsonVal };
            this.defaultMaxTimeRule = { ...jsonVal };
          } else {
            this.maxProcessHandleTime = value.value;
            this.defaultMaxProcessHandleTime = value.value;
          }
        } else if (this.inValue.propName === FLOW_MODEL_PRO_TYPE.OUT_TIME_EXECUTION) {
          this.updateProcessPropsForm.addControl('outTimeName', new FormControl('', Validators.required));
          this.updateProcessPropsForm.removeControl('endEmitServiceName');
          this.updateProcessPropsForm.removeControl('startConName');
          this.updateProcessPropsForm.removeControl('startConMapName');
          this.updateProcessPropsForm.removeControl('checkOutName');
          this.updateProcessPropsForm.removeControl('channelFlowName');
          this.updateProcessPropsForm.removeControl('processMode');
          this.updateProcessPropsForm.removeControl('isflagName');
          this.updateProcessPropsForm.removeControl('eventIds');
          this.updateProcessPropsForm.removeControl('eventId');
          const value = JSON.parse(this.inValue.propValue);
          this.selectedtTimeoutHandlingType = findObject(value.type, this.timeoutHandlingType, 'type');
          this.deafultSelectedtTimeoutHandlingType = findObject(value.type, this.timeoutHandlingType, 'type');
        } else if (this.inValue.propName === FLOW_MODEL_PRO_TYPE.START_CONDITION || this.inValue.propName === FLOW_MODEL_PRO_TYPE.END_CONDITION) {
          const value = JSON.parse(this.inValue.propValue);
          this.updateProcessPropsForm.addControl('startConName', new FormControl(''));
          this.updateProcessPropsForm.addControl('startConMapName', new FormControl('', Validators.required));
          this.updateProcessPropsForm.removeControl('endEmitServiceName');
          this.updateProcessPropsForm.removeControl('maxTimeName');
          this.updateProcessPropsForm.removeControl('outTimeName');
          this.updateProcessPropsForm.removeControl('checkOutName');
          this.updateProcessPropsForm.removeControl('channelFlowName');
          this.updateProcessPropsForm.removeControl('processMode');
          this.updateProcessPropsForm.removeControl('isflagName');
          this.updateProcessPropsForm.removeControl('eventId');
          this.updateProcessPropsForm.removeControl('eventIds');
          if (isJsonString(value.value)) {
            const jsonVal = JSON.parse(value.value);
            this.defaultStartConditonMapValue = { ...jsonVal };
            this.startConditonMapValue = { ...jsonVal };
          } else {
            this.startConditonString = value.value;
            this.defaultStartConditonString = value.value;
          }
        } else if (this.inValue.propName === FLOW_MODEL_PRO_TYPE.CHANNEL_FLOW_CTRL_LIST) {
          this.updateProcessPropsForm.addControl('channelFlowName', new FormControl('', Validators.required));
          this.updateProcessPropsForm.removeControl('endEmitServiceName');
          this.updateProcessPropsForm.removeControl('startConName');
          this.updateProcessPropsForm.removeControl('startConMapName');
          this.updateProcessPropsForm.removeControl('checkOutName');
          this.updateProcessPropsForm.removeControl('outTimeName');
          this.updateProcessPropsForm.removeControl('processMode');
          this.updateProcessPropsForm.removeControl('isflagName');
          this.updateProcessPropsForm.removeControl('eventIds');
          this.updateProcessPropsForm.removeControl('eventId');
          this.channelFlowList = this.inValue.propValue;
          this.defaultChannelFlowList = this.inValue.propValue;
        } else if (this.inValue.propName === FLOW_MODEL_PRO_TYPE.FLOW_AUTO_REPEAT_PROCESS) {
          this.updateProcessPropsForm.removeControl('endEmitServiceName');
          this.updateProcessPropsForm.removeControl('outTimeName');
          this.updateProcessPropsForm.removeControl('startConName');
          this.updateProcessPropsForm.removeControl('startConMapName');
          this.updateProcessPropsForm.removeControl('channelFlowName');
          this.updateProcessPropsForm.removeControl('checkOutName');
          this.updateProcessPropsForm.removeControl('isflagName');
          this.updateProcessPropsForm.removeControl('eventIds');
          this.updateProcessPropsForm.removeControl('eventId');
          this.updateProcessPropsForm.addControl('processMode', new FormControl('', Validators.required));
          this.selectedprocessMode = JSON.parse(this.inValue.propValue);
          this.defaultProcessMode = JSON.parse(this.inValue.propValue);
        } else if (this.inValue.propName === FLOW_MODEL_PRO_TYPE.IS_FLOW_AUTHORIZE) {
          this.updateProcessPropsForm.removeControl('endEmitServiceName');
          this.updateProcessPropsForm.removeControl('outTimeName');
          this.updateProcessPropsForm.removeControl('startConName');
          this.updateProcessPropsForm.removeControl('startConMapName');
          this.updateProcessPropsForm.removeControl('channelFlowName');
          this.updateProcessPropsForm.removeControl('checkOutName');
          this.updateProcessPropsForm.removeControl('processMode');
          this.updateProcessPropsForm.removeControl('eventId');
          this.updateProcessPropsForm.addControl('isflagName', new FormControl('', Validators.required));
          this.updateProcessPropsForm.addControl('eventIds', new FormControl(''));
          this.isFlowAuthorize.isflag = JSON.parse(this.inValue.propValue)['isflag'];
          this.isFlowAuthorize.defaultIsflag = JSON.parse(this.inValue.propValue)['isflag'];
          this.isFlowAuthorize.ids = JSON.parse(this.inValue.propValue)['eventId'];
          this.isFlowAuthorize.defaultIds = JSON.parse(this.inValue.propValue)['eventId'];
        } else if (this.inValue.propName === FLOW_MODEL_PRO_TYPE.NOTIFY_FOR_PROCESS_CREATER) {
          this.updateProcessPropsForm.removeControl('endEmitServiceName');
          this.updateProcessPropsForm.removeControl('outTimeName');
          this.updateProcessPropsForm.removeControl('startConName');
          this.updateProcessPropsForm.removeControl('startConMapName');
          this.updateProcessPropsForm.removeControl('channelFlowName');
          this.updateProcessPropsForm.removeControl('checkOutName');
          this.updateProcessPropsForm.removeControl('processMode');
          this.updateProcessPropsForm.removeControl('isflagName');
          this.updateProcessPropsForm.removeControl('eventIds');
          this.updateProcessPropsForm.addControl('eventId', new FormControl(''));
          this.eventId = JSON.parse(this.inValue.propValue)['eventId'];
          this.defaultEventId = JSON.parse(this.inValue.propValue)['eventId'];
        }
      }
    }
  }


  // 新增定时启动
  addTimeToStart(timeStart): void {
    timeStart.resetAdd();
    this.timeDisplay = true;
  }


  // 定时启动传上来的值
  callBackTime(event): void {
    this.timeDisplay = !event;
    this.timeToStart.push(event);
    this.timeToStart = this.timeToStart.filter(val => {
      if (val['startWeekStr']) {
        return val.startWeekStr !== this.isRemoveData.startWeekStr;
      } else if (val['startDate']) {
        return val.startDate !== this.isRemoveData.startDate;
      }
    })
  }


  // 删除定时启动
  deleteTimeToStart(item): void {
    this.confirmationService.confirm({
      message: '确认删除吗？',
      header: '信息提示框',
      icon: 'fa fa-question-circle',
      key: 'mapConfirm3',
      accept: () => {
        this.timeToStart = this.timeToStart.filter(val => {
          if (item['startWeekStr']) {
            return (val.startWeekStr !== item.startWeekStr)
          } else if (item['startDate']) {
            return (val.startDate !== item.startDate);
          }
        })
      }
    })



  }

  // 修改定时启动
  modifyTimeToStart(item, timeStart): void {
    this.isRemoveData = { ...item };
    this.isModifyNum++;
    this.timeDisplay = true;
    timeStart.startTime = item.startTime;
    timeStart.defaultStartTime = item.startTime;
    if (item['startWeekStr']) {
      timeStart.timeToStartForm.addControl('weekName', new FormControl('', Validators.required));
      timeStart.timeToStartForm.removeControl('dateName');
      timeStart.defaultSelectedTimeStartType = findObject('week', this.timeToStartType, 'type');
      timeStart.selectedTimeStartType = findObject('week', this.timeToStartType, 'type');
      item.startWeekStr.forEach(element => {
        const obj = findObject(element, this.weeks, 'descp');
        timeStart.defaultSelectedWeeks.push(obj);
        timeStart.selectedWeeks.push(obj);
      });
    } else if (item['startDate']) {
      timeStart.timeToStartForm.addControl('dateName', new FormControl('', Validators.required));
      timeStart.timeToStartForm.removeControl('weekName');
      timeStart.defaultSelectedTimeStartType = findObject('date', this.timeToStartType, 'type');
      timeStart.selectedTimeStartType = findObject('date', this.timeToStartType, 'type');
      timeStart.defaultDateTime = item.startDate;
      timeStart.dateTime = item.startDate;
    }

  }


  // 测试脚本模态框的关闭按钮
  callBackVerifyScript(event): void {
    this.verifyScriptDisplay = !event;
  }




}
