import { Injectable } from '@angular/core';
import { HttpService } from '../../common/service/http.service';
import { EVENT_SERVICE_API } from '../process-definition.constant';
import { RTS_HTTP_URL } from 'app/@uisftech/common/service/http.constant';
// import { RTS_HTTP_URL } from '../../common/constant/config';


@Injectable()
export class EventServiceService {

  constructor(
    private httpService: HttpService
  ) {
  }

  // 查询事件服务列表
  searchlistEvtList = (params) => this.httpService.post(RTS_HTTP_URL + EVENT_SERVICE_API.listEvtServiceDef, params, null);

  // 更新修改的事件服务
  updateEvtService = (params) => this.httpService.post(RTS_HTTP_URL + EVENT_SERVICE_API.updateEvtServiceDef, params, null);

  // 保存新增的事件服务
  addEvtService = (params) => this.httpService.post(RTS_HTTP_URL + EVENT_SERVICE_API.addEvtServiceDef, params, null);

  // 删除事件服务
  deleteEvtService = (params) => this.httpService.post(RTS_HTTP_URL + EVENT_SERVICE_API.deleteEvtServiceDef, params, null);
}
