import { Component, OnChanges, Input } from '@angular/core';
import { BusinessNodeProsExtendModel, AUTO_NODE_PRO_TYPE, BUSINESS_SIGN_APPROVE_PRO_TYPE, SUB_NODE_PRO_TYPE } from '../../process-definition-model';
import { EVENT_SERVICE_CALL_MODE, TIMEOUT_WARNING_TYPE, TASK_NOTIFY_TYPE, TASK_NODE_TYPES } from '../../../common/constant/code.value';
import { handleResult, handleError, findObject, isJsonString } from '../../../common/func/common-func';
import { MessageService } from 'primeng/components/common/messageservice';
import { ConfirmationService } from 'primeng/primeng';
import { BusinessNodeManagementService } from '../../business-node-management/business-node-management.service';

@Component({
  selector: 'app-node-extend-props-list',
  templateUrl: './node-extend-props-list.component.html',
  styleUrls: ['./node-extend-props-list.component.scss']
})
export class NodeExtendPropsListComponent implements OnChanges {
  msg = [];
  // 从流程建模页面传回来的流程定义
  @Input() inValue: any;
  // 从点击流程建模界面的查看扩展属性按钮传的值
  @Input() isShow: number = 0;
  // 父dialog  解决dialog嵌套
  @Input() dialogTemplate: any;
  // 流程节点扩展属性列表
  businessNodeProsExtendList: BusinessNodeProsExtendModel[] = [];
  // 单选选中的扩展节点属性实例
  selectedBusinessNodeProsExtend: BusinessNodeProsExtendModel;
  // 新增属性按钮 新增模态框数据是否清空
  isRest: number = 0;
  // 修改属性按钮, 修改模态框数据是否清空
  isRestModify: number = 0;
  // 显示新增属性模态框
  showProsModal = true;

  dialogParam = {
    display: false,
    showModel: '',
    headerTitle: ''
  };
  // 所属模型类型
  isNodeModelType: string = '0';  //  '0' -节点属性 '1'-流程属性

  // 预警消息类型
  timeOutWarningType = TIMEOUT_WARNING_TYPE;
  //  任务通知机制
  taskNotifyTypes = TASK_NOTIFY_TYPE;
  // 节点定义类型
  businessNodeTypes = TASK_NODE_TYPES;
  // 调用模式
  eventServiceCallMode = EVENT_SERVICE_CALL_MODE;

  constructor(
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private businessNodeManagementService: BusinessNodeManagementService,
  ) {

  }

  ngOnChanges() {
    this.isShow && this.getBusinessNodePropExtends();
  }


  // 查看扩展属性按钮
  getBusinessNodePropExtends(): void {
    if (!this.inValue) {
      return;
    }
    const bodyParams = {
      ownId: this.inValue.nodeId
    };
    this.businessNodeManagementService.queryBussinessNodePropExtends(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.businessNodeProsExtendList = data.pageData[0].result;
            this.businessNodeProsExtendList.forEach(element => {
              const allextendPro: string[] = [];
              if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.USERRULE) {
                if (element.propValue) {
                  const value = JSON.parse(element.propValue);
                  element.extendType = value.label;
                }
              } else if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.FUNCTIONLIST) {
                if (element.propValue) {
                  const value = JSON.parse(element.propValue);
                  if (isJsonString(value.value)) {
                    const jsonVal = JSON.parse(value.value);
                    element.extendType = jsonVal.label
                  } else {
                    element.extendType = value.value;
                  }
                }
              } else if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.TIME_START) {
                if (element.propValue) {
                  element.extendType = `${element.propValue}天`;
                }
              } else if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_OPTION_TIME) {
                const value = JSON.parse(element.propValue);
                if (element.propValue) {
                  if (isJsonString(element.propValue)) {
                    if (value.timeOut.value) {
                      const unit = value.timeOut.dataType === '0' ? '小时': '天';
                      element.extendType = value.timeOut.value + unit;
                    } else {
                      element.extendType = value.urule.label;
                    }
                  } else {
                    element.extendType = value + '小时';
                  }
                }
              } else if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_OPTION_TIME_RULE) {
                if (element.propValue) {
                  const value = JSON.parse(element.propValue);
                  element.extendType = value.label;
                }
              } else if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.MAX_CHECK_OUT_NUM) {
                if (element.propValue) {
                  element.extendType = `${element.propValue}笔`;
                }
              } else if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.SELECT_OPTION) {
                if (element.propValue) {
                  element.extendType = element.propValue;
                }
              } else if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.ACCESSORY_PROP || element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.NODE_AUTO_REPEATP_ROCESS) {
                if (element.propValue) {
                  const value = JSON.parse(element.propValue);
                  element.extendType = value.descp;
                }
              } else if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_ALLOCATION_TYPE) {
                if (element.propValue) {
                  const value = JSON.parse(element.propValue);
                  element.extendType = value.type === '2' ? `${value.descp}${value.percentage}` : value.descp;
                }
              } else if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.TIME_OUT_OPRATE) {
                if (element.propValue) {
                  const nameArray = [];
                  const value = JSON.parse(element.propValue);
                  if (Object.prototype.toString.call(value.type) == '[object Object]') {
                    if (value['value']) {
                      element.extendType = `${value.type.descp} + ${value.value.label}`;
                    } if (value['urule']) {
                      element.extendType = `${value.type.descp} + ${value.urule.label}`;
                    } else if (value['event']) {
                      value.event.forEach((item, index) => {
                        if (item.order && item.callMode) {
                          const mode = findObject(item.callMode, this.eventServiceCallMode, 'type');
                          const str = `(${index + 1})、${item.name}:调用次序为${item.order};调用模式为${mode.descp}`;
                          nameArray.push(str);
                        } else {
                          const str = `(${index + 1})、${item.name}:调用次序(无);调用模式(无)`;
                          nameArray.push(str);
                        }
                      });
                      element.extendType = `${value.type.descp} +${nameArray.join('、')}`;
                    } else if(value.type.type === '9') {
                      const unit = value.timeOut.dataType === '0' ? '小时': '天';
                      const screenSpace = value.timeOut.value + unit;
                      element.extendType = `${value.type.descp} + ${screenSpace}`;
                    }
                  } else {
                    element.extendType = `${value.descp}`;
                  }
                }
              } else if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.TIME_OUT_WARN) {
                if (element.propValue) {
                  const value = JSON.parse(element.propValue);
                  let str = null;
                  if (value['time']) {
                    str = `超时${value.time}小时预警`;
                  }
                  if (value['template']) {
                    // const template = JSON.parse(value.template);
                    const messageType = findObject(value.template.type, this.timeOutWarningType, 'type');
                    str += `   使用${messageType.descp}`;
                  }
                  element.extendType = str;
                }
              } else if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.NOTIFY_TYPE) {
                if (element.propValue) {
                  const value = JSON.parse(element.propValue);
                  const messageType = findObject(value.type, this.taskNotifyTypes, 'type');
                  element.extendType = messageType.descp;
                }
              } else if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.START_TASK_EVENT 
                || element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.END_TASK_EVENT
                || element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.CANCEL_TASK_EVENT
                || element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.RECALL_TASK_EVENT
                || element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.BACK_TASK_EVENT) {
                if (element.propValue) {
                  const value = JSON.parse(element.propValue);
                  const nameArray = [];
                  value.forEach((item, index) => {
                    if (item.order && item.callMode) {
                      const mode = findObject(item.callMode, this.eventServiceCallMode, 'type');
                      const str = `(${index + 1})、${item.name}:调用次序为${item.order};调用模式为${mode.descp}`;
                      nameArray.push(str);
                    } else {
                      const str = `(${index + 1})、${item.name}:调用次序(无);调用模式(无)`;
                      nameArray.push(str);
                    }
                  });
                  element.extendType = nameArray.join('、');
                }
              } else if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.CHANNEL_NODE_CTRL_LIST) {
                if (element.propValue) {
                  element.extendType = element.propValue;
                }
              } else if (element.propName === AUTO_NODE_PRO_TYPE.AUTO_NODE_SERVERICE_CALL) {
                if (element.propValue) {
                  const value = JSON.parse(element.propValue);
                  element.extendType = value.name;
                }
              } else if (element.propName === AUTO_NODE_PRO_TYPE.NODE_ENTRY_CONDITION
                || element.propName === AUTO_NODE_PRO_TYPE.NODE_EXIT_CONDITION
                || element.propName === SUB_NODE_PRO_TYPE.NODE_EXIT_CONDITION
                || element.propName === SUB_NODE_PRO_TYPE.NODE_ENTRY_CONDITION
                || element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.NODE_ENTRY_CONDITION
                || element.propName === SUB_NODE_PRO_TYPE.NODE_EXIT_CONDITION
                || element.propName === SUB_NODE_PRO_TYPE.NODE_ENTRY_CONDITION
                || element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.NODE_EXIT_CONDITION) {
                if (element.propValue) {
                  const value = JSON.parse(element.propValue);
                  if (isJsonString(value.value)) {
                    const jsonVal = JSON.parse(value.value);
                    element.extendType = jsonVal.label
                  } else {
                    element.extendType = value.value;
                  }
                }
              } else if (element.propName === AUTO_NODE_PRO_TYPE.START_TASK_EVENT || element.propName === AUTO_NODE_PRO_TYPE.END_TASK_EVENT) {
                if (element.propValue) {
                  const value = JSON.parse(element.propValue);
                  const nameArray = [];
                  value.forEach((item, index) => {
                    if (item.order && item.callMode) {
                      const mode = findObject(item.callMode, this.eventServiceCallMode, 'type');
                      const str = `(${index + 1})、${item.name}:调用次序为${item.order};调用模式为${mode.descp}`;
                      nameArray.push(str);
                    } else {
                      const str = `(${index + 1})、${item.name}:调用次序(无);调用模式(无)`;
                      nameArray.push(str);
                    }
                  });
                  element.extendType = nameArray.join('、');
                }
              } else if (element.propName === SUB_NODE_PRO_TYPE.SUB_FLOW_SELECTION_CALL) {
                if (element.propValue) {
                  const value = JSON.parse(element.propValue);
                  // const nameArray = [];
                  // value.forEach(item => {
                  //   nameArray.push(item.defId);
                  // });
                  element.extendType = `流程编码:${value[0].defId},流程名称:${value[0].defName}`;
                }
              } else if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.CHECK_IN_OUT_VALUE) {
                if (element.propValue) {
                  const value = JSON.parse(element.propValue);
                  const flag = value.checkInFlag === '3'? '领用后不可退回任务池': '领用后可退回任务池';
                  element.extendType = flag + '；' + '最大可领用数' + value.maxCheckOutNum + '笔；'
                }
              } else if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.CHANNEL_NODE_QUE_LIST) {
                element.extendType = element.propValue || '';
              } else if (element.propName === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_RECALL_CRTL) {
                if (element.propValue) {
                  const value = JSON.parse(element.propValue);
                  const flag = value.crtlType === '0'? '下节任务未处理可追回': '实例未结束可追回';
                  element.extendType = flag + '：' + value.ctrlChannelList
                }
              }
            }
            );
          }, true, { errorMsg: '查询失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      );
  }



  // 新增扩展属性 ***
  addProsExtend(): void {
    this.isRest++;
    let nodeObj = null;
    this.dialogParam.display = true;
    this.showProsModal = true;
    if (this.inValue) {
      this.dialogParam.headerTitle = `新增${this.inValue['defName']}的扩展属性`;
      this.dialogParam.showModel = '10';
    }

    if (this.inValue) {
      nodeObj = findObject(this.inValue.type, this.businessNodeTypes, 'type');
      this.dialogParam.headerTitle = `新增${nodeObj['descp']}扩展属性`
      if (this.inValue.type === "task" || this.inValue.type === "approve" || this.inValue.type === "sign") {
        this.dialogParam.showModel = '10';
      } else if (this.inValue.type === "auto") {
        this.dialogParam.showModel = '20';
      }
      else if (this.inValue.type === "subproc") {
        this.dialogParam.showModel = '30';
      }
    }
  }


  // 修改扩展属性 ***
  modifyProsExtend(item): void {
    this.isRestModify = 0;
    this.selectedBusinessNodeProsExtend = { ...item };
    if (this.inValue.type === "task" || this.inValue.type === "approve" || this.inValue.type === "sign") {
      this.dialogParam.showModel = '11';
    } else if (this.inValue.type === "auto") {
      this.dialogParam.showModel = '21';
    }
    else if (this.inValue.type === "subproc") {
      this.dialogParam.showModel = '31';
    }
    this.dialogParam.headerTitle = `修改${item.propDesc}扩展属性`;
    this.dialogParam.display = true;
    this.showProsModal = true;

  }

  // 删除扩展属性
  deleteProsExtend(item): void {
    this.confirmationService.confirm({
      message: '您确定要删除吗？',
      header: '信息提示框',
      icon: 'fa fa-question-circle',
      key: 'mapConfirm2',
      accept: () => {
        const bodyParams = {
          ownId: item.ownId || '',
          propName: item.propName || ''
        };
        this.businessNodeManagementService.deleteNodePropExtends(bodyParams)
          .subscribe(
            data => {
              handleResult(data, this.messageService, () => {
                this.getBusinessNodePropExtends();
                this.selectedBusinessNodeProsExtend = null;
              }, true, { message: '删除成功', errorMsg: '删除失败' });
            },
            err => {
              handleError(err, this.messageService, () => {
              });
            }
          );
      }
    });


  }



  // 新增属性模态框传值 ***
  addPropsCallBack(event): void {
    this.showProsModal = !event;
    this.getBusinessNodePropExtends();
    this.selectedBusinessNodeProsExtend = null;
  }




  // 修改属性模态框传值 ***
  updatePropscallBack(event): void {
    this.showProsModal = !event;
    this.getBusinessNodePropExtends();
    this.selectedBusinessNodeProsExtend = null;
  }



}
