import { Component, OnInit, OnChanges, Input, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { PROCESS_MODEL_EXTEND_PROPS, TIMEOUT_HANDING_TYPES } from '../../../common/constant/code.value';
import { ProsTypeModel, FLOW_MODEL_PRO_TYPE, DictionaryTypeModel, RuleModel, ProcessDefinitionModel } from '../../process-definition-model';
import { BusinessNodeManagementService } from '../../business-node-management/business-node-management.service';
import { handleResult, handleError, findObject } from '../../../common/func/common-func';
import { MenuItem } from 'primeng/primeng';
import { Pager } from '../../../common/constant/config';
import { ProcessMaintenanceService } from '../process-maintenance.service';
// import { ProcessMaintenanceService } from 'app/pages/activiti-work-flow/process-definition/process-maintenance/process-maintenance.service';
// import { Pager } from 'app/pages/activiti-work-flow/common/constant/config';

@Component({
  selector: 'app-query-history-defVersion',
  templateUrl: './query-history-defVersion.component.html',
  styleUrls: ['./query-history-defVersion.component.scss']
})
export class QueryHistoryDefVersionComponent implements OnChanges {

  msg = [];

  @Input() inValue: any;
  // 分页
  pager: Pager = new Pager();
  // 点击历史流程版本
  @Input() isRest: number = 0;

  @Output() outValueEmitter: EventEmitter<any> = new EventEmitter();

  // 历史版本列表
  processDefinitionVersion: ProcessDefinitionModel[] = [];



  constructor(
    private messageService: MessageService,
    private processMaintenanceService: ProcessMaintenanceService,
  ) {
  }


  ngOnChanges() {
    this.isRest && this.queryDefVersion();
  }

  queryDefVersion() {
    if (this.inValue) {
      const bodyParams = {
        defId: this.inValue.defId,
        pageRows: parseInt(this.pager.rows, 10),
        curPage: parseInt(this.pager.page, 10)
      };
      this.processMaintenanceService.selectVersionList(bodyParams)
        .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            const dataResult: Array<ProcessDefinitionModel> = data.pageData[0].result;
            this.pager.totalRecords = data.pageData[0].totalRows;
            this.processDefinitionVersion = dataResult.filter(item => item.defVersion !== 0);
            const maxDefversion = Math.max.apply(Math, this.processDefinitionVersion.map(o => o.defVersion));
            this.processDefinitionVersion.forEach(val => {
              val.currentVersion = val.defVersion === maxDefversion;
            })
          }, true, { errorMsg: '查询失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
        );
    }

  }

  // 节点定义分页
  paginate(event) {
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.queryDefVersion();
  }

  // 查看设计器
  viewDesign(item): void {
    const parms = `${item.id}/userId001/sysCode001/tokenCode001`;
    this.processMaintenanceService.toActivitiPage(parms);
  }

  // 恢复至此版本
  recoverThisDefversion(item): void {
    const bodyParams = {
      defId: item.defId,
      defVersion: item.defVersion
    };
    this.processMaintenanceService.backHistVersion(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.outValueEmitter.emit(event);
        }, true, { message: ' 恢复成功', errorMsg: '恢复失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
  }



}
