import { Component, OnInit, Input, EventEmitter, Output, OnChanges } from '@angular/core';
import { ProsTypeModel, AUTO_NODE_PRO_TYPE, EventServiceListModel, RuleModel, SUB_NODE_PRO_TYPE, ProcessDefinitionModel } from '../../process-definition-model';
import { AUTO_NODE_TYPES, SUB_NODE_TYPES } from '../../../common/constant/code.value';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { BusinessNodeManagementService } from '../business-node-management.service';
import { handleResult, handleError, showMessage } from '../../../common/func/common-func';

@Component({
  selector: 'app-add-props-extend-subnode',
  templateUrl: './add-props-extend-subnode.component.html',
  styleUrls: ['./add-props-extend-subnode.component.scss']
})
export class AddPropsExtendSubnodeComponent implements OnChanges {



  @Input() inValue: any;
  // 模型类型
  @Input() modelType: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();

  @Input() isRest: number = 0;

  // 父dialog  解决dialog嵌套
  @Input() dialogTemplate: any;

  msg = [];
  // 自动节点的属性类型
  prosExtendSubTypes = SUB_NODE_TYPES;
  // 选中的扩展属性类型
  selectedProsExtendType: ProsTypeModel = new ProsTypeModel();
  // 表单
  subNodeForm: FormGroup;

  dialogParam = {
    display: false,
    headerTitle: ''
  };

  // 显示规则树还是自动服务调用
  showRuleTreeOrAutoService = {
    isRuleTree: false,
    isSubNode: false,
  }
  // 选中流程建模
  selectedFlowModel: ProcessDefinitionModel = new ProcessDefinitionModel();
  // 节点进入、离开条件
  nodeConditonString: string;
  // 节点进入、离开条件映射值
  nodeConditonMapValue: RuleModel = new RuleModel();
  // 任务处理时间
  taskHandleTime: number;
  // 选中的流程数组
  selectedFlowModelArray: ProcessDefinitionModel = new ProcessDefinitionModel();
  // 调用服务规则
  isRuleNum: number = 0;
  // 控制脚本测试的模态框显隐
  verifyScriptDisplay = false;





  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private businessNodeManagementService: BusinessNodeManagementService
  ) {
    this.createValidators();
  }

  ngOnChanges() {
    (this.isRest > 0) && this.resetAddProsExtend();

  }


  createValidators() {
    this.subNodeForm = this.fb.group({
      'addProsExtendTypeName': new FormControl('', Validators.required),
      'subFlowlName': new FormControl('', Validators.required),
      'nodeExitConName': new FormControl(''),
      'nodeExitConMapName': new FormControl('')
    });
  }


  removeAlAddValiditor(): void {
    this.subNodeForm.removeControl('subFlowlName');
  }

  // 属性类型选择
  changeAddProsExtendType(event): void {
    this.removeAlAddValiditor();
    if (event.type === SUB_NODE_PRO_TYPE.SUB_FLOW_SELECTION_CALL) {
      this.subNodeForm.addControl('subFlowlName', new FormControl('', Validators.required));
      this.selectedFlowModelArray = null;
    } else {
      this.nodeConditonMapValue.label = null;
    }

  }



  // 重置新增规则
  resetAddProsExtend(): void {
    this.selectedProsExtendType = new ProsTypeModel();
    this.selectedFlowModel.defId = null;
    this.nodeConditonString = null;
    this.nodeConditonMapValue.label = null;
  }



  // 选择流程建模
  selectedPro(fm): void {
    this.dialogParam.headerTitle = '流程建模列表';
    this.dialogParam.display = true;
    this.showRuleTreeOrAutoService = {
      isSubNode: true,
      isRuleTree: false
    };
    fm.reset();
    fm.resetSelected();
    fm.getProcessDefinitions();
  }

  // 选择调用规则树
  selectedProRule(): void {
    this.dialogParam.headerTitle = '规则树及列表';
    this.dialogParam.display = true;
    this.showRuleTreeOrAutoService = {
      isSubNode: false,
      isRuleTree: true
    };
    this.isRuleNum++;
  }

  // 选择流程建模
  selectedEventService(event): void {
    console.log(event);
    // if (!item.selectedValues || (item.selectedValues && item.selectedValues < 1)) {
    //   showMessage(this.messageService, 'error', '请选择流程建模！');
    //   return;
    // }

    // // this.selectedFlowModelArray = Array.from(item.selectedValues);
    // const nameArray = [];
    // const noOrderArray = [];
    // if (item.selectedValues && item.selectedValues.length) {
    //   this.subFlowModelValue = null;
    // }
    // item.selectedValues.forEach(val => {
    //   nameArray.push(val.defId);
    //   const hasValue = this.selectedFlowModelArray.some(element => {
    //     return element.defId === val.defId;
    //   })
    //   if (!hasValue) {
    //     this.selectedFlowModelArray.push({ defId: val.defId });
    //   }
    // })
    this.selectedFlowModelArray = { ...event };
    // this.subFlowModelValue = event.defId;
    this.dialogParam.display = false;
  }


  // 验证脚本按钮
  verifyScript(verifyTestScript): void {
    verifyTestScript.resetListAndResult();
    this.verifyScriptDisplay = true;
  }

  // 测试脚本模态框的关闭按钮
  callBackVerifyScript(event): void {
    this.verifyScriptDisplay = !event;
  }


  // 从规则树传上来的值
  callBackRule(event): void {
    this.dialogParam.display = !event;
    if (this.selectedProsExtendType) {
      if (this.selectedProsExtendType.type === AUTO_NODE_PRO_TYPE.NODE_ENTRY_CONDITION || this.selectedProsExtendType.type === AUTO_NODE_PRO_TYPE.NODE_EXIT_CONDITION) {
        this.nodeConditonMapValue = { ...event };
      }
    }

  }





  // 从流程建模上传的值
  callBackFlowModel(event): void {
    this.dialogParam.display = !event;
  }

  //  保存属性值
  saveProsExtend(): void {
    if (!this.selectedProsExtendType.type) {
      showMessage(this.messageService, 'error', '请选择扩展属性类型！');
      return;
    }
    let value = null;
    if (this.selectedProsExtendType.type === SUB_NODE_PRO_TYPE.NODE_ENTRY_CONDITION || this.selectedProsExtendType.type === SUB_NODE_PRO_TYPE.NODE_EXIT_CONDITION) {
      if (!this.nodeConditonMapValue.label) {
        showMessage(this.messageService, 'error', '规则映射无属性值，禁止保存');
        return;
      }
      let valueParams = {};
      this.nodeConditonString ? valueParams = {
        type: '3',
        value: this.nodeConditonString
      } : this.nodeConditonMapValue.label ? valueParams = {
        type: '2',
        value: JSON.stringify(this.nodeConditonMapValue)
      } : valueParams;
      value = JSON.stringify(valueParams);
    } else if (this.selectedProsExtendType.type === SUB_NODE_PRO_TYPE.SUB_FLOW_SELECTION_CALL) {
      const obj = [{
        defId:this.selectedFlowModelArray.defId,
        defName:this.selectedFlowModelArray.defName
      }];
      value = JSON.stringify(obj);
    }
    const bodyParams = {
      ownId: this.inValue.nodeId || '',
      ownType: this.modelType,
      propName: this.selectedProsExtendType.type || '',
      propDesc: this.selectedProsExtendType.descp || '',
      propValue: value,
    }
    this.businessNodeManagementService.addNodePropExtends(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.outValue.emit(event);
          // this.subFlowModelValue = null;
          this.nodeConditonMapValue.label = null;
        }, true, { message: ' 保存成功', errorMsg: '保存失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
          this.outValue.emit(event);
        });
      }
      );
  }





}
