import { Component, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { GeneralParametersService } from '../../general-parameters.service';
import { handleResult, handleError } from '../../../../common/func/common-func';

@Component({
  selector: 'app-modify-channel-map',
  templateUrl: './modify-channel-map.component.html',
  styleUrls: ['./modify-channel-map.component.scss']
})
export class ModifyChannelMapComponent implements OnChanges {

  msg = [];

  @Input() inValue: any;

  @Output() outValueEmitter: EventEmitter<any> = new EventEmitter();
  // 表单
  channelMapForm: FormGroup;

  isSubmiting = false;



  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private generalParametersService: GeneralParametersService,
  ) { }


  ngOnChanges() {
    this.isSubmiting = false;
    this.inValue && this.createValidators();
  }

  createValidators() {
    this.channelMapForm = this.fb.group({
      'paramCode': [this.inValue.paramCode, Validators.required],
      'paramName': [this.inValue.paramName, Validators.required],
      'paramObjCode': [this.inValue.paramObjCode, Validators.required],
      'paramObjName': [this.inValue.paramObjName, Validators.required]
    });

  }

  // 保存
  preserveClick() {
    this.isSubmiting = true;
    // 处理是否提交
    for (const key in this.channelMapForm.controls) {
      if (this.channelMapForm.controls.hasOwnProperty(key)) {
        this.channelMapForm.controls[key].markAsDirty();
      }
    }

    // 表单校验不通过  不提交
    if (!this.channelMapForm.valid) {
      this.isSubmiting = false;
      return;
    }
    // 提交服务
    const param = Object.assign(this.channelMapForm.value, {
      paramType: 'H0',
      id: this.inValue.id,
    })
    this.generalParametersService.updateWfParam(param)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.outValueEmitter.emit(event)
          }, true, { message: '更新成功！', errorMsg: '更新失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
            this.outValueEmitter.emit(event)
          });
        }
      );
  }


  // 重置
  reset() {
    this.channelMapForm.get('paramCode').setValue(this.inValue.paramCode);
    this.channelMapForm.get('paramName').setValue(this.inValue.paramName);
    this.channelMapForm.get('paramObjCode').setValue(this.inValue.paramObjCode);
    this.channelMapForm.get('paramObjName').setValue(this.inValue.paramObjName);
  }
}





