import { Component, OnChanges, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { TreeNode, ConfirmationService, MenuItem } from 'primeng/primeng';
import { BusinessNodeManagementService } from '../../business-node-management.service';
import { handleResult, handleError } from '../../../../common/func/common-func';
import { RuleTypeModel } from '../../../process-definition-model';
import { MessageService } from 'primeng/components/common/messageservice';

@Component({
  selector: 'app-rule-tree',
  templateUrl: './rule-tree.component.html',
  styleUrls: ['./rule-tree.component.scss']
})
export class RuleTreeComponent implements OnChanges {

  // 规则类型
  @Input() inValue: any;

  @Input() isRest: number = 0;

  @Output() outValue: EventEmitter<any> = new EventEmitter();

  msg = [];
  // 项目列表树
  projectTreeList: TreeNode[];
  // 保存项目列表展开状态的变量
  expandNodes: TreeNode[] = [];
  // 选中的集合
  selectedProject: TreeNode;
  // 规则列表
  ruleList: RuleTypeModel[] = [];
  // 单选选中的规则
  selectedRule: RuleTypeModel = new RuleTypeModel();




  constructor(
    private messageService: MessageService,
    private businessNodeManagementService: BusinessNodeManagementService,
  ) { }


  ngOnChanges() {
    if (this.isRest) {
      this.queryProjectTree();
      this.ruleList = [];
    }
  }

  // 查找流程目录树
  queryProjectTree(): void {
    this.businessNodeManagementService.queryLoadLibProjectTree(null)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.projectTreeList = data.pageData[0].result;
          if (this.projectTreeList && this.projectTreeList.length) {
            this.expandRecursive(this.projectTreeList[0], true);
          }
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {

        });
      });
  }

  // 折叠或展开
  expandRecursive(node: TreeNode, isExpand: boolean) {
    node.expanded = isExpand;
    node.children && node.children.forEach(childNode => {
      this.expandRecursive(childNode, isExpand);
    });
  }


  // 点击左边节点
  onNodeSelect(event): void {
    const bodyParams = {
      id: this.selectedProject['id'] || '',
      ruleType: this.inValue
    };
    this.businessNodeManagementService.queryRuleFileList(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.ruleList = data.pageData[0].result
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
  }


  selceteRule(event): void {
    this.outValue.emit(event);
  }




}
