import { Component, OnInit, OnChanges, Input, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { PROCESS_MODEL_EXTEND_PROPS, TIMEOUT_HANDING_TYPES, TIME_TO_START_TYPE, WEEKS, CHECK_IN_OUT_VALUE, PROCESS_MODE } from '../../../common/constant/code.value';
import { ProsTypeModel, FLOW_MODEL_PRO_TYPE, DictionaryTypeModel, RuleModel, TimeToStartModel } from '../../process-definition-model';
import { BusinessNodeManagementService } from '../../business-node-management/business-node-management.service';
import { handleResult, handleError, findObject, showMessage } from '../../../common/func/common-func';
import { ConfirmationService } from 'primeng/primeng';


@Component({
  selector: 'app-add-process-props-extend',
  templateUrl: './add-process-props-extend.component.html',
  styleUrls: ['./add-process-props-extend.component.scss']
})
export class AddProcessPropsExtendComponent implements OnInit, OnChanges {
  msg = [];

  @Input() inValue: any;
  // 模型类型
  @Input() modelType: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();

  @Input() isRest: any;
  // 父dialog  解决dialog嵌套
  @Input() dialogTemplate: any;

  @Input() isShow: boolean;
  // 流程建模扩展属性类型
  flowProsExtendTypes = PROCESS_MODEL_EXTEND_PROPS;
  // 选中的扩展属性类型
  selectedProsExtendType: ProsTypeModel = new ProsTypeModel();
  // 新增表单
  addProcessPropsForm: FormGroup;
  // 结束触发调用服务值
  endEmitService: string;

  dialogParam = {
    display: false,
    headerTitle: ''
  };
  // 选中事件服务映射数组
  selectedEventServiceMap: any[];
  // 首节点是否跳过
  isSkipFirstNode: boolean;
  // 定时启动时长
  startTime: number;
  // 流程最长处理时间
  maxProcessHandleTime: number;
  // 流程超时处理机制类型
  timeoutHandlingType = TIMEOUT_HANDING_TYPES;
  // 选中的流程超时处理机制
  selectedtTimeoutHandlingType = new DictionaryTypeModel();
  // 检入检出值
  checkInOuts = CHECK_IN_OUT_VALUE;
  // 选中的检入检出
  selectedCheckInOut = new DictionaryTypeModel();
  // 显示规则树还是事件服务
  showRuleTreeOEventService = {
    isEventService: false,
    isRuleTree: false
  };
  // 启动条件/结束条件
  startConditonString: string;
  // 启动条件/结束条件映射值
  startConditonMapValue: RuleModel = new RuleModel();

  resetFlag: number = 0;
  // 服务调用
  isRuleNum = 0;
  // 定时启动列表
  timeToStart: TimeToStartModel[] = [];
  // 定时时间模态框显隐
  timeDisplay = false;
  // 定时类型
  timeToStartType = TIME_TO_START_TYPE;
  // 修改定时启动
  isModifyNum: number = 0;
  // 星期
  weeks = WEEKS;
  // 需要删除的数据
  isRemoveData: TimeToStartModel = new TimeToStartModel();
  // 控制脚本测试的模态框显隐
  verifyScriptDisplay = false;
  // 最长处理时间规则
  maxTimeRule = new RuleModel();
  // 流程控制渠道
  channelFlowList: string;
  // 选中的处理方式值
  selectedprocessMode = new DictionaryTypeModel();
  // 选中方式下拉框值
  processMode = PROCESS_MODE;
  // 是否转授权
  isFlowAuthorize = {
    isflag: true,
    ids: ''
  };
  // 动态提醒->事件ID
  eventId: string;
  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private businessNodeManagementService: BusinessNodeManagementService
  ) {
    this.createValidators();
  }

  ngOnInit() {
  }


  ngOnChanges() {
    this.resetAddProsExtend();
    this.resetFlag = this.isRest;
  }

  createValidators() {
    this.addProcessPropsForm = this.fb.group({
      'addProsExtendTypeName': ['', Validators.required],
      'endEmitServiceName': ['', Validators.required],
      'skipFistNodeName': [''],
      // 'timeToStartName': ['', Validators.required],
      'maxTimeName': [''],
      'maxTimeRulelName': [''],
      'outTimeName': ['', Validators.required],
      'startConName': [''],
      'startConMapName': ['', Validators.required],
      'checkOutName': ['', Validators.required],
      'channelFlowName': ['', Validators.required],
      'processMode': ['', Validators.required],
      'isflagName': [true, Validators.required],
      'eventIds': [''],
      'eventId': []
    });
  }

  // 移除所有表单验证(属性类型除外)
  removeAlAddValiditor(): void {
    this.addProcessPropsForm.removeControl('endEmitServiceName');
    // this.addProcessPropsForm.removeControl('timeToStartName');
    // this.addProcessPropsForm.removeControl('maxTimeName');
    // this.addProcessPropsForm.removeControl('maxTimeRulelName');
    this.addProcessPropsForm.removeControl('outTimeName');
    this.addProcessPropsForm.removeControl('startConName');
    this.addProcessPropsForm.removeControl('startConMapName');
    this.addProcessPropsForm.removeControl('checkOutName');
    this.addProcessPropsForm.removeControl('channelFlowName');
    this.addProcessPropsForm.removeControl('processMode');
    this.addProcessPropsForm.removeControl('isflagName');
    this.addProcessPropsForm.removeControl('eventIds');
    this.addProcessPropsForm.removeControl('eventId');
  }

  // 属性类型选择
  changeAddProsExtendType(event): void {
    this.removeAlAddValiditor();
    this.resetAllValue();
    if (event.type === FLOW_MODEL_PRO_TYPE.FLOW_START_EVENT || event.type === FLOW_MODEL_PRO_TYPE.FLOW_END_EVENT || event.type === FLOW_MODEL_PRO_TYPE.FLOW_END_ERROR_EVENT || event.type === FLOW_MODEL_PRO_TYPE.HANG_UP_EVENT || event.type === FLOW_MODEL_PRO_TYPE.RECOVER_EVENT || event.type === FLOW_MODEL_PRO_TYPE.CANCEL_EVENT) {
      this.addProcessPropsForm.addControl('endEmitServiceName', new FormControl('', Validators.required));
    } else if (event.type === FLOW_MODEL_PRO_TYPE.SKIP_FIRST_NODE || event.type === FLOW_MODEL_PRO_TYPE.CHECK_PERSON || event.type === FLOW_MODEL_PRO_TYPE.CONFIR_INFO || event.type === FLOW_MODEL_PRO_TYPE.PRESON_PRO || event.type === FLOW_MODEL_PRO_TYPE.CALL_BACK_JUMP) {
      // this.addProcessPropsForm.addControl('skipFistNodeName', new FormControl('', Validators.required));
    } else if (event.type === FLOW_MODEL_PRO_TYPE.TIME_TO_START) {
      // this.addProcessPropsForm.addControl('timeToStartName', new FormControl('', Validators.required));
    } else if (event.type === FLOW_MODEL_PRO_TYPE.OUT_TIME_EXECUTION) {
      this.addProcessPropsForm.addControl('outTimeName', new FormControl('', Validators.required));
    } else if (event.type === FLOW_MODEL_PRO_TYPE.START_CONDITION || event.type === FLOW_MODEL_PRO_TYPE.END_CONDITION) {
      this.addProcessPropsForm.addControl('startConName', new FormControl(''));
      this.addProcessPropsForm.addControl('startConMapName', new FormControl('', Validators.required));
    } else if (event.type === FLOW_MODEL_PRO_TYPE.CHECKINOUT) {
      this.addProcessPropsForm.addControl('checkOutName', new FormControl('', Validators.required));
    } else if (event.type === FLOW_MODEL_PRO_TYPE.CHANNEL_FLOW_CTRL_LIST) {
      this.addProcessPropsForm.addControl('channelFlowName', new FormControl('', Validators.required));
    } else if (event.type === FLOW_MODEL_PRO_TYPE.FLOW_AUTO_REPEAT_PROCESS) {
      this.addProcessPropsForm.addControl('processMode', new FormControl('', Validators.required));
    } else if (event.type === FLOW_MODEL_PRO_TYPE.IS_FLOW_AUTHORIZE) {
      this.addProcessPropsForm.addControl('isflagName', new FormControl(true, Validators.required));
      this.addProcessPropsForm.addControl('eventIds', new FormControl(''));
    } else if (event.type === FLOW_MODEL_PRO_TYPE.NOTIFY_FOR_PROCESS_CREATER) {
      this.addProcessPropsForm.addControl('eventId', new FormControl(''));
    }
  }



  // 重置新增
  resetAddProsExtend(): void {
    this.selectedProsExtendType = new ProsTypeModel();
    this.resetAllValue();
  }

  // 将所有值置空
  resetAllValue(): void {
    this.endEmitService = null;
    this.isSkipFirstNode = false;
    this.startTime = null;
    this.maxProcessHandleTime = null;
    // 流程超时处理机制为“人工干预”
    this.selectedtTimeoutHandlingType = findObject('0', this.timeoutHandlingType, 'type');
    this.startConditonString = null;
    this.startConditonMapValue.label = null;
    this.maxTimeRule.label = null;
    this.timeToStart = [];
    this.selectedCheckInOut = findObject(this.checkInOuts[0].type, this.checkInOuts, 'type');
    this.channelFlowList = null;
    this.selectedprocessMode = findObject(this.processMode[0].type, this.processMode, 'type');
    this.isFlowAuthorize.isflag = true;
    this.isFlowAuthorize.ids = null;
    this.eventId = null;
  }


  // 事件服务调用选择
  selectedPropsMap(eventService): void {
    this.dialogParam.headerTitle = '事件服务';
    this.dialogParam.display = true;
    this.showRuleTreeOEventService = {
      isRuleTree: false,
      isEventService: true
    }
    eventService.reset();
    eventService.resetSelected();
    eventService.getEventServiceList();
  }



  // 选择规则属性映射
  selectedProRule(): void {
    this.dialogParam.headerTitle = '规则树及列表';
    this.dialogParam.display = true;
    this.showRuleTreeOEventService = {
      isRuleTree: true,
      isEventService: false
    };
    this.isRuleNum++;

  }

  // 验证脚本按钮
  verifyScript(verifyTestScript): void {
    verifyTestScript.resetListAndResult();
    this.verifyScriptDisplay = true;
  }



  // 选择事件服务
  selectedEventService(item): void {
    if (!item.selectedValues || (item.selectedValues && !item.selectedValues.length)) {
      showMessage(this.messageService, 'error', '请选择结束触发调用服务！');
      return;
    }
    let hash = {};
    this.selectedEventServiceMap = item.selectedValues.reduce((item, next) => {
      hash[next.id] ? '' : hash[next.id] = true && item.push(next);
      return item;
    }, [])

    // this.selectedEventServiceMap = Array.from(item.selectedValues);
    const noSetService = this.selectedEventServiceMap.filter(item => {
      return !item.order;
    });
    if (Array.isArray(noSetService) && noSetService.length) {
      showMessage(this.messageService, 'error', '有服务调用次序、方式、条件未配置！');
      return;
    }
    const nameArray = [];
    const noOrderArray = [];
    this.selectedEventServiceMap.forEach(val => {
      nameArray.push(val.name);
      // if (!val.order) {
      //   noOrderArray.push(val.name);
      // }
    })
    // if (noOrderArray.length > 0) {
    //   this.messageService.add({ severity: 'error', summary: '信息提示框', detail: `[${noOrderArray}]无调用次序，请点击修改按钮进行添加次序！` });
    //   return;
    // }
    this.endEmitService = nameArray.join('、');
    this.resetFlag++;
    this.dialogParam.display = false;
  }


  // 保存
  saveProsExtend(): void {
    if (!this.selectedProsExtendType.type) {
      showMessage(this.messageService, 'error', '请选择扩展属性类型！');
      return;
    }
    let value = null;
    if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.FLOW_START_EVENT || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.FLOW_END_EVENT || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.FLOW_END_ERROR_EVENT || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.HANG_UP_EVENT || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.RECOVER_EVENT || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.CANCEL_EVENT) {
      Array.isArray(this.selectedEventServiceMap) && this.selectedEventServiceMap.forEach(item => {
        if (!item['callMode']) {
          item = Object.assign(item, { callMode: '1' });
        }
      })
      value = JSON.stringify(this.selectedEventServiceMap);
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.SKIP_FIRST_NODE || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.CHECK_PERSON || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.CONFIR_INFO || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.PRESON_PRO || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.CALL_BACK_JUMP) {
      value = `${this.isSkipFirstNode}`;
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.TIME_TO_START) {
      if (!this.timeToStart || !this.timeToStart.length) {
        showMessage(this.messageService, 'error', '未定义定时启动时间！');
        return;
      }
      // value = `${this.startTime}`
      value = JSON.stringify(this.timeToStart);
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.MAX_TIME) {
      if (!this.maxProcessHandleTime && !this.maxTimeRule.label) {
        showMessage(this.messageService, 'error', '最长处理时间和最长时间规则必选其一！');
        return;
      }
      let valueParams = {};
      this.maxProcessHandleTime ? valueParams = {
        type: '3',
        value: this.maxProcessHandleTime
      } : (this.maxTimeRule.label ? valueParams = {
        type: '2',
        value: JSON.stringify(this.maxTimeRule)
      } : valueParams);
      value = JSON.stringify(valueParams);
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.CHECKINOUT) {
      value = JSON.stringify(this.selectedCheckInOut);
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.OUT_TIME_EXECUTION) {
      value = JSON.stringify(this.selectedtTimeoutHandlingType);
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.START_CONDITION || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.END_CONDITION) {
      let valueParams = {};
      this.startConditonString ? valueParams = {
        type: '3',
        value: this.startConditonString
      } : this.startConditonMapValue.label ? valueParams = {
        type: '2',
        value: JSON.stringify(this.startConditonMapValue)
      } : valueParams;
      value = JSON.stringify(valueParams);
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.CHANNEL_FLOW_CTRL_LIST) {
      value = this.channelFlowList;
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.FLOW_AUTO_REPEAT_PROCESS) {
      value = JSON.stringify(this.selectedprocessMode);
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.IS_FLOW_AUTHORIZE) {
      const obj = {
        isflag: this.isFlowAuthorize.isflag,
        eventId: this.isFlowAuthorize.ids
      };
      value = JSON.stringify(obj);
    } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.NOTIFY_FOR_PROCESS_CREATER) {
      value = JSON.stringify({type: this.selectedProsExtendType.type, descp: this.selectedProsExtendType.descp, eventId: this.eventId});
    }
    const bodyParams = {
      ownId: this.inValue.defId || '',
      ownType: this.modelType,
      propName: this.selectedProsExtendType.type || '',
      propDesc: this.selectedProsExtendType.descp || '',
      propValue: value,
    }
    this.businessNodeManagementService.addNodePropExtends(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.outValue.emit(event);
          }, true, { message: ' 保存成功', errorMsg: '保存失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
            this.outValue.emit(event);
          });
        }
      );
  }




  // 从规则树传上来的值
  callBack(event): void {
    this.dialogParam.display = !event;
    if (this.selectedProsExtendType) {
      if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.START_CONDITION || this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.END_CONDITION) {
        this.startConditonMapValue = { ...event };
      } else if (this.selectedProsExtendType.type === FLOW_MODEL_PRO_TYPE.MAX_TIME) {
        this.maxTimeRule = { ...event };
      }
    }
  }


  // 新增定时启动
  addTimeToStart(timeStart): void {
    timeStart.resetAdd();
    this.timeDisplay = true;
  }


  // 定时启动传上来的值
  callBackTime(event): void {
    this.timeDisplay = !event;
    this.timeToStart.push(event);
    this.timeToStart = this.timeToStart.filter(val => {
      if (val['startWeekStr']) {
        return val.startWeekStr !== this.isRemoveData.startWeekStr;
      } else if (val['startDate']) {
        return val.startDate !== this.isRemoveData.startDate;
      }
    })
  }


  // 删除定时启动
  deleteTimeToStart(item): void {
    this.confirmationService.confirm({
      message: '确认删除吗？',
      header: '信息提示框',
      icon: 'fa fa-question-circle',
      key: 'mapConfirm3',
      accept: () => {
        this.timeToStart = this.timeToStart.filter(val => {
          if (item['startWeekStr']) {
            return (val.startWeekStr !== item.startWeekStr)
          } else if (item['startDate']) {
            return (val.startDate !== item.startDate);
          }
        })
      }
    })
  }

  // 修改定时启动
  modifyTimeToStart(item, timeStart): void {
    this.isRemoveData = { ...item };
    this.isModifyNum++;
    timeStart.startTime = item.startTime;
    timeStart.defaultStartTime = item.startTime;
    this.timeDisplay = true;
    if (item['startWeekStr']) {
      timeStart.timeToStartForm.addControl('weekName', new FormControl('', Validators.required));
      timeStart.timeToStartForm.removeControl('dateName');
      timeStart.defaultSelectedTimeStartType = findObject('week', this.timeToStartType, 'type');
      timeStart.selectedTimeStartType = findObject('week', this.timeToStartType, 'type');
      item.startWeekStr.forEach(element => {
        const obj = findObject(element, this.weeks, 'descp');
        timeStart.defaultSelectedWeeks.push(obj);
        timeStart.selectedWeeks.push(obj);
      });
    } else if (item['startDate']) {
      timeStart.timeToStartForm.addControl('dateName', new FormControl('', Validators.required));
      timeStart.timeToStartForm.removeControl('weekName');
      timeStart.defaultSelectedTimeStartType = findObject('date', this.timeToStartType, 'type');
      timeStart.selectedTimeStartType = findObject('date', this.timeToStartType, 'type');
      timeStart.defaultDateTime = item.startDate;
      timeStart.dateTime = item.startDate;
    }

  }


  // 测试脚本模态框的关闭按钮
  callBackVerifyScript(event): void {
    this.verifyScriptDisplay = !event;
  }


}
