import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { GeneralParamModel, RadioConfig } from '../../process-definition-model';
import { GENERAL_PARAMS_TYPES, ADVANCE_LEVEL } from '../../../common/constant/code.value';
import { GeneralParametersService } from '../general-parameters.service';
import { handleResult, handleError, showMessage } from '../../../common/func/common-func';
import { Pager } from 'app/pages/tzb/workflow/activiti-work-flow/common/constant/config';
import { ConfirmationService } from 'primeng/primeng';

@Component({
  selector: 'app-small-class',
  templateUrl: './small-class.component.html',
  styleUrls: ['./small-class.component.scss']
})

export class SmallClassComponent implements OnInit {

  generalParameterList: GeneralParamModel[] = [];
  // 查询条件的通用参数实例
  queryGeneralParam: GeneralParamModel = new GeneralParamModel();
  //新增或者修改的事项大类实例
  addOrUpdateGeneralParams: GeneralParamModel = new GeneralParamModel();
  // 修改时重置的临时变量
  defaultGeneralParams: GeneralParamModel = new GeneralParamModel();
  // 分页
  pager: Pager = new Pager();
  // 参数类型
  generalParamTypes = GENERAL_PARAMS_TYPES;

  dialogParam = {
    display: false,
    isAdd: false,
    isModify: false,
    headerTitle: ''
  };

  //表单
  smallClassForm: FormGroup;
  // 控制流程定义模态框显隐
  processDisplay = false;
  // 控制事项大类模态框显隐
  classDisplay = false;
  // 大类ID
  bigClassId: string;
  // 修改重置的临时变量
  defaultBigClassId: string;

  // 优先级
  advanceConfig = new RadioConfig();
  ADVANCE_LEVEL = ADVANCE_LEVEL;
  sortDialogParam = {
    display: false,
    headerTitle: '',
    showModal: '0'
  };
  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private generalParametersService: GeneralParametersService,
  ) {
    this.createValidators();
    this.advanceConfig.name = 'paramUrgent';
    this.advanceConfig.options = this.ADVANCE_LEVEL;
  }


  ngOnInit() {
  }


  createValidators() {
    this.smallClassForm = this.fb.group({
      'paramNameName': ['', Validators.required],
      'belongLargeCodeName': ['', Validators.required],
      'belongLargeName': ['', Validators.required],
      'processCodeName': ['', Validators.required],
      'paramCodeName': ['', Validators.required],
      'processNameName': ['', Validators.required],
      'paramUrgent': ['0', Validators.required]
    });

  }


  // 重置查询
  reset(): void {
    this.queryGeneralParam.paramCode = null;
    this.queryGeneralParam.paramName = null;
    this.queryGeneralParam.paramObjCode = null;
    this.queryGeneralParam.paramObjName = null;
    this.query();
  }

  // 按照搜索条件查询
  query() {
    this.pager.first = '0';
    this.pager.page = '1';
    this.getGeneralParamList('01', this.queryGeneralParam.paramCode, this.queryGeneralParam.paramName, this.queryGeneralParam.paramObjCode, this.queryGeneralParam.paramObjName);
  }


  // 获取事项大类列表
  getGeneralParamList(paramType = '01', paramCode?: string, paramName?: string, paramObjCode?: string, paramObjName?: string): void {
    const bodyParams = {
      paramType,
      paramCode,
      paramName,
      paramObjCode,
      paramObjName,
      pageRows: parseInt(this.pager.rows, 10),
      curPage: parseInt(this.pager.page, 10)
    };
    this.generalParametersService.listWfParam(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.generalParameterList = data.pageData[0].result;
          this.pager.totalRecords = data.pageData[0].totalRows;
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {

        });
      }
      );
  }

  // 分页
  paginate(event) {
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.getGeneralParamList('01', this.queryGeneralParam.paramCode, this.queryGeneralParam.paramName, this.queryGeneralParam.paramObjCode, this.queryGeneralParam.paramObjName);  }

  //  新增事项大类按钮
  add(): void {
    this.resetAdd();
    this.dialogParam.headerTitle = '新增事项小类';
    this.dialogParam.isModify = false;
    this.dialogParam.isAdd = true;
    this.dialogParam.display = true;
  }

  // 修改事项大类按钮
  modify(item): void {
    this.addOrUpdateGeneralParams = { ...item };
    this.defaultGeneralParams = { ...item };
    this.bigClassId = item.upid;
    this.defaultBigClassId = item.upid;
    this.dialogParam.headerTitle = '修改事项小类';
    this.dialogParam.isModify = true;
    this.dialogParam.isAdd = false;
    this.dialogParam.display = true;
  }


  // 保存
  saveOrUpate(): void {
    const isHas = this.generalParameterList.some(item => {
      return item.paramCode === this.addOrUpdateGeneralParams.paramCode;
    })
    if (this.dialogParam.isAdd && isHas) {
      showMessage(this.messageService, 'error', `小类编码已存在`);
      return;
    }
    let bodyParams = {
      paramType: '01',   // 事项小类
      paramCode: this.addOrUpdateGeneralParams.paramCode,
      paramName: this.addOrUpdateGeneralParams.paramName,
      paramObjCode: this.addOrUpdateGeneralParams.paramObjCode,
      paramObjName: this.addOrUpdateGeneralParams.paramObjName,
      processCode: this.addOrUpdateGeneralParams.processCode,
      processName: this.addOrUpdateGeneralParams.processName,
      paramUrgent: this.addOrUpdateGeneralParams.paramUrgent,
      paramOrder: this.addOrUpdateGeneralParams.paramOrder
    };
    if (this.dialogParam.isAdd) {
      bodyParams = Object.assign(bodyParams, {
        upid: this.bigClassId
      })
      this.generalParametersService.addWfParam(bodyParams)
        .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.reset();
            this.getGeneralParamList();
            this.dialogParam.display = false;
            this.dialogParam.isAdd = false;
            this.dialogParam.isModify = false;
          }, true, { message: '保存成功！', errorMsg: '保存失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
        );
    } else if (this.dialogParam.isModify) {
      bodyParams = Object.assign(bodyParams, {
        upid: this.bigClassId,
        id: this.addOrUpdateGeneralParams.id,
      })
      this.generalParametersService.updateWfParam(bodyParams)
        .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.reset();
            this.getGeneralParamList();
            this.dialogParam.display = false;
            this.dialogParam.isAdd = false;
            this.dialogParam.isModify = false;
          }, true, { message: '更新成功！', errorMsg: '更新失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
        );
    }

  }

  resetSaveOrUpate() {
    this.dialogParam.isAdd && this.resetAdd();
    this.dialogParam.isModify && this.resetModify();
  }

  // 重置新增
  resetAdd(): void {
    this.addOrUpdateGeneralParams.paramName = null;
    this.addOrUpdateGeneralParams.paramCode = null;
    this.addOrUpdateGeneralParams.paramObjCode = null;
    this.addOrUpdateGeneralParams.paramObjName = null;
    this.addOrUpdateGeneralParams.processCode = null;
    this.addOrUpdateGeneralParams.processName = null;
    this.bigClassId = null;
    this.addOrUpdateGeneralParams.paramUrgent = '0';
  }

  // 重置修改
  resetModify(): void {
    this.addOrUpdateGeneralParams = { ...this.defaultGeneralParams };
    this.bigClassId = this.defaultBigClassId;
  }

  // 删除通用参数
  delete(item) {
    this.confirmationService.confirm({
      message: '确认删除吗？',
      header: '信息提示框',
      icon: 'fa fa-question-circle',
      accept: () => {
        const bodyParams = {
          id: item.id || ''
        };
        this.generalParametersService.deletleWfParam(bodyParams)
          .subscribe(
          data => {
            handleResult(data, this.messageService, () => {
              this.getGeneralParamList();
            }, true, { message: '删除成功！', errorMsg: '删除失败' });
          },
          err => {
            handleError(err, this.messageService, () => {
            });
          }
          );
      }
    });
  }

  // 查找大类/小类按钮
  queryLargeClass(bigClass): void {
    bigClass.queryLargeClass();
    this.classDisplay = true;
  }

  // 从事项大类/小类传上来的
  callBackClass(event): void {
    this.classDisplay = !event;
    this.addOrUpdateGeneralParams.paramObjCode = event.data.paramCode;
    this.addOrUpdateGeneralParams.paramObjName = event.data.paramName;
    this.bigClassId = event.data.id;
  }


  //查询流程编码按钮
  queryProcessCode(defineList): void {
    defineList.reset();
    defineList.queryProcessCode();
    this.processDisplay = true;
  }


  // 从流程定义列表传上来的
  callBackDefine(event): void {
    this.processDisplay = !event;
    this.addOrUpdateGeneralParams.processCode = event.data.defId;
    this.addOrUpdateGeneralParams.processName = event.data.defName;
  }
  // 排序调整
  adjustSorting () {
    this.sortDialogParam.display = true;
    this.sortDialogParam.headerTitle = '事项小类排序';
    this.sortDialogParam.showModal = '1';
  }
  // 关闭排序调整弹框
  sortDialogHidden () {
    this.sortDialogParam.showModal = '0';
    this.getGeneralParamList('01', this.queryGeneralParam.paramCode, this.queryGeneralParam.paramName);
  }
}
