import { Injectable } from '@angular/core';
import { HttpService } from '../../common/service/http.service';
// import { RTS_HTTP_URL } from '../../common/constant/config';
import { PROCESS_NODE_BUSINESS_STATUS_API } from '../process-definition.constant';
import { RTS_HTTP_URL } from 'app/@uisftech/common/service/http.constant';

@Injectable()
export class BusinessStatusService {

  constructor(
    private httpService: HttpService
  ) { }

  // 查询
  listWfNodeState = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_NODE_BUSINESS_STATUS_API.listWfNodeState, params, null);

  // 删除
  deletleWfNodeState = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_NODE_BUSINESS_STATUS_API.deletleWfNodeState, params, null);

  // 新增
  addWfNodeState = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_NODE_BUSINESS_STATUS_API.addWfNodeState, params, null);

  // 修改
  updateWfNodeState = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_NODE_BUSINESS_STATUS_API.updateWfNodeState, params, null);

}
