import { Component, Input, OnChanges, Output, EventEmitter } from "@angular/core";
import { MessageService } from "primeng/components/common/messageservice";
import { ProcessMaintenanceService } from "../process-maintenance.service";
import { handleResult, handleError, findObject, isJsonString } from "../../../common/func/common-func";
import { FLOW_MODEL_PRO_TYPE, BUSINESS_SIGN_APPROVE_PRO_TYPE, AUTO_NODE_PRO_TYPE, SUB_NODE_PRO_TYPE } from "../../process-definition-model";
import { EVENT_SERVICE_CALL_MODE, TIMEOUT_WARNING_TYPE, TASK_NOTIFY_TYPE } from "../../../common/constant/code.value";

@Component({
  selector: 'app-export-list',
  templateUrl: './export-list.component.html'
})
export class ExportListComponent implements OnChanges {
  @Input()
  ids: any;
  exportList= [];
  // 流程分类列表
  processDirectorys = [];
  // 调用模式
  eventServiceCallMode = EVENT_SERVICE_CALL_MODE;
  // 预警消息类型
  timeOutWarningType = TIMEOUT_WARNING_TYPE;
  //  任务通知机制
  taskNotifyTypes = TASK_NOTIFY_TYPE;
  type: string;
  @Output()
  display = new EventEmitter<boolean>();
  constructor(
    private messageService: MessageService,
    private processMaintenanceService: ProcessMaintenanceService
  ) {

  }
  ngOnChanges() {
    this.exportList = [];
    this.getFlowList();
  }
  getFlowList(){
    if (this.ids){
      this.processMaintenanceService.wfDefCollect({id: this.ids})
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            const pageData = data.pageData[0];
            this.processDirectorys = [];
            pageData.forEach(item => {
              const obj = {
                systemName: item.definition.defName,
                systemId: item.definition.id,
                tableData: item.defProperty || [],
                children: [],
                expanded: true
              };
              item.property.forEach(item => {
                const childrenObj = {
                  systemName: item.nodeName,
                  systemId: item.nodeId,
                  tableData: item.wfModelPropExtends || [],
                  children: []
                }
                obj['children'].push(childrenObj);
              });
              this.processDirectorys.push(obj);
            });
          }, true, { message: ' 查询成功', errorMsg: '查询失败' });
        },
        err => {
          handleError(err, this.messageService, () => {

          });
        }
      );
    }
  }
  // 选择左边的树
  onNodeSelect(event) {
    const clickNodeData = event.node;
    this.type = event.node.parent? 'node': 'flow';
    this.exportList = this.reGroupData(clickNodeData.tableData, this.type);
  }
  // 对table的数据做处理
  reGroupData(arr: Array<object>, type: string) {
    if (type === 'flow') {
      arr.forEach(item => {
        if (item['propName'] === FLOW_MODEL_PRO_TYPE.FLOW_START_EVENT || item['propName'] === FLOW_MODEL_PRO_TYPE.FLOW_END_EVENT || item['propName'] === FLOW_MODEL_PRO_TYPE.FLOW_END_ERROR_EVENT || item['propName'] === FLOW_MODEL_PRO_TYPE.HANG_UP_EVENT || item['propName'] === FLOW_MODEL_PRO_TYPE.RECOVER_EVENT || item['propName'] === FLOW_MODEL_PRO_TYPE.CANCEL_EVENT) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            const nameArray = [];
            value.forEach((item, index) => {
              if (item.order && item.callMode) {
                const mode = findObject(item.callMode, this.eventServiceCallMode, 'type');
                const str = `(${index + 1})、${item.name}:调用次序为${item.order};调用模式为${mode.descp}`;
                nameArray.push(str);
              } else {
                const str = `(${index + 1})、${item.name}:调用次序(无);调用模式(无)`;
                nameArray.push(str);
              }
            });
            item['extendType'] = nameArray.join('、');
          }
        } else if (item['propName'] === FLOW_MODEL_PRO_TYPE.SKIP_FIRST_NODE || item['propName'] === FLOW_MODEL_PRO_TYPE.CHECK_PERSON || item['propName'] === FLOW_MODEL_PRO_TYPE.CONFIR_INFO || item['propName'] === FLOW_MODEL_PRO_TYPE.PRESON_PRO || event.type === FLOW_MODEL_PRO_TYPE.CALL_BACK_JUMP) {
          if (item['propValue']) {
            item['extendType'] = item['propValue'] === 'true' ? '是' : '否';
          }
        } else if (item['propName'] === FLOW_MODEL_PRO_TYPE.TIME_TO_START) {
          if (item['propValue'] && item['propValue'] !== 'undefined') {
            // item['extendType'] = `${item['propValue']}天`;
            const value = JSON.parse(item['propValue']);
            let str = '';
            let str1 = '';
            Array.isArray(value) && value.forEach((item, index) => {
              if (item['startWeek']) {
                str += `(${index + 1})、${item.startWeekStr}的${item.startTime}启动；`
              } else if (item['startDate']) {
                str1 += `(${index + 1})、每个月的${item.startDate}号${item.startTime}启动；`
              }
            });
            item['extendType'] = str + str1;
          }
        } else if (item['propName'] === FLOW_MODEL_PRO_TYPE.MAX_TIME) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            if (isJsonString(value.value)) {
              const jsonVal = JSON.parse(value.value);
              item['extendType'] = jsonVal.label
            } else {
              item['extendType'] = `${value.value}小时`;
            }
          }
        } else if (item['propName'] === FLOW_MODEL_PRO_TYPE.OUT_TIME_EXECUTION 
          || item['propName'] === FLOW_MODEL_PRO_TYPE.CHECKINOUT
          || item['propName'] === FLOW_MODEL_PRO_TYPE.FLOW_AUTO_REPEAT_PROCESS  
          ) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue'])
            item['extendType'] = value.descp;
          }
        } else if (item['propName'] === FLOW_MODEL_PRO_TYPE.START_CONDITION || item['propName'] === FLOW_MODEL_PRO_TYPE.END_CONDITION) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            if (isJsonString(value.value)) {
              const jsonVal = JSON.parse(value.value);
              item['extendType'] = jsonVal.label
            } else {
              item['extendType'] = value.value;
            }
          }
        } else if (item['propName'] === FLOW_MODEL_PRO_TYPE.CHANNEL_FLOW_CTRL_LIST) {
          if (item['propValue']) {
            item['extendType'] = item['propValue'];
          }
        }
      });
    } else if (type === 'node') {
      arr.forEach(item => {
        const allextendPro: string[] = [];
        if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.USERRULE) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            item['extendType'] = value.label;
          }
        } else if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.FUNCTIONLIST) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            if (isJsonString(value.value)) {
              const jsonVal = JSON.parse(value.value);
              item['extendType'] = jsonVal.label
            } else {
              item['extendType'] = value.value;
            }
          }
        } else if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.TIME_START) {
          if (item['propValue']) {
            item['extendType'] = `${item['propValue']}天`;
          }
        } else if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_OPTION_TIME) {
          const value = JSON.parse(item['propValue']);
          if (item['propValue']) {
            if (value.timeOut.value) {
              const unit = value.timeOut.dataType === '0' ? '小时': '天';
              item['extendType'] = value.timeOut.value + unit;
            } else {
              item['extendType'] = value.urule.label;
            }
          }
        } else if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_OPTION_TIME_RULE) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            item['extendType'] = value.label;
          }
        } else if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.MAX_CHECK_OUT_NUM) {
          if (item['propValue']) {
            item['extendType'] = `${item['propValue']}笔`;
          }
        } else if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.SELECT_OPTION) {
          if (item['propValue']) {
            item['extendType'] = item['propValue'];
          }
        } else if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.ACCESSORY_PROP || item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.NODE_AUTO_REPEATP_ROCESS) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            item['extendType'] = value.descp;
          }
        } else if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_ALLOCATION_TYPE) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            item['extendType'] = value.type === '2' ? `${value.descp}${value.percentage}` : value.descp;
          }
        }
        else if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.TIME_OUT_OPRATE) {
          if (item['propValue']) {
            const nameArray = [];
            const value = JSON.parse(item['propValue']);
            if (Object.prototype.toString.call(value.type) == '[object Object]') {
              if (value['value']) {
                item['extendType'] = `${value.type.descp} + ${value.value.label}`;
              } if (value['urule']) {
                item['extendType'] = `${value.type.descp} + ${value.urule.label}`;
              } else if (value['event']) {
                value.event.forEach((item, index) => {
                  if (item.order && item.callMode) {
                    const mode = findObject(item.callMode, this.eventServiceCallMode, 'type');
                    const str = `(${index + 1})、${item.name}:调用次序为${item.order};调用模式为${mode.descp}`;
                    nameArray.push(str);
                  } else {
                    const str = `(${index + 1})、${item.name}:调用次序(无);调用模式(无)`;
                    nameArray.push(str);
                  }
                });
                item['extendType'] = `${value.type.descp} +${nameArray.join('、')}`;
              } else if(value.type.type === '9') {
                const unit = value.timeOut.dataType === '0' ? '小时': '天';
                const screenSpace = value.timeOut.value + unit;
                item['extendType'] = `${value.type.descp} + ${screenSpace}`;
              }
            } else {
              item['extendType'] = `${value.descp}`;
            }
          }
        } else if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.TIME_OUT_WARN) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            let str = null;
            if (value['time']) {
              str = `超时${value.time}小时预警`;
            }
            if (value['template']) {
              // const template = JSON.parse(value.template);
              const messageType = findObject(value.template.type, this.timeOutWarningType, 'type');
              str += `   使用${messageType.descp}`;
            }
            item['extendType'] = str;
          }
        } else if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.NOTIFY_TYPE) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            const messageType = findObject(value.type, this.taskNotifyTypes, 'type');
            item['extendType'] = messageType.descp;
          }
        } else if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.START_TASK_EVENT 
          || item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.END_TASK_EVENT
          || item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.CANCEL_TASK_EVENT
          || item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.RECALL_TASK_EVENT
          || item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.BACK_TASK_EVENT) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            const nameArray = [];
            value.forEach((item, index) => {
              if (item.order && item.callMode) {
                const mode = findObject(item.callMode, this.eventServiceCallMode, 'type');
                const str = `(${index + 1})、${item.name}:调用次序为${item.order};调用模式为${mode.descp}`;
                nameArray.push(str);
              } else {
                const str = `(${index + 1})、${item.name}:调用次序(无);调用模式(无)`;
                nameArray.push(str);
              }
            });
            item['extendType'] = nameArray.join('、');
          }
        } else if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.CHANNEL_NODE_CTRL_LIST) {
          if (item['propValue']) {
            item['extendType'] = item['propValue'];
          }
        } else if (item['propName'] === AUTO_NODE_PRO_TYPE.AUTO_NODE_SERVERICE_CALL) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            item['extendType'] = value.name;
          }
        } else if (item['propName'] === AUTO_NODE_PRO_TYPE.NODE_ENTRY_CONDITION
          || item['propName'] === AUTO_NODE_PRO_TYPE.NODE_EXIT_CONDITION
          || item['propName'] === SUB_NODE_PRO_TYPE.NODE_EXIT_CONDITION
          || item['propName'] === SUB_NODE_PRO_TYPE.NODE_ENTRY_CONDITION
          || item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.NODE_ENTRY_CONDITION
          || item['propName'] === SUB_NODE_PRO_TYPE.NODE_EXIT_CONDITION
          || item['propName'] === SUB_NODE_PRO_TYPE.NODE_ENTRY_CONDITION
          || item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.NODE_EXIT_CONDITION) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            if (isJsonString(value.value)) {
              const jsonVal = JSON.parse(value.value);
              item['extendType'] = jsonVal.label
            } else {
              item['extendType'] = value.value;
            }
          }
        } else if (item['propName'] === AUTO_NODE_PRO_TYPE.START_TASK_EVENT || item['propName'] === AUTO_NODE_PRO_TYPE.END_TASK_EVENT) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            const nameArray = [];
            value.forEach((item, index) => {
              if (item.order && item.callMode) {
                const mode = findObject(item.callMode, this.eventServiceCallMode, 'type');
                const str = `(${index + 1})、${item.name}:调用次序为${item.order};调用模式为${mode.descp}`;
                nameArray.push(str);
              } else {
                const str = `(${index + 1})、${item.name}:调用次序(无);调用模式(无)`;
                nameArray.push(str);
              }
            });
            item['extendType'] = nameArray.join('、');
          }
        } else if (item['propName'] === SUB_NODE_PRO_TYPE.SUB_FLOW_SELECTION_CALL) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            // const nameArray = [];
            // value.forEach(item => {
            //   nameArray.push(item.defId);
            // });
            item['extendType'] = `流程编码:${value[0].defId},流程名称:${value[0].defName}`;
          }
        } else if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.CHECK_IN_OUT_VALUE) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            const flag = value.checkInFlag === '3'? '领用后不可退回任务池': '领用后可退回任务池';
            item['extendType'] = flag + '；' + '最大可领用数' + value.maxCheckOutNum + '笔；'
          }
        } else if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.CHANNEL_NODE_QUE_LIST) {
          item['extendType'] = item['propValue'] || '';
        } else if (item['propName'] === BUSINESS_SIGN_APPROVE_PRO_TYPE.TASK_RECALL_CRTL) {
          if (item['propValue']) {
            const value = JSON.parse(item['propValue']);
            const flag = value.crtlType === '0'? '下节任务未处理可追回': '实例未结束可追回';
            item['extendType'] = flag + '：' + value.ctrlChannelList
          }
        }
      });
    }
    return arr;
  }
}