import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { DictionaryTypeModel, GeneralParamModel } from '../../process-definition-model';
import { GENERAL_PARAMS_TYPES, BUSINESS_PARAMS_TYPES } from '../../../common/constant/code.value';
import { GeneralParametersService } from '../general-parameters.service';
import { handleResult, handleError, findObject, showMessage } from '../../../common/func/common-func';
import { Pager } from 'app/pages/tzb/workflow/activiti-work-flow/common/constant/config';
import { ConfirmationService } from 'primeng/primeng';

@Component({
  selector: 'app-business-param',
  templateUrl: './business-param.component.html',
  styleUrls: ['./business-param.component.scss']
})

export class BusinessParamComponent implements OnInit {

  generalParameterList: GeneralParamModel[] = [];
  // 查询条件的通用参数实例
  queryGeneralParam: GeneralParamModel = new GeneralParamModel();
  //新增或者修改的事项大类实例
  addOrUpdateGeneralParams: GeneralParamModel = new GeneralParamModel();
  // 修改时重置的临时变量
  defaultGeneralParams: GeneralParamModel = new GeneralParamModel();
  // 分页
  pager: Pager = new Pager();
  // 参数类型
  generalParamTypes = GENERAL_PARAMS_TYPES;

  dialogParam = {
    display: false,
    isAdd: false,
    isModify: false,
    headerTitle: ''
  };

  bigClassForm: FormGroup;
  // 业务参数类型
  businessParamsType = BUSINESS_PARAMS_TYPES;
  // 选中业务参数类型
  selectedBusinessParamsType: DictionaryTypeModel = new DictionaryTypeModel();
  // 修改时重置的临时变量
  defalutBusinessParamsType: DictionaryTypeModel = new DictionaryTypeModel();
  // 控制流程定义列表
  processDisplay = false;


  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private generalParametersService: GeneralParametersService,
  ) {
    this.createValidators();
  }

  ngOnInit() {

  }

  createValidators() {
    this.bigClassForm = this.fb.group({
      'processCodeName': ['', Validators.required],
      'paramCodeName': ['', [Validators.required, Validators.maxLength(32)]],
      'processCodeNameName': ['', Validators.required],
      'paramNameName': ['', Validators.required],
      'businessParamName': ['', Validators.required]
    });

  }


  // 重置查询
  resetBigClass(): void {
    this.queryGeneralParam.paramCode = null;
    this.queryGeneralParam.paramName = null;
    this.queryBigClass();
  }

  // 按照搜索条件查询
  queryBigClass() {
    this.pager.first = '0';
    this.pager.page = '1';
    this.getGeneralParamList('C0', this.queryGeneralParam.paramCode, this.queryGeneralParam.paramName);
  }


  // 获取事项大类列表
  getGeneralParamList(paramType = 'C0', paramCode?: string, paramName?: string): void {
    const bodyParams = {
      paramType,
      paramCode,
      paramName,
      pageRows: parseInt(this.pager.rows, 10),
      curPage: parseInt(this.pager.page, 10)
    };
    this.generalParametersService.listWfParam(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.generalParameterList = data.pageData[0].result;
          this.pager.totalRecords = data.pageData[0].totalRows;
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {

        });
      }
      );
  }

  // 分页
  paginate(event) {
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.getGeneralParamList('C0', this.queryGeneralParam.paramCode, this.queryGeneralParam.paramName);
  }


  //  新增业务比较参数按钮
  add(): void {
    this.resetAdd();
    this.dialogParam.headerTitle = '新增业务比较参数';
    this.dialogParam.isModify = false;
    this.dialogParam.isAdd = true;
    this.dialogParam.display = true;
  }



  // 修改业务参数按钮
  modifyBigClass(item): void {
    this.addOrUpdateGeneralParams = { ...item };
    this.addOrUpdateGeneralParams.processCode = item.paramCode;
    this.addOrUpdateGeneralParams.processName = item.paramName;
    this.selectedBusinessParamsType = findObject(item.paramObjType, this.businessParamsType, 'type');
    this.defaultGeneralParams = { ...item };
    this.dialogParam.headerTitle = '修改业务比较参数';
    this.dialogParam.isModify = true;
    this.dialogParam.isAdd = false;
    this.dialogParam.display = true;
  }


  // 保存
  saveOrUpate(): void {
    const isHas = this.generalParameterList.some(item => {
      return item.paramObjCode === this.addOrUpdateGeneralParams.paramObjCode;
    })
    if (this.dialogParam.isAdd && isHas) {
      showMessage(this.messageService, 'error', `业务参数编码已存在`);
      return;
    }
    let bodyParams = {
      paramType: 'C0',   // 业务比较参数
      // paramCode: this.addGeneralParams.paramCode,
      // paramName: this.addGeneralParams.paramName,
      paramObjCode: this.addOrUpdateGeneralParams.paramObjCode,
      paramObjName: this.addOrUpdateGeneralParams.paramObjName,
      paramObjType: this.selectedBusinessParamsType.type,
      processCode: this.addOrUpdateGeneralParams.processCode,
      processName: this.addOrUpdateGeneralParams.processName
    };
    if (this.dialogParam.isAdd) {
      bodyParams = Object.assign(bodyParams, {
        upid: this.addOrUpdateGeneralParams.id
      })
      this.generalParametersService.addWfParam(bodyParams)
        .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.resetBigClass();
            this.getGeneralParamList();
            this.dialogParam.display = false;
            this.dialogParam.isAdd = false;
            this.dialogParam.isModify = false;
          }, true, { message: '保存成功！', errorMsg: '保存失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
        );
    } else if (this.dialogParam.isModify) {
      bodyParams = Object.assign(bodyParams, {
        upid: this.addOrUpdateGeneralParams.upid,
        id: this.addOrUpdateGeneralParams.id,
      })
      this.generalParametersService.updateWfParam(bodyParams)
        .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.resetBigClass();
            this.getGeneralParamList();
            this.dialogParam.display = false;
            this.dialogParam.isAdd = false;
            this.dialogParam.isModify = false;
          }, true, { message: '更新成功！', errorMsg: '更新失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
        );
    }

  }







  reset() {
    this.dialogParam.isAdd && this.resetAdd();
    this.dialogParam.isModify && this.resetModify();
  }


  // 重置新增
  resetAdd(): void {
    this.addOrUpdateGeneralParams.processCode = null;
    this.addOrUpdateGeneralParams.processName = null;
    this.addOrUpdateGeneralParams.paramObjCode = null;
    this.selectedBusinessParamsType = findObject('0', this.businessParamsType, 'type');
    this.addOrUpdateGeneralParams.paramObjName = null;
  }

  // 重置修改
  resetModify(): void {
    this.addOrUpdateGeneralParams = { ...this.defaultGeneralParams };
    this.selectedBusinessParamsType = findObject(this.defaultGeneralParams.paramObjType, this.businessParamsType, 'type');
    this.addOrUpdateGeneralParams.processCode = this.defaultGeneralParams.paramCode;
  }


  // 删除通用参数
  deleteBigClass(item) {
    this.confirmationService.confirm({
      message: '确认删除吗？',
      header: '信息提示框',
      icon: 'fa fa-question-circle',
      accept: () => {
        const bodyParams = {
          id: item.id || ''
        };
        this.generalParametersService.deletleWfParam(bodyParams)
          .subscribe(
          data => {
            handleResult(data, this.messageService, () => {
              this.getGeneralParamList();
            }, true, { message: '删除成功！', errorMsg: '删除失败' });
          },
          err => {
            handleError(err, this.messageService, () => {
            });
          }
          );
      }
    });

  }

  //查询流程编码按钮
  queryProcessCode(defineList): void {
    defineList.reset();
    defineList.queryProcessCode();
    this.processDisplay = true;
  }


  // 从流程定义列表传上来的
  callBackDefine(event): void {
    this.processDisplay = !event;
    this.addOrUpdateGeneralParams.processCode = event.data.defId;
    this.addOrUpdateGeneralParams.processName = event.data.defName;
  }





}
