import { OnInit, Component } from "@angular/core";
import { RadioConfig } from "../../../process-definition-model";
import { SORT_TYPE_OPTIONS } from "../../../../common/constant/code.value";
import { GeneralParametersService } from "../../general-parameters.service";
import { MessageService } from "primeng/components/common/messageservice";
import { handleResult, handleError } from "../../../../common/func/common-func";

@Component({
  selector: 'small-class-adjust-sorting',
  templateUrl: './adjust-sorting.component.html',
  styleUrls: ['./adjust-sorting.component.scss']
})

export class SmallClassAdjustSortingComponent implements OnInit { 
  sortRadioConfig = new RadioConfig();
  orderType: string;
  SORT_TYPE_OPTIONS = SORT_TYPE_OPTIONS;
  // 依照大类编码排序
  sortField = 'paramObjCode';
  treeData = [];
  // 记录哪个是已展开的
  expandedRow: string='';
  constructor(
    private generalParametersService: GeneralParametersService,
    private messageService: MessageService
  ) {
    this.sortRadioConfig.name = 'orderType';
    this.sortRadioConfig.options = this.SORT_TYPE_OPTIONS;
  }
  
  ngOnInit() {
    this.orderType = this.SORT_TYPE_OPTIONS[0].type;
    this.getGeneralParamList();
  }
  // 修改排序
  updateOrder(orderType,currentRow) {
    // 当前行数据和前一行的paramOrder值交换
    let  pereveRow;
    const index = this.checkParamObjCodeExist(currentRow.parent.children, currentRow.data.paramCode,'paramCode');
    // 记录当前是在哪个大类下操作的
    this.expandedRow = currentRow.data.paramObjCode;
    if (orderType === 'asc') {
     pereveRow =  currentRow.parent.children[index-1];
    } else if (orderType === 'desc') {
      pereveRow = currentRow.parent.children[index+1];
    } else {
      return;
    }
    console.log(this.treeData);
    this.updateOrderRequest({id:currentRow.data.id + ',' + pereveRow.data.id });
  }
  // 获取事项小类列表
  getGeneralParamList(paramType = '01',orderType = this.orderType): void {
    const bodyParams = {
      paramType,
      orderType
    };
    this.generalParametersService.listWfParam(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          const list = data.pageData[0].result;
          this.treeData = [];
          list.forEach((item) => {
            const flag = this.checkParamObjCodeExist(this.treeData, item.paramObjCode, 'paramObjCode');
            if (flag < 0 ) {
              // 表示当前treeData无此大类
              this.treeData.push({
                data: {paramObjName: item.paramObjName, paramObjCode: item.paramObjCode,  paramCode:'', paramName: '', paramOrder: ''},
                children: [{data: item}]
              });
            } else {
              // 表示当前treeData已有此大类 此时flag代表此大类在treeData所处的下标
              this.treeData[flag]['children'].push({data: item});
            }
          });
          // 寻找已展开的行
          this.treeData.forEach(item=>{
            if (item.data.paramObjCode === this.expandedRow) {
              item.expanded = true;
            }
          });
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {

        });
      }
    );
  }
  // 更新排序值
  updateOrderRequest (bodyParams) {
    this.generalParametersService.order(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.getGeneralParamList();
        }, true, { message: '更新成功！', errorMsg: '更新失败' });
      },
      err => {
        handleError(err, this.messageService, () => {

        });
      }
    );
  }
  // 是否显示升序按钮
  isShowAsc(rowData) {
    const parent = rowData.parent;
    if (parent) {
      return this.checkParamObjCodeExist(parent.children, rowData['data']['paramCode'], 'paramCode')


 > 0;
    } else {
      return false;
    }
  }
  // 是否显示降序按钮
  isShowDesc(rowData) {
    const parent = rowData.parent;
    if (parent) {
      return this.checkParamObjCodeExist(parent.children, rowData['data']['paramCode'], 'paramCode')


 < parent['children'].length-1;
    } else {
      return false;
    }
  }

  // 检查列表中是否已经有此大类or小类，若有，是第几个
  checkParamObjCodeExist(list: Object[], paramObjCode: string, filed: string) {
    let index = -1;
    list.forEach((item, idx) => {
      if (item['data'][filed] && item['data'][filed] === paramObjCode) {
        index = idx;
      }
    });
    return index;
  }
}