import { Component, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { GeneralParametersService } from '../../general-parameters.service';
import { handleResult, handleError } from '../../../../common/func/common-func';

@Component({
  selector: 'app-add-channel-map',
  templateUrl: './add-channel-map.component.html',
  styleUrls: ['./add-channel-map.component.scss']
})
export class AddChannelMapComponent implements OnChanges {

  msg = [];

  @Input() isShow: boolean;

  @Output() outValueEmitter: EventEmitter<any> = new EventEmitter();
  // 表单
  channelMapForm: FormGroup;

  isSubmiting = false;



  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private generalParametersService: GeneralParametersService,
  ) { }


  ngOnChanges() {
    this.isSubmiting = false;
    this.createValidators();
  }

  createValidators() {
    this.channelMapForm = this.fb.group({
      'paramCode': ['', Validators.required],
      'paramName': ['', Validators.required],
      'paramObjCode': ['', Validators.required],
      'paramObjName': ['', Validators.required]
    });

  }

  // 保存
  preserveClick() {
    this.isSubmiting = true;
    // 处理是否提交
    for (const key in this.channelMapForm.controls) {
      if (this.channelMapForm.controls.hasOwnProperty(key)) {
        this.channelMapForm.controls[key].markAsDirty();
      }
    }

    // 表单校验不通过  不提交
    if (!this.channelMapForm.valid) {
      this.isSubmiting = false;
      return;
    }
    // 提交服务
    const param = Object.assign(this.channelMapForm.value, { paramType: 'H0' })
    this.generalParametersService.addWfParam(param)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.reset();
            this.outValueEmitter.emit(event)
          }, true, { message: '保存成功！', errorMsg: '保存失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
            this.outValueEmitter.emit(event)
          });
        }
      );
  }


  // 重置
  reset() {
    this.channelMapForm.get('paramCode').setValue(null);
    this.channelMapForm.get('paramName').setValue(null);
    this.channelMapForm.get('paramObjCode').setValue(null);
    this.channelMapForm.get('paramObjName').setValue(null);
  }



}





