import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { GeneralParametersService } from '../../general-parameters.service';
import { MessageService } from 'primeng/components/common/messageservice';
import { GENERAL_PARAMS_TYPE, GeneralParamModel } from '../../../process-definition-model';
import { GENERAL_PARAMS_TYPES } from '../../../../common/constant/code.value';
import { handleResult, handleError } from '../../../../common/func/common-func';
import { Pager } from 'app/pages/tzb/workflow/activiti-work-flow/common/constant/config';

@Component({
  selector: 'app-item-class',
  templateUrl: './item-class.component.html',
  styleUrls: ['./item-class.component.scss']
})

export class ItemClassComponent implements OnInit {
  msg = [];
  @Input() inValue: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();

  // 归属大类列表
  belongLargeList: GeneralParamModel[] = [];
  // 参数类型
  generalParamTypes = GENERAL_PARAMS_TYPES;
  // 分页
  pager: Pager = new Pager();
  // 查询条件的通用参数实例
  queryGeneralParam: GeneralParamModel = new GeneralParamModel();

  constructor(
    private messageService: MessageService,
    private generalParametersService: GeneralParametersService,
  ) { }

  ngOnInit() {
  }


  // 查找归属大类/小类
  queryLargeClass(paramCode?: string, paramName?: string): void {
    if (this.inValue) {
      const bodyParams = {
        paramType: this.inValue,
        paramCode,
        paramName,
        pageRows: parseInt(this.pager.rows, 10),
        curPage: parseInt(this.pager.page, 10)
      };
      this.generalParametersService.listWfParam(bodyParams)
        .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.belongLargeList = data.pageData[0].result;
            this.pager.totalRecords = data.pageData[0].totalRows;
          }, true, { errorMsg: '查询失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
        );
    }

  }


  selectLargeClass(event): void {
    this.outValue.emit(event);
  }


  // 重置查询
  resetBigClass(): void {
    this.queryGeneralParam.paramCode = null;
    this.queryGeneralParam.paramName = null;
  }

  // 按照搜索条件查询
  queryBigClass() {
    this.pager.first = '0';
    this.pager.page = '1';
    this.queryLargeClass(this.queryGeneralParam.paramCode, this.queryGeneralParam.paramName);
  }

  // 流程定义分页
  paginate(event) {
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.queryLargeClass(this.queryGeneralParam.paramCode, this.queryGeneralParam.paramName);
  }


}
