import { Component, OnInit } from '@angular/core';


@Component({
  selector: 'app-general-parameters',
  templateUrl: './general-parameters.component.html',
  styleUrls: ['./general-parameters.component.scss']
})
export class GeneralParametersComponent implements OnInit {

  paramsType: Array<string> = ['10', '01', 'C0', 'G0', 'H0', 'M0', 'GE'];
  // 
  index: number = 0;
  // 通用参数类型
  generalParamsType: string;

  msg = [];
  constructor(
  ) {
  }

  ngOnInit() {
  }


  onTabChange(event, bigClass, smallClass, businessParam, timeoutNotify, processRange, globalEvent) {
    this.index = event.index;
    this.index === 0 && bigClass.getGeneralParamList();
    this.index === 1 && smallClass.getGeneralParamList();
    this.index === 2 && businessParam.getGeneralParamList();
    this.index === 3 && timeoutNotify.getGeneralParamList();
    // this.index === 4 && channelMap.getGeneralParamList();
    this.index === 5 && processRange.query();
    this.index === 6 && globalEvent.getGlobalEventList();
    this.generalParamsType = this.paramsType[this.index];
  }
}
