import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { ConfirmationService } from 'primeng/primeng';
import { ExceptionStrategyService } from '../exception-strategy.service';
import { ExceptionStrategyModel } from '../../process-definition-model';
import { EXCEPTION_TYPE, EXCEPTION_PATTERN, EXCEPTION_HANDLE_MODELS, EXCEPTION_AUTO_HANDLE, EXCEPTION_NOTIFY_TYPES, EXCEPTION_HANDLE_USR_TYPES } from '../../../common/constant/code.value';
import { EngTypeModel } from '../../../process-instance/process-instance-model';
import { findObject, handleResult, handleError } from '../../../common/func/common-func';

@Component({
  selector: 'app-add-update-strategy',
  templateUrl: './add-update-strategy.component.html',
  styleUrls: ['./add-update-strategy.component.scss']
})
export class AddUpdateStrategyComponent implements OnInit, OnChanges {

  msg = [];
  @Input() inValue: any;

  @Output() outValueEmitter: EventEmitter<any> = new EventEmitter();

  @Input() exceptionStrategyInstance: ExceptionStrategyModel;

  // 异常类型
  exceptionTypes = EXCEPTION_TYPE;;
  // 异常模式
  exceptionPatterns = EXCEPTION_PATTERN;
  // 表单
  exceptionStrategyForm: FormGroup;
  // 异常处理模式
  exceptionHandleModels = EXCEPTION_HANDLE_MODELS;
  // 选中的异常处理模式
  selectedExceptionHandleModel: EngTypeModel = new EngTypeModel();
  // 异常自动处理模式
  execptionAutoHandle = EXCEPTION_AUTO_HANDLE;
  // 选中的自动处理模式
  selectedExecptionAutoHandle: EngTypeModel = new EngTypeModel();
  // 异常通知方式
  exceptionNotify = EXCEPTION_NOTIFY_TYPES;
  // 选中的异常通知方式
  selectedExceptionNotify: EngTypeModel = new EngTypeModel();
  // 异常处理人员类型
  exceptionHandleUser = EXCEPTION_HANDLE_USR_TYPES;
  // 选中的异常处理人员类型
  selectedExceptionHandleUsr: EngTypeModel = new EngTypeModel();
  // 修改重置时是临时变量
  defaultExceptionStrategy: ExceptionStrategyModel;




  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private exceptionStrategyService: ExceptionStrategyService,
  ) { }

  ngOnInit() {
    this.createValidators();
  }

  ngOnChanges() {
    if (this.inValue) {
      if (this.inValue.isAdd) {
        this.resetAdd();
      } else if (this.inValue.isModify) {
        this.defaultExceptionStrategy = { ...this.exceptionStrategyInstance };
        this.dropDownSetValue();
      }
    }


  }



  createValidators() {
    this.exceptionStrategyForm = this.fb.group({
      'exceptionTypeName': ['', Validators.required],
      'exceptionPatternName': ['', Validators.required],
      'exceptionHandleModelName': ['', Validators.required],
      'exceptionAutoHandleName': ['', Validators.required],
      'exceptionNotifyName': ['', Validators.required],
      'expRateName': [],
      'processCodeName': [],
      'expDoServiceName': [],
      'expTimeOutName': [],
      'exceptionHandleUserName': [],
      'expDoManIdName': [],
      'expDoManTellName': [],
      'expDoManMailName': [],
      'expAutoNumName': [],
      'expDoIntervalName': []
    });

  }


  // 重置
  reset(): void {
    this.inValue.isAdd && this.resetAdd();
    this.inValue.isModify && this.resetModify();


  }

  // 重置新增
  resetAdd(): void {
    this.exceptionStrategyInstance = new ExceptionStrategyModel();
    this.selectedExceptionHandleModel = { ...this.exceptionHandleModels[0] };
    this.selectedExecptionAutoHandle = { ...this.execptionAutoHandle[0] };
    this.selectedExceptionNotify = { ...this.exceptionNotify[0] };
    this.selectedExceptionHandleUsr = { ...this.exceptionHandleUser[0] };
  }

  // 重置修改
  resetModify(): void {
    this.exceptionStrategyInstance = { ...this.defaultExceptionStrategy };
    this.dropDownSetValue();
  }

  // 修改时的下拉框复制
  dropDownSetValue(): void {
    this.selectedExceptionHandleModel = this.exceptionStrategyInstance.expDoType ? findObject(this.exceptionStrategyInstance.expDoType, this.exceptionHandleModels, 'type') : null;
    this.selectedExecptionAutoHandle = this.exceptionStrategyInstance.expDoWay ? findObject(this.exceptionStrategyInstance.expDoWay, this.execptionAutoHandle, 'type') : null;
    this.selectedExceptionNotify = this.exceptionStrategyInstance.expTellMode ? findObject(this.exceptionStrategyInstance.expTellMode, this.exceptionNotify, 'type') : null;
    this.selectedExceptionHandleUsr = this.exceptionStrategyInstance.expDoManFlag ? findObject(this.exceptionStrategyInstance.expDoManFlag, this.exceptionHandleUser, 'type') : null;
  }


  // 更新或者保存
  saveOrUpdate(): void {
    const bodyParams = {
      processCode: this.exceptionStrategyInstance.processCode,
      expType: this.exceptionTypes[1].type,
      expMode: this.exceptionPatterns[11].type,
      expRate: this.exceptionStrategyInstance.expRate,
      expDoType: this.selectedExceptionHandleModel ? this.selectedExceptionHandleModel.type : '',
      expAutoNum: this.exceptionStrategyInstance.expAutoNum,
      expDoInterval: this.exceptionStrategyInstance.expDoInterval,
      expDoWay: this.selectedExecptionAutoHandle ? this.selectedExecptionAutoHandle.type : '',
      expDoService: this.exceptionStrategyInstance.expDoService,
      expTimeOut: this.exceptionStrategyInstance.expTimeOut,
      expDoManFlag: this.selectedExceptionHandleUsr ? this.selectedExceptionHandleUsr.type : '',
      expDoManId: this.exceptionStrategyInstance.expDoManId,
      expTellMode: this.selectedExceptionNotify ? this.selectedExceptionNotify.type : '',
      expDoManTell: this.exceptionStrategyInstance.expDoManTell,
      expDoManMail: this.exceptionStrategyInstance.expDoManMail
    };
    if (this.inValue.isModify) {
      Object.assign(bodyParams, { id: this.exceptionStrategyInstance.id });
      this.exceptionStrategyService.updateExceptionDo(bodyParams)
        .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.outValueEmitter.emit(event);
          }, true, { message: '更新成功！', errorMsg: '更新失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
            this.outValueEmitter.emit(event);
          });
        }
        );
    }
    if (this.inValue.isAdd) {
      this.exceptionStrategyService.addExceptionDo(bodyParams)
        .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.outValueEmitter.emit(event);
          }, true, { message: '保存成功！', errorMsg: '保存失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
            this.outValueEmitter.emit(event);
          });
        }
        );
    }

  }




}
