import { Component, OnInit, Input, EventEmitter, Output, OnChanges, forwardRef } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators, NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
import { MessageService } from 'primeng/components/common/messageservice';
import { TIME_TO_START_TYPE, WEEKS } from '../../../../common/constant/code.value';
import { DictionaryTypeModel, VerifyVariableModel } from '../../../process-definition-model';
import { findObject, formatDate, showMessage, handleResult, handleError } from '../../../../common/func/common-func';
import { TIME_FORMAT } from '../../../../common/constant/config';
import { BusinessNodeManagementService } from '../../../business-node-management/business-node-management.service';

@Component({
  selector: 'add-verify-test-script',
  templateUrl: './verify-test-script.component.html',
  styleUrls: ['./verify-test-script.component.scss'],
  providers: [{
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => VerifyTestScriptComponent),
    multi: true
  }]
})

export class VerifyTestScriptComponent implements OnInit, ControlValueAccessor {



  @Output() outValueEmitter: EventEmitter<any> = new EventEmitter();

  @Input() inValue: string;

  msg = [];

  verifyVarList: VerifyVariableModel[] = [];
  // 测试结果
  testResult: string;
  // 控制新增测试变量模态框的显隐
  addTestVarDisplay = false;
  // 新增测试变量表单
  addTestVarForm: FormGroup;
  // 新增测试变量实例
  addVerifyVarList: VerifyVariableModel = new VerifyVariableModel();

  model: any;

  public onModelChange: Function = () => { };

  public onModelTouched: Function = () => { };



  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private businessNodeManagementService: BusinessNodeManagementService,
  ) {
  }

  ngOnInit() {
    this.createValidators();
  }


  createValidators() {
    this.addTestVarForm = this.fb.group({
      'variableName': ['', Validators.required],
      'testValName': ['', Validators.required]
    });
  }

  resetListAndResult(): void {
    this.verifyVarList = [];
    this.testResult = null;
  }


  // 添加测试变量
  addTestVar(): void {
    this.resetTestVar();
    this.addTestVarDisplay = true;
  }


  // 测试脚本按钮
  verifyTestScriptFun(): void {
    let obj = {};
    this.verifyVarList.forEach(val => {
      obj[`${val.varName}`] = val.testVal;
    })
    const bodyParams = {
      js: this.model,
      bussinessMap: obj
    }
    this.businessNodeManagementService.javaScriptTest(bodyParams)
      .subscribe(
      data => {
        if (data.returnCode.type === 'S') {
          this.testResult = data.pageData[0].result[0];
        } else {
          this.testResult = data.returnCode.message;
        }
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      }
      );
  }


  // 关闭按钮
  cancle(): void {
    this.outValueEmitter.emit('关闭');
  }

  // 删除测试变量
  deleteTestVar(item): void {
    this.verifyVarList = this.verifyVarList.filter(val => val.varName !== item.varName);
  }


  saveTestVar(): void {
    const isHas: boolean = this.verifyVarList.some(val => val.varName === this.addVerifyVarList.varName);
    if (isHas) {
      showMessage(this.messageService, 'error', `${this.addVerifyVarList.varName}已存在！请重新输入`);
      return;
    }
    this.verifyVarList.push({ ...this.addVerifyVarList });
    this.addTestVarDisplay = false;
  }


  // 新增测试变量的的重置
  resetTestVar(): void {
    this.addVerifyVarList.varName = null;
    this.addVerifyVarList.testVal = null;
  }

  writeValue(value: any) {
    if (value) {
      this.model = value;
    }
  }
  registerOnChange(fn: Function): void {
    this.onModelChange = fn;
  }
  registerOnTouched(fn: Function): void {
    this.onModelTouched = fn;
  }
  modelChange(value) {
    this.model = value;
    this.onModelChange(this.model); // 主要是要调用这个去重置绑定的model的值
  }


}

