import { Component, OnInit, OnChanges, EventEmitter, Output } from '@angular/core';
import { MessageService } from 'primeng/components/common/messageservice';
import { Pager } from '../../../../common/constant/config';
import { ProcessDefinitionModel, BusinessNodeModel } from '../../../process-definition-model';
import { ProcessMaintenanceService } from '../../../process-maintenance/process-maintenance.service';
import { handleResult, handleError } from '../../../../common/func/common-func';
import { BusinessNodeManagementService } from '../../../business-node-management/business-node-management.service';
import { TASK_NODE_TYPES } from '../../../../common/constant/code.value';

@Component({
  selector: 'app-node-define-list',
  templateUrl: './node-define-list.component.html',
  styleUrls: ['./node-define-list.component.scss']
})
export class NodeDefineListComponent implements OnInit {
  msg = [];

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  // 流程定义分页
  pager: Pager = new Pager();
  // 查询到节点定义集合
  queryBusinessNodes: BusinessNodeModel[] = [];
  // 选中的单行流程定义
  selectedBusinessNode: BusinessNodeModel = new BusinessNodeModel();
  // 查询条件的的流程定义
  queryProcessDefine: BusinessNodeModel = new BusinessNodeModel();
  // 节点定义类型
  businessNodeTypes = TASK_NODE_TYPES;

  constructor(
    private messageService: MessageService,
    private processMaintenanceService: ProcessMaintenanceService,
    private businessNodeManagementService: BusinessNodeManagementService,
  ) { }
  ngOnInit() {
    this.getBusinessNode();
  }

  // 重置查询
  reset(): void {
    this.queryProcessDefine.nodeId = null;
    this.queryProcessDefine.nodeName = null;
  }

  // 按照搜索条件查询
  query() {
    this.pager.first = '0';
    this.pager.page = '1';
    this.getBusinessNode();
  }


  // 流程定义分页
  paginate(event) {
    this.pager.first = event.first;
    this.pager.rows = event.rows;
    this.pager.page = event.page + 1;
    this.getBusinessNode();
  }


  // 查找节点定义列表
  getBusinessNode(): void {
    const bodyParams = {
      nodeId: this.queryProcessDefine.nodeId,
      nodeName: this.queryProcessDefine.nodeName,
      pageRows: parseInt(this.pager.rows, 10),
      curPage: parseInt(this.pager.page, 10)
    };
    this.businessNodeManagementService.queryBussinessNode(bodyParams)
      .subscribe(
        data => {
          handleResult(data, this.messageService, () => {
            this.queryBusinessNodes = data.pageData[0].result;
            this.pager.totalRecords = data.pageData[0].totalRows;
          }, true, { errorMsg: '查询失败' });
        },
        err => {
          handleError(err, this.messageService, () => {
          });
        }
      );


  }


  // 选中节点定义
  selectNodeDefine(event): void {
    this.outValue.emit(event);
  }



}
